<template>
  <div class="shop-list" v-if="shops && oftenShops">
    <div class="shop-list__search-container">
      <input class="shop-list__search-container__search-bar" placeholder="输入门店名称" v-model="searchShop">
    </div>
    <div class="shop-list__current-shop" ref="currentShop">
      <div class="shop-list__current-shop__title-row">
        <span class="shop-list__current-shop__title-row__title">当前定位</span>
      </div>
      <div class="shop-list__current-shop__item">
        <div class="shop-list__current-shop__item__icon-location"></div>
        <div class="shop-list__current-shop__item__current-address" v-if="currentAddress">{{ currentAddress }}</div>
        <div class="shop-list__current-shop__item__refresh-location" @click="setCurrentAddress">重新定位</div>
      </div>
      <div class="shop-list__whitespace"></div>

      <template v-if="oftenShops.length > 0">
        <div class="shop-list__my-shops__title-row">
          <span class="shop-list__my-shops__title-row__title">常去店铺</span>
        </div>
        <shop-item v-for="shop in oftenShops" :shop="shop" :key="shop.shopId"></shop-item>
      </template>

      <div class="shop-list__whitespace"></div>

      <template v-if="nearbyShops.length > 0">
        <div class="shop-list__nearby-shops__title-row">
          <span class="shop-list__nearby-shops__title-row__title">附近店铺</span>
        </div>
        <shop-item v-for="shop in nearbyShops" :shop="shop" :key="shop.shopId"></shop-item>
      </template>
    </div>
  </div>
</template>

<script>
import api from '@/api';
import _ from 'lodash';
import envService from '@/services/env';
import calculateDistance from '@/utils/calculate-distance';
import qqLbsService from '@/services/qq-lbs';
import wechatSdkService from '@/services/wechat-mini-program-bridge';
import ShopItem from './ShopList/ShopItem';
import LoadingModal from '@/components/LoadingModal';

export default {
  components: {
    ShopItem
  },
  data() {
    return {
      oftenShops: null,
      currentAddress: null,
      nearbyShopsLength: 10,
      searchShop: null,
      currentLocation: null
    }
  },

  created () {
    this.fetchOftenShoplist();
  },

  mounted () {
    qqLbsService.init().then(() => {
      this.setCurrentAddress();
    });
  },

  updated () {
    this.$refs.currentShop.addEventListener('scroll', this.handleScroll);
  },

  computed: {
    shops () {
      const _shops = this.$store.state.shop.shops;
      if (_.isEmpty(_shops)) {
        return [];
      } else {
        const regionBlockCode = envService.getRegionBlockCode();
        return _shops[regionBlockCode];
      }
    },
    sortedShopsWithDistance() {
      const currentLocation = this.currentLocation;
      if (currentLocation) {
        const shopsWithDistance =  this.shops.map(shop => {
          const _shop = Object.assign({}, shop);
          _shop.distance = calculateDistance(shop.latitude, shop.longitude, currentLocation.latitude, currentLocation.longitude);
          return _shop;
        });

        shopsWithDistance.sort((aShop, bShop) => aShop.distance - bShop.distance);
        return shopsWithDistance;
      } else {
        return this.shops;
      }
    },
    filterShops(){
      if (this.searchShop) {
        return this.sortedShopsWithDistance.filter(item => item.shopName.includes(this.searchShop));
      } else {
        return this.sortedShopsWithDistance;
      }
    },
    currentShop() {
      return this.sortedShopsWithDistance[0];
    },
    myShops() {
      return this.sortedShopsWithDistance.slice(1,3);
    },
    nearbyShops() {
      return this.filterShops.slice(0, this.nearbyShopsLength);
    }
  },

  methods: {
    handleScroll: _.debounce(function() {
      const scrollTop      = this.$refs.currentShop.scrollTop;
      const innerHeight    = this.$refs.currentShop.clientHeight;
      const scrollHeight   = this.$refs.currentShop.scrollHeight;
      const isScrollBottom = (scrollHeight <= (innerHeight + scrollTop));

      if (isScrollBottom) {
        this.nearbyShopsLength += 10;
      }
    }, 150),

    fetchOftenShoplist() {
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();
      api.shop.fetchOftenShoplist({ regionBlockCode }).then(res => {
        this.oftenShops = res.data.data.slice(0,2);
      }).finally(() => LoadingModal.hide());
    },

    setCurrentAddress() {
      this.currentAddress = null;
      wechatSdkService.getLocation().then(res => {
        const latitude = res.latitude;
        const longitude = res.longitude;
        this.currentLocation = { latitude, longitude };
        qqLbsService.getAddressByLatLng(latitude, longitude).then(res => {
          this.currentAddress = res.address;
        })
      })
    }
  },
};
</script>

