<template>
  <div class="shop-item">
    <div class="shop-item__info">
      <div class="shop-item__basic-info" @click="selectPickupShop(shop.shopId)">
        <div class="shop-item__shop-name">
          <span>{{ shop.shopName }}</span>
          <format-business-time v-if="shop.businessTime !== ''" :businessTime="shop.businessTime"></format-business-time>
        </div>
        <div class="shop-item__shop-address">{{ shop.address }}</div>
      </div>

      <div class="shop-item__map-info" v-if="shop.distance">
        <div class="shop-item__distance">距您{{ shop.distance | toKilometer }}</div>
        <div class="shop-item__show-in-map" @click="openMap">地图 ></div>
      </div>
    </div>
  </div>
</template>

<script>
import wechatSdkService from '@/services/wechat-mini-program-bridge';
import FormatBusinessTime from '@/components/FormatBusinessTime';

export default {
  props: {
    shop: Object
  },

  components: {
    FormatBusinessTime
  },

  methods: {
    openMap() {
      const latitude = this.shop.latitude;
      const longitude = this.shop.longitude;
      const name = this.shop.shopName;
      const address = this.shop.address;
      wechatSdkService.openLocation(latitude, longitude, name, address);
    },

    selectPickupShop(shopId) {
      this.$store.commit('ecOrderInfo/setShopId', shopId);
      this.$router.back()
    }
  },

  filters: {
    toKilometer(number) {
      if (number > 20) {
        return '> 20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  }
};
</script>

