import MtaH5 from 'mta-h5-analysis';
import router from '@/router';
import Vue    from 'vue';

const VUE_APP_MTA_SID = process.env.VUE_APP_MTA_SID;
const VUE_APP_MTA_CID = process.env.VUE_APP_MTA_CID;

const vm = new Vue();

export default {
  init() {
    MtaH5.init({
      "sid": VUE_APP_MTA_SID, //必填，统计用的appid
      "cid": VUE_APP_MTA_CID, //如果开启自定义事件，此项目为必填，否则不填
      "autoReport": 1,//是否开启自动上报(1:init完成则上报一次,0:使用pgv方法才上报)
      "senseHash": 1, //hash锚点是否进入url统计
      "senseQuery": 1, //url参数是否进入url统计
      "performanceMonitor": 1,//是否开启性能监控
      "ignoreParams": [] //开启url参数上报时，可忽略部分参数拼接上报
    });

    vm.$watch(() => router.app.$route, () => {
      MtaH5.pgv();
    });
  }
}
