<template>
  <div class="campaign-express-address">
    <ul class="campaign-express-address__list" v-if="campaignPostExchangedList.length">
      <li v-for="(item,index) in campaignPostExchangedList" class="campaign-express-address__list__item" :key="index">
        <div class="campaign-express-address__list__item__left" >
          <p class="campaign-express-address__list__item__left__title">第{{index + 1}}轮: &nbsp;&nbsp;{{item.giftName === null ? '未中奖' : item.giftName }}</p>
        </div>
      </li>
    </ul>
    <div class="campaign-express-address__empty" v-else>
      <p class="campaign-express-address__empty__icon"></p>
      <p class="campaign-express-address__empty__text">没有兑换过礼品</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      campaignPostExchangedList: null
    };
  },
  created() {
    const userStampList = this.$route.params.userStampList.filter(item => item.status === 2 || item.status === 3 );
    this.campaignPostExchangedList = userStampList;
  }
};

