<template>
  <div class="mission-progress-bar-discrete">
    <div class="mission-progress-bar-discrete__sections">
      <div class="mission-progress-bar-discrete__sections__complete" :style="{ 'flex-grow': current }"></div>
      <div class="mission-progress-bar-discrete__sections__incomplete" :style="{ 'flex-grow': left }"></div>
    </div>
    <div class="mission-progress-bar-discrete__milestones">
      <template v-for="n in total">
        <div class="mission-progress-bar-discrete__milestones__milestone complete" v-if="n - 1 <= current">{{ n - 1 }}</div>
        <div class="mission-progress-bar-discrete__milestones__milestone" v-else>{{ n - 1 }}</div>
      </template>
      <div class="mission-progress-bar-discrete__milestones__end" :class="{ 'with-check': current === total }"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    total: Number,
    current: Number
  },
  computed: {
    left() {
      return this.total - this.current;
    }
  }
};
</script>

