export default {
  namespaced: true,
  state: {
    shopId: null,
    takeDate: null
  },
  mutations: {
    setShopId(state, shopId) {
      state.shopId = shopId;
    },

    setTakeDate(state, takeDate) {
      state.takeDate = takeDate;
    },

    initState(state) {
      state.shopId = null;
      state.takeDate = null;
    }
  }
}