<template>
  <div class="coupon-item" @click="goToCouponDetail">
    <img :src="ossImageUrl + '/coupon/my-coupons-background.png'" class="coupon-item__image">
    <div class="coupon-item__info">
      <div class="coupon-item__price" v-if="couponType === 'totalPriceMinusSomeCoupon'">
        <div class="coupon-item__price__total-price">
          满<span class="coupon-item__price__total-price__number">{{ coupon.originalPrice }}</span>元
        </div>
        <div class="coupon-item__price__discount-price">
          <span>减</span>
          <span class="coupon-item__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
      </div>
      <div class="coupon-item__price coupon-item__limit-product" v-if="couponType === 'productPriceMinusToFreeCoupon'">
        <div class="coupon-item__price__total-price">
          全额折扣
        </div>
        <div class="coupon-item__price__discount-price">
          兑换
        </div>
        <div class="coupon-item__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item__price" v-if="couponType === 'productPriceMinusSomeCoupon'">
        <div class="coupon-item__price__total-price">
          消费立减
        </div>
        <div class="coupon-item__price__discount-price">
          <span>减</span>
          <span class="coupon-item__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
        <div class="coupon-item__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item__price coupon-item__limit-product" v-if="couponType === 'productPriceMinusToSomeCoupon'">
        <div class="coupon-item__price__total-price">
          消费立减
        </div>
        <div class="coupon-item__price__discount-price product-price-minus-to-some-coupon">
          <span>减至</span>
          <span class="coupon-item__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
        <div class="coupon-item__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item__description">
        <div class="coupon-item__description__title">
          {{ coupon.couponName }}
        </div>
        <div class="coupon-item__description__button" v-if="couponType === 'totalPriceMinusSomeCoupon'">
          全品类
        </div>
        <div class="coupon-item__description__button" v-if="couponType === 'productPriceMinusToFreeCoupon' || couponType === 'productPriceMinusSomeCoupon' || couponType === 'productPriceMinusToSomeCoupon'">
          限定商品
        </div>
        <div class="coupon-item__description__time" v-if="coupon.dynamicPeriodFlg === 0">
          有效期{{ coupon.useDateFrom | formatDate }}-{{ coupon.useDateTo | formatDate }}
        </div>
      </div>
      <div class="coupon-item__activity-progress">
        <div v-if="coupon.sendUpperLimit > 0">
          <div class="coupon-item__activity-progress__persent">
            <canvas ref="canvas" class="canvas" width=120 height=120></canvas>
            <div class="coupon-item__activity-progress__persent__text">
              <span>已抢</span>
              <span>{{ coupon.percentage }}%</span>
            </div>
          </div>
          <div class="coupon-item__activity-progress__button" @click="pickCoupon">
            <span>立即领取</span>
          </div>
        </div>
        <div v-else class="coupon-item__activity-progress__limit-none-button" @click="pickCoupon">
          <span>立即领取</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import couponTypeService from '@/services/coupon/coupon-type';

export default {
  props: ['coupon'],

  data () {
    return {
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL,
      currentDate: Date.now(),
    }
  },

  computed: {
    couponType() {
      return couponTypeService.getType(this.coupon);
    }
  },

  filters: {
    formatDate (timeStamp) {
      const y = (new Date(timeStamp)).getFullYear();
      const _m = (new Date(timeStamp)).getMonth() + 1;
      const _d = (new Date(timeStamp)).getDate();

      // 小于10的月和日前面需要加0，例如“3.2”需要显示为“03.02”。
      const month = (_m < 10) ? ('0' + _m) : String(_m);
      const day   = (_d < 10) ? ('0' + _d) : String(_d);
      return `${y}.${month}.${day}`;
    }
  },

  watch: {
    coupon(val, oldVal) {
      this.drawCanvas();
    }
  },

  mounted() {
    this.drawCanvas();
  },

  methods: {
    goToCouponDetail() {
      const couponId = this.coupon.couponId;

      this.$router.push({ name: 'coupon-detail', params: { couponId } });
    },

    pickCoupon(e) {
      e.stopImmediatePropagation();
      this.$emit('click');
    },

    drawCanvas() {
      if(this.coupon.sendUpperLimit > 0) {
        const context = this.$refs.canvas.getContext('2d');
        const drawPercentage = this.coupon.percentage;

        function drawSpeedometer(percentage) {
          context.clearRect(0, 0, 100, 100);
          context.beginPath();
          context.arc(60, 60, 32, mapToRadian(0), mapToRadian(100));
          context.strokeStyle = '#f5c7c0';
          context.lineWidth = 3;
          context.lineCap = 'round';
          context.stroke();

          context.beginPath();
          context.arc(60, 60, 32, mapToRadian(0), mapToRadian(percentage));
          context.strokeStyle = '#e65c58';
          context.lineWidth = 3;
          context.lineCap = 'round';
          context.stroke();
        }

        function step(percentage) {
          if (percentage <= drawPercentage) {
            drawSpeedometer(percentage);
            requestAnimationFrame(() => {
              step(percentage + 1);
            });
          }
        }

        function linearMap(x, smin, smax, dmin, dmax) {
          const ratio = (x - smin) / (smax - smin);
          return dmin + ratio * (dmax - dmin);
        }

        function mapToRadian(percentage) {
          return linearMap(percentage, 0, 100, Math.PI * (0.5 + (1 / 4)), Math.PI * (2 + (1 / 4)));
        }

        requestAnimationFrame(() => step(0));
      }
    }
  }
}
</script>
