<template>
    <div class="goto-home" @click="gotoHome"></div>
</template>

<script>
import envService from '@/services/env';

export default {
  methods: {
    gotoHome() {
      const isGuest = envService.getUserInfo() === null;
      if (isGuest) {
        wx.miniProgram.reLaunch({ url: '/pages/index/index?pageKey=homepage' });
      } else {
        wx.miniProgram.switchTab({ url: '/pages/homepage/homepage' });
      }
    }
  }
}
</script>

