export default {
  namespaced: true,
  state: {
    chooseAddressInfo: null,
    editAddressInfo: null,
    addAddressInfo: null,
    needShowEditAddressSuccessToast: false,
    needShowAddAddressSuccessToast: false
  },
  mutations: {
    setChooseAddressInfo(state, addressInfo) {
      state.chooseAddressInfo = addressInfo;
    },
    setEditAddressInfo(state, addressInfo) {
      state.editAddressInfo = addressInfo;
    },
    setAddAddressInfo(state, addressInfo) {
      state.addAddressInfo = addressInfo;
    },
    setNeedShowEditAddressSuccessToast(state, isShow) {
      state.needShowEditAddressSuccessToast = isShow;
    },
    setNeedShowAddAddressSuccessToast(state, isShow) {
      state.needShowAddAddressSuccessToast = isShow;
    },
    cleanupChooseAddressInfo(state) {
      state.chooseAddressInfo = null;
    },
    cleanupEditAddressInfo(state) {
      state.editAddressInfo = null;
    },
    cleanupAddAddressInfo(state) {
      state.addAddressInfo = null;
    },
    hideEditAddressSuccessToast(state) {
      state.needShowEditAddressSuccessToast = false;
    },
    hideAddAddressSuccessToast(state) {
      state.needShowAddAddressSuccessToast = false;
    }
  }
}