import axios from 'axios';
import signatureService from '@/services/sign-api';

const BASE_URL = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchEcProductShopList ({ regionBlockCode, cityId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getCommodityList`;
    return axios.get(url, { params: { timestamp, nonce, signature, regionBlockCode, cityId } });
  },

  fetchEcProductShopDetail ({ commodityId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getCommodityDetail?commodityId=${commodityId}`;
    return axios.get(url, { params: { timestamp, nonce, signature } });
  },

  createOrder ({ commodityId, quantity }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/addOrder`;
    return axios.post(url, { timestamp, nonce, signature, commodityId, qty:quantity });
  },

  fetchOrderDetail ({ orderId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getOrderDetail?orderId=${orderId}`;
    return axios.get(url, { params: { timestamp, nonce, signature } });
  },

  fetchPayOrderDetail ({ orderId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getPayedOrderDetail?orderId=${orderId}`;
    return axios.get(url, { params: { timestamp, nonce, signature } });
  },

  fetchOrderShopList({ orderId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getOrderShopList?orderId=${orderId}`;
    return axios.get(url, { params: { timestamp, nonce, signature } });
  },

  fetchMyOrderList({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/getOrderList?regionBlockCode=${regionBlockCode}`;
    return axios.get(url, { params: { timestamp, nonce, signature } });
  },

  deleteOrder({ orderId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/deleteOrder`;
    return axios.post(url, { timestamp, nonce, signature, orderId });
  },

  editOrder({ orderId, takeDate, shopId }) {
    const { timestamp, nonce, signature } = signatureService.generateModulesApi();
    const url = `${BASE_URL}/modules/mina/ec/modifyOrder`;
    return axios.post(url, { timestamp, nonce, signature, orderId, takeDate, shopId});
  }
};