<template>
  <div class="add-address">
    <div class="add-address__contact-content">
      <span class="add-address__contact">联系人</span><input class="add-address__contact-name" placeholder="请输入你的姓名" v-model.trim="name">
    </div>
    <div class="add-address__gender-info">
        <span :class="isSelectMale?'add-address__gender-mr tagSelect':'add-address__gender-mr'" @click="selectMale">先生</span>
        <span :class="isSelectFemale?'add-address__gender-miss tagSelect':'add-address__gender-miss'"  @click="selectFemale">女士</span>
      </div>

    <div class="add-address__phone-content">
      <span class="add-address__phone">手机号</span><input oninput="if(value.length>11)value=value.slice(0,11)" class="add-address__phone-number" placeholder="请输入你的手机号" v-model.number="phoneNumber" type="number">
    </div>

    <div class="add-address__address-content" @click="chooseAddress">
      <span class="add-address__address">地址</span><input class="add-address__address-description" placeholder="请选择地址" v-model.trim="addressDetailName" onfocus="this.blur()">
      <span class="add-address__address-description__icon" @click="chooseAddress"></span>
    </div>

    <div class="add-address__address-detail-content">
      <span class="add-address__address-detail">门牌号</span><input class="add-address__address-detail-description" placeholder="请输入门牌号，如A座8A座" v-model.trim="addressExtraInfo">
    </div>

    <div class="add-address__tag-info">
      <span class="add-address__tag">标签</span>
      <span :class="isSelectHome?'add-address__tag-home tagSelect':'add-address__tag-home'" @click="selectHome">家</span>
      <span :class="isSelectCompany?'add-address__tag-company tagSelect':'add-address__tag-company'" @click="selectCompany">公司</span>
      <span :class="isSelectSchool?'add-address__tag-school tagSelect':'add-address__tag-school'" @click="selectSchool">学校</span>
    </div>

    <div class="add-address__save-address" @click="saveAddress">确定</div>
  </div>
</template>

<script>
import Toast        from '@/components/Toast';
import CommittingModal from '@/components/CommittingModal';

import api          from '@/api/index';
import envService   from '@/services/env';

export default {
  data() {
    return {
      name: '',
      gender: 'FEMALE',
      phoneNumber: '',
      addressName: '',
      addressDetailName: null,
      addressExtraInfo: '',
      tags: [],

      addAddress: null,

      isSelectMale: false,
      isSelectFemale: false,

      isSelectHome: false,
      isSelectCompany: false,
      isSelectSchool: false,

      isLocationPickerVisible: false,

      locationInfo: null,
    };
  },
  created() {
    this.addAddress = this.$store.state.campaignSendExchangeAddress.addAddressInfo;
    if (this.addAddress) {
      this.name              = this.addAddress.name;
      this.gender            = this.addAddress.gender;
      this.phoneNumber       = this.addAddress.phoneNumber;
      this.addressName       = this.$store.state.location.locationInfo.addressName;
      this.addressDetailName = this.$store.state.location.locationInfo.addressDetailName;
      this.addressExtraInfo  = this.addAddress.addressExtraInfo;

      const latitude    = this.$store.state.location.locationInfo.latitude;
      const longitude   = this.$store.state.location.locationInfo.longitude;
      this.locationInfo = { latitude, longitude };

      const isMale = (this.addAddress.gender === 'MALE') ? true : false;
      if (isMale) {
        this.isSelectMale = true;
        this.gender = 'MALE';
      } else {
        this.isSelectFemale = true;
        this.gender = 'FEMALE';
      }

      const hasTags = this.addAddress.tags ? true : false;

      if (hasTags) {
        var isTagHome    = (this.addAddress.tags[0] === '家') ? true : false;
        var isTagCompany = (this.addAddress.tags[0] === '公司') ? true : false;
        var isTagSchool  = (this.addAddress.tags[0] === '学校') ? true : false;
        if (isTagHome) {
          this.isSelectHome = true;
          this.tags = ['家'];
        } else if (isTagCompany) {
          this.isSelectCompany = true;
          this.tags = ['公司'];
        } else if (isTagSchool) {
          this.isSelectSchool = true;
          this.tags = ['学校'];
        }
      }
    } else {
      this.isSelectFemale = true;
    }
  },
  methods: {
    chooseAddress() {
      const name              = this.name;
      const gender            = this.gender;
      const phoneNumber       = this.phoneNumber;
      const addressName       = this.addressName;
      const addressDetailName = this.addressDetailName;
      const addressExtraInfo  = this.addressExtraInfo;
      const tags              = this.tags;

      const addAddressInfoForChosen = {
        name,
        gender,
        phoneNumber,
        addressName,
        addressDetailName,
        addressExtraInfo,
        tags
      };
      this.$store.commit('campaignSendExchangeAddress/setAddAddressInfo', addAddressInfoForChosen);

      this.$router.push({ path: '/choose-location' });
    },
    saveAddress() {
      const regionBlockCode  = envService.getRegionBlockCode();
      const isnameValid             = (this.name !== '') ? false : true;
      const isGenderValid           = (this.gender !== '') ? false : true;
      const isphoneNumberValid      = (this.phoneNumber !== '') ? false: true;
      const isAddressNameValid      = (this.addressName !== '') ? false : true;
      const isAddressExtraInfoValid = (this.addressExtraInfo !== '') ? false : true;
      const isPhoneNumberValid      = this.checkPhoneValid(this.phoneNumber) ? false : true;

      if (isnameValid) {
        Toast.show({ msg:'请输入你的姓名' });
      } else if (isGenderValid) {
        Toast.show({ msg:'请选择性别' });
      } else if (isphoneNumberValid) {
        Toast.show({ msg:'请输入你的手机号' });
      } else if (isPhoneNumberValid) {
        Toast.show({ msg:'请输入有效的手机号' });
      } else if (isAddressNameValid) {
        Toast.show({ msg:'请选择地址' });
      } else if (isAddressExtraInfoValid) {
        Toast.show({ msg:'请输入门牌号' });
      } else {
        const addressInfo = {
          regionBlockCode,
          name: this.name,
          gender: this.gender,
          phoneNumber: this.phoneNumber,
          addressName: this.addressName,
          addressDetailName: this.addressDetailName,
          addressExtraInfo: this.addressExtraInfo,
          latitude: this.locationInfo.latitude,
          longitude: this.locationInfo.longitude,
          tags: this.tags
        };
        CommittingModal.show();
        api.address.add({ addressInfo, regionBlockCode }).then(res => {
          CommittingModal.hide();

          const isFetchAddressInfoSuccess = res.data.code === 0 ? true : false;
          if (isFetchAddressInfoSuccess) {
            this.$router.go(-1);
            this.$store.commit('campaignSendExchangeAddress/cleanupAddAddressInfo');
            this.$store.commit('campaignSendExchangeAddress/setNeedShowAddAddressSuccessToast', true);
          } else {
            Toast.show({ msg: '新增地址信息失败, 请稍后再试' });
          }

        }).catch(err => {
          CommittingModal.hide();
          Toast.show({ msg: '新增地址信息失败, 请稍后再试' });
        });
      }
    },
    checkPhoneValid(value) {
      if((/^1\d{10}$/.test(value))){
        return true;
      } else {
        return false;
      }
    },
    selectMale() {
      this.isSelectMale = true;
      this.isSelectFemale = false;
      this.gender = 'MALE';
    },
    selectFemale() {
      this.isSelectFemale = true;
      this.isSelectMale = false;
      this.gender = 'FEMALE';
    },
    selectHome() {
      if (this.isSelectHome) {
        this.isSelectHome = false;
        this.tags = [];
      } else {
        this.isSelectHome = true;
        this.tags = ['家'];
      }
      this.isSelectCompany = false;
      this.isSelectSchool = false;
    },
    selectCompany() {
      if (this.isSelectCompany) {
        this.isSelectCompany = false;
        this.tags = [];
      } else {
        this.isSelectCompany = true;
        this.tags = ['公司'];
      }
      this.isSelectHome = false;
      this.isSelectSchool = false;
    },
    selectSchool() {
      if (this.isSelectSchool) {
        this.isSelectSchool = false;
        this.tags = [];
      } else {
        this.isSelectSchool = true;
        this.tags = ['学校'];
      }
      this.isSelectHome = false;
      this.isSelectCompany = false;
    }
  },
};
</script>

