<template>
  <div class="mission-center-loading" v-if="missions === null"></div>
  <div class="mission-center" v-else>
    <div class="mission-center__content">
      <div class="mission-center__content__background"></div>
      <div class="mission-center__content__rule" @click="gotoRule">
        <span class="mission-center__content__rule__text">活动规则</span>
        <span class="mission-center__content__rule__question-mark"></span>
      </div>
      <div class="mission-center__content__misc" v-if="userGroupInfo !== null">
        <div class="mission-center__content__misc__avatar">
          <img :src="avatarUrl">
        </div>
        <div class="mission-center__content__misc__checkin-count">
          <p class="mission-center__content__misc__checkin-count__number">{{ userGroupInfo.continusDays }}</p>
          <p class="mission-center__content__misc__checkin-count__title">签到</p>
        </div>
        <div class="mission-center__content__misc__lottery-coin-count">
          <p class="mission-center__content__misc__lottery-coin-count__number">{{ userGroupInfo.ticketNum }}</p>
          <p class="mission-center__content__misc__lottery-coin-count__title">抽奖币</p>
        </div>
        <div class="mission-center__content__misc__go-to-lottery">
          <button class="mission-center__content__misc__go-to-lottery__button" @click="gotoLottery">去抽奖</button>
        </div>
      </div>
      <div class="mission-center__content__weekly-missions" v-if="missions !== null">
        <div class="mission-center__content__weekly-missions__title"></div>
        <div class="mission-center__content__weekly-missions__come-to-shop-mission">
          <come-to-shop-mission :mission="comeToShopMission"></come-to-shop-mission>
        </div>
        <div class="mission-center__content__weekly-missions__purchase-amount-mission">
          <purchase-amount-mission :mission="purchaseAmountMission"></purchase-amount-mission>
        </div>
      </div>
      <div class="mission-center__content__other-missions" v-if="missions !== null">
        <div class="mission-center__content__other-missions__title"></div>
        <div class="mission-center__content__other-missions__mission-item" v-for="otherMission in otherMissions">
          <other-mission :mission="otherMission"></other-mission>
        </div>
      </div>
      <div class="mission-center__content__data-refresh-hint">数据可能会有延迟，24小时内更新</div>
    </div>
    <transition name="fade">
      <div class="mission-center__pick-result-modal" v-if="pickedResult !== null">
        <div class="mission-center__pick-result-modal__icon"></div>
        <p class="mission-center__pick-result-modal__text">任务已完成</p>
        <p class="mission-center__pick-result-modal__text">{{ pickedResultText }}</p>
      </div>
    </transition>
  </div>
</template>

<script>
import _ from 'lodash';

import api from '@/api';

import OtherMission from './MissionCenter/OtherMission';
import ComeToShopMission from './MissionCenter/ComeToShopMission';
import PurchaseAmountMission from './MissionCenter/PurchaseAmountMission';

import missionService from '@/services/mission';
import envService from '@/services/env';

export default {
  components: { OtherMission, ComeToShopMission, PurchaseAmountMission },
  data() {
    return {
      missions: null,
      userGroupInfo: null,
      avatarUrl: envService.getUserInfo().basic.photo,
      pickedResult: null
    }
  },
  computed: {
    comeToShopMission() {
      return this.missions.find(mission => missionService.getMissionType(mission) === 'comeToShopWeekly');
    },
    purchaseAmountMission() {
      return this.missions.find(mission => missionService.getMissionType(mission) === 'purchaseAmountWeekly');
    },
    otherMissions() {
      // 每月来店任务与每月消费额任务需要隐藏
      return this.missions.filter(mission => {
        const excludedMissionTypes = ['comeToShopWeekly', 'purchaseAmountWeekly', 'comeToShopMonthly', 'purchaseAmountMonthly'];
        const missionType = missionService.getMissionType(mission);
        return excludedMissionTypes.indexOf(missionType) < 0;
      });
    },
    pickedResultText() {
      const pickedRewardType = missionService.getMissionRewardType(this.pickedResult);
      if (pickedRewardType === 'point') {
        return `积分+${this.pickedResult.lotteryPoint}`;
      } else if (pickedRewardType === 'coupon') {
        return `礼券+1`;
      } else if (pickedRewardType === 'lotteryCoin') {
        return `抽奖币+${this.pickedResult.lotteryTicketNum}`;
      }
    }
  },
  methods: {
    gotoRule() {
      this.$router.push('/mission-center/rule');
    },
    gotoLottery() {
      this.$router.replace('/lottery');
    },
    fetchUserGroupInfo() {
      const regionBlockCode = envService.getRegionBlockCode();
      api.user.fetchUserGroupInfo({ regionBlockCode }).then(userGroupInfo => {
        this.userGroupInfo = userGroupInfo;
      });
    }
  },
  created() {
    const regionBlockCode = envService.getRegionBlockCode();
    this.fetchUserGroupInfo();
    api.mission.fetchMissions({ regionBlockCode }).then(async missions => {
      this.missions = missions;
      const completeNotPickedRewardMissions = this.missions.filter(mission => mission.receiveFlg === 0);
      if (completeNotPickedRewardMissions.length > 0) {
        for (let i = 0; i < completeNotPickedRewardMissions.length; i++) {
          const mission = completeNotPickedRewardMissions[i];
          const pickResult = await api.mission.pickReward({ missionHistoryId: mission.missionHistoryId });
          this.pickedResult = pickResult;
          this.fetchUserGroupInfo();
          const timeout3s = new Promise((resolve) => {
            setTimeout(() => {
              resolve();
            }, 3000);
          });
          await timeout3s;
          this.pickedResult = null;
        }
        api.mission.fetchMissions({ regionBlockCode }).then(missions => {
          this.missions = missions;
        });
      }
    });
  }
}
</script>

