<template>
  <div id="app">
    <canvas id="cvs" :width="w" :height="h" @touchmove="handleMouseMove"></canvas>
  </div>
</template>

<script>
import '@/assets/style/global.scss';
import '@/assets/style/variables.scss';

let mouseX = 0;
let mouseY = 0;

const gridVertices = [
  -100.0, 0.0, -100.0,
  100.0, 0.0, -100.0,
  -100.0, 0.0, 100.0,
  100.0, 0.0, 100.0
];

// position, normal, texCoord
const squareVertices = [
  // front
  // triangle1
  -0.5,0.5,0.5,0.0,0.0,1.0,      0,0,
  0.5,0.5,0.5,0.0,0.0,1.0,      1,0,
  -0.5,-0.5,0.5,0.0,0.0,1.0,      0,1,

  // triangle2
  0.5,0.5,0.5,0.0,0.0,1.0,      1,0,
  -0.5,-0.5,0.5,0.0,0.0,1.0,      0,1,
  0.5,-0.5,0.5,0.0,0.0,1.0,      1,1,

  // back
  // triangle1
  -0.5,0.5,-0.5,0.0,0.0,-1.0,      0,0,
  0.5,0.5,-0.5,0.0,0.0,-1.0,      1,0,
  -0.5,-0.5,-0.5,0.0,0.0,-1.0,      0,1,

  // triangle2
  0.5,0.5,-0.5,0.0,0.0,-1.0,      1,0,
  -0.5,-0.5,-0.5,0.0,0.0,-1.0,      0,1,
  0.5,-0.5,-0.5,0.0,0.0,-1.0,      1,1,

  // left
  // triangle1
  -0.5,0.5,-0.5,-1.0,0.0,0.0,      0,0,
  -0.5,0.5,0.5,-1.0,0.0,0.0,      0,1,
  -0.5,-0.5,-0.5,-1.0,0.0,0.0,      1,0,

  // triangle2
  -0.5,-0.5,0.5,-1.0,0.0,0.0,      1,1,
  -0.5,0.5,0.5,-1.0,0.0,0.0,      0,1,
  -0.5,-0.5,-0.5,-1.0,0.0,0.0,      1,0,

  // right
  // triangle1
  0.5,0.5,-0.5,1.0,0.0,0.0,      0,0,
  0.5,0.5,0.5,1.0,0.0,0.0,      0,1,
  0.5,-0.5,-0.5,1.0,0.0,0.0,      1,0,

  // triangle2
  0.5,-0.5,0.5,1.0,0.0,0.0,      1,1,
  0.5,0.5,0.5,1.0,0.0,0.0,      0,1,
  0.5,-0.5,-0.5,1.0,0.0,0.0,      1,0,

  // top
  // triangle1
  -0.5,0.5,-0.5,0.0,1.0,0.0,      0,0,
  -0.5,0.5,0.5,0.0,1.0,0.0,      0,1,
  0.5,0.5,0.5,0.0,1.0,0.0,      1,1,

  // triangle2
  0.5,0.5,0.5,0.0,1.0,0.0,      1,1,
  0.5,0.5,-0.5,0.0,1.0,0.0,      1,0,
  -0.5,0.5,-0.5,0.0,1.0,0.0,      0,0,

  // bottom
  // triangle1
  -0.5,-0.5,-0.5,0.0,-1.0,0.0,      0,0,
  -0.5,-0.5,0.5,0.0,-1.0,0.0,      0,1,
  0.5,-0.5,0.5,0.0,-1.0,0.0,      1,1,

  // triangle2
  0.5,-0.5,0.5,0.0,-1.0,0.0,      1,1,
  0.5,-0.5,-0.5,0.0,-1.0,0.0,      1,0,
  -0.5,-0.5,-0.5,0.0,-1.0,0.0,      0,0,
]

function createShaderProgram(gl, vsSource, fsSource) {
  const vertexShaderSource = vsSource;
  const fragmentShaderSource = fsSource;

  const vs = gl.createShader(gl.VERTEX_SHADER);
  gl.shaderSource(vs, vertexShaderSource);
  gl.compileShader(vs);

  if (!gl.getShaderParameter(vs, gl.COMPILE_STATUS)) {
    console.error('An error occurred compiling the vertex shaders: ' + gl.getShaderInfoLog(vs));
    gl.deleteShader(vs);
    return null;
  }

  const fs = gl.createShader(gl.FRAGMENT_SHADER);
  gl.shaderSource(fs, fragmentShaderSource);
  gl.compileShader(fs);

  if (!gl.getShaderParameter(fs, gl.COMPILE_STATUS)) {
    console.error('An error occurred compiling the fragment shaders: ' + gl.getShaderInfoLog(fs));
    gl.deleteShader(fs);
    return null;
  }

  const shaderProgram = gl.createProgram();
  gl.attachShader(shaderProgram, vs);
  gl.attachShader(shaderProgram, fs);
  gl.linkProgram(shaderProgram);
  return shaderProgram;
}

const FSIZE = 4;

export default {
  data() {
    return {
      w: 750,
      h: 750
    }
  },
  mounted() {
    const gl = document.querySelector('#cvs').getContext('webgl', {
      stencil: true,
      antialias: false
    });

    const squareVao = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, squareVao);
    const squareData = new Float32Array(squareVertices);
    gl.bufferData(gl.ARRAY_BUFFER, squareData, gl.STATIC_DRAW);

    const gridVao = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, gridVao);

    const gridData = new Float32Array(gridVertices);
    gl.bufferData(gl.ARRAY_BUFFER, gridData, gl.STATIC_DRAW);

    const gridShaderProgram = createShaderProgram(gl, 
      `
        attribute vec3 aPosition;

        uniform mat4 model;
        uniform mat4 view;
        uniform mat4 projection;

        varying vec3 FragPos;

        void main() {
          FragPos = vec3(model * vec4(aPosition, 1.0));
          gl_Position = projection * view * vec4(FragPos, 1.0);
        }
      `,
      `
        precision mediump float;
        varying vec3 FragPos;

        void main() {
          vec2 xz = FragPos.xz;
          float line_width = pow(-xz.y, 0.9) * .02;
          xz *= 2.;
          vec2 f = fract(xz);
          vec4 color;
          if (f.x <= line_width || f.y <= line_width) {
            color = vec4(1.,1.,1.,.2);
          } else {
            color = vec4(0.);
          }
          gl_FragColor = color;
        }
      `
    );

    const shaderProgram = createShaderProgram(gl, 
      `
        attribute vec3 aPosition;
        attribute vec3 aNormal;
        attribute vec2 aTexCoord;

        uniform mat4 model;
        uniform mat4 view;
        uniform mat4 projection;
        uniform mat4 mouse;

        varying vec3 Normal;
        varying vec3 FragPos;
        varying vec3 Color;
        varying vec2 TexCoord;
        void main() {
          FragPos = vec3(model * vec4(aPosition, 1.0));
          Normal = vec3(model * vec4(aNormal, 1.0));
          Color = aPosition * .5 + .5;
          TexCoord = aTexCoord;
          
          gl_Position = projection * view * vec4(FragPos, 1.0);
        }
      `,
      `
        precision mediump float;
        varying vec3 Normal;
        varying vec3 FragPos;
        varying vec3 Color;
        varying vec2 TexCoord;

        uniform float utime;

        const vec3 light = vec3(2.,2.,0.);
        const vec3 camera = vec3(0.,0.,1.);

        vec2 rotate2d(vec2 st, float a) {
          float c = cos(a);
          float s = sin(a);
          mat2 t = mat2(c,-s,s,c);
          return t*st;
        }

        #define DELTA 0.02
        #define OFFSET 0.002
        #define SCALE 2.
        #define IRON_PLATE_SCALE 10.

        float sdfcircle(vec2 st, vec2 c, float r) {
          return length(st-c)-r;
        }

        float avr(vec3 c) {
          return (c.r+c.g+c.b)/3.;
        }

        float lotus(vec2 st) {
            float d1 = sdfcircle(st,vec2(0.),.75);
            float d2 = sdfcircle(st,vec2(1.),.75);
            if (d1 > 0.||d2>.0) {
                return 0.;
            } else {
                return 1.;
            }
        }

        float iron_plate(vec2 st) {
            st*=IRON_PLATE_SCALE;
            vec2 i = floor(st);
            vec2 f = fract(st);
            f = f * .5 + .25;
            if (mod((i.x), 2.)==0.) {
                f.x=1.-f.x;
            }
            if (mod((i.x+i.y), 2.)==0.) {
                f=0.1*f;
            }
            float c1 = lotus(f-vec2(-DELTA,-DELTA));
            float c2 = lotus(f-vec2(DELTA,DELTA));
            float c3 = lotus(f-vec2(-DELTA,DELTA));
            float c4 = lotus(f-vec2(DELTA,-DELTA));

            float color = (c1+c2+c3+c4)/4.;
            return color;
        }

        vec3 iron_plate_nm(vec2 st) {
            float ac = iron_plate(vec2(st.x+OFFSET,st.y));
            float bc = iron_plate(vec2(st.x-OFFSET,st.y));
            float cc = iron_plate(vec2(st.x,st.y+OFFSET));
            float dc = iron_plate(vec2(st.x,st.y-OFFSET));

            ac*=ac;
            bc*=bc;
            cc*=cc;
            dc*=dc;

            float e = SCALE * ac - SCALE * bc;
            float f = SCALE * cc - SCALE * dc;

            vec3 s = vec3(1.,0.,e);
            vec3 t = vec3(0.,1.,f);

            vec3 cross_p = cross(s, t);
            vec3 n = cross_p / length(cross_p);

            n = n*.5 +.5;
            return n;
        }

        void main() {
          vec3 objectColor = vec3(.3);
          vec3 rlight = light;
          rlight.xz = rotate2d(light.xz, utime*2.);
          vec3 norm = normalize(Normal);
          vec3 preturb = iron_plate_nm(TexCoord);
          norm = preturb;
          norm = normalize(norm);
          vec3 lightDir = normalize(rlight-FragPos);
          vec3 ambient = 0.5 * objectColor;
          float diff = max(dot(lightDir, norm),0.);
          vec3 diffuse = diff * objectColor;

          vec3 reflectDir = reflect(-lightDir, norm);
          float spec = pow(max(dot(reflectDir, camera), 0.), 256.);
          vec3 specular = spec * objectColor;

          vec3 result = ambient + .8*diffuse+1.0*specular;
          gl_FragColor = vec4(result, 1.);
        }
      `
    );

    const outlineShaderProgram = createShaderProgram(gl,
      `
        attribute vec3 aPosition;
        uniform mat4 model;
        uniform mat4 view;
        uniform mat4 projection;
        uniform mat4 mouse;

        void main() {
          gl_Position = projection * view * model * vec4(aPosition, 1.0);
        }
      `,
      `
        void main() {
          gl_FragColor = vec4(1.,.5,0.,1.);
        }
      `
    );

    // draw call
    let time = 0;
    let then = 0;
    function draw(now) {
      if (!now) now = 0;
      now *= 0.0005;
      const deltaTime = now - then;
      then = now;

      gl.enable(gl.DEPTH_TEST);
      gl.enable(gl.STENCIL_TEST);
      gl.blendFunc(gl.SRC_ALPHA, gl.ONE);
      gl.disable(gl.BLEND);


      gl.clearColor(0.3,0.3,0.3,1.0);
      gl.clearDepth(1.0);

      gl.clear(gl.COLOR_BUFFER_BIT|gl.DEPTH_BUFFER_BIT|gl.STENCIL_BUFFER_BIT);

      gl.bindBuffer(gl.ARRAY_BUFFER, squareVao);

      // ================== cube

      // position attrib
      gl.vertexAttribPointer(0, 3, gl.FLOAT, false, 32, 0);
      gl.enableVertexAttribArray(0);
      gl.bindAttribLocation(shaderProgram, 0, 'aPosition');

      // normal attrib
      gl.vertexAttribPointer(1, 3, gl.FLOAT, false, 32, 12);
      gl.enableVertexAttribArray(1);
      gl.bindAttribLocation(shaderProgram, 1, 'aNormal');

      // normal attrib
      gl.vertexAttribPointer(2, 2, gl.FLOAT, false, 32, 24);
      gl.enableVertexAttribArray(2);
      gl.bindAttribLocation(shaderProgram, 2, 'aTexCoord');

      gl.linkProgram(shaderProgram);
      gl.useProgram(shaderProgram);

      // model uniform
      let modelMatrix = mat4.create();
      let rml = gl.getUniformLocation(shaderProgram, 'model');
      gl.uniformMatrix4fv(rml, false, modelMatrix);

      // view matrix
      let viewMatrix = mat4.create();
      const mx = 2*mouseX;
      const my = 8*mouseY;
      const x = Math.cos(mx) * 0 - Math.sin(mx) * 3;
      const z = Math.sin(mx) * 0 + Math.cos(mx) * 3;
      mat4.lookAt(viewMatrix, [x,-my+1.0,z],[0,0,0],[0,1,0]);

      let vml = gl.getUniformLocation(shaderProgram, 'view');
      gl.uniformMatrix4fv(vml, false, viewMatrix);

      // projection uniform
      let projectionMatrix = mat4.create();
      mat4.perspective(projectionMatrix, 45 * Math.PI / 180, 1, 0.1,100);

      let pml = gl.getUniformLocation(shaderProgram, 'projection');
      gl.uniformMatrix4fv(pml, false, projectionMatrix);

      // mouse uniform
      let mouseMatrix = mat4.create();

      let mml = gl.getUniformLocation(shaderProgram, 'mouse');
      gl.uniformMatrix4fv(mml, false, mouseMatrix);

      // time uniform
      let tml = gl.getUniformLocation(shaderProgram, 'utime');
      gl.uniform1f(tml, time);

      gl.clear(gl.STENCIL_BUFFER_BIT);
      gl.stencilFunc(gl.ALWAYS, 1, 0xff);
      gl.stencilOp(gl.REPLACE, gl.KEEP, gl.REPLACE);
      gl.stencilMask(0xff);

      gl.drawArrays(gl.TRIANGLES, 0, 36);

      // ================== orange outline

      // position attrib
      gl.vertexAttribPointer(0, 3, gl.FLOAT, false, 32, 0);
      gl.enableVertexAttribArray(0);
      gl.bindAttribLocation(outlineShaderProgram, 0, 'aPosition');

      gl.linkProgram(outlineShaderProgram);
      gl.useProgram(outlineShaderProgram);

      // model uniform
      modelMatrix = mat4.create();
      mat4.scale(modelMatrix, modelMatrix, [1.02,1.02,1.02]);

      rml = gl.getUniformLocation(outlineShaderProgram, 'model');
      gl.uniformMatrix4fv(rml, false, modelMatrix);

      // view matrix
      vml = gl.getUniformLocation(outlineShaderProgram, 'view');
      gl.uniformMatrix4fv(vml, false, viewMatrix);

      // projection uniform
      pml = gl.getUniformLocation(outlineShaderProgram, 'projection');
      gl.uniformMatrix4fv(pml, false, projectionMatrix);

      // mouse uniform
      mml = gl.getUniformLocation(outlineShaderProgram, 'mouse');
      gl.uniformMatrix4fv(mml, false, mouseMatrix);

      gl.stencilFunc(gl.NOTEQUAL, 1, 0xff);
      gl.stencilMask(0x00);
      // gl.drawArrays(gl.TRIANGLES, 0, 36);

      gl.disable(gl.STENCIL_TEST);

      gl.enable(gl.BLEND);

      // ================== world grid

      // draw grid
      gl.bindBuffer(gl.ARRAY_BUFFER, gridVao);
      // position attrib
      gl.vertexAttribPointer(0, 3, gl.FLOAT, false, 12, 0);
      gl.enableVertexAttribArray(0);
      gl.bindAttribLocation(gridShaderProgram, 0, 'aPosition');

      gl.linkProgram(gridShaderProgram);
      gl.useProgram(gridShaderProgram);

      modelMatrix = mat4.create();
      rml = gl.getUniformLocation(gridShaderProgram, 'model');
      gl.uniformMatrix4fv(rml, false, modelMatrix);

      vml = gl.getUniformLocation(gridShaderProgram, 'view');
      gl.uniformMatrix4fv(vml, false, viewMatrix);

      pml = gl.getUniformLocation(gridShaderProgram, 'projection');
      gl.uniformMatrix4fv(pml, false, projectionMatrix);

      gl.drawArrays(gl.TRIANGLE_STRIP, 0, 4);

      time+=deltaTime;

      window.requestAnimationFrame((timestamp) => draw(timestamp));
    }
    draw();
  },
  methods: {
    handleMouseMove(e) {
      const x = e.touches[0].clientX;
      const normalizedX = x / this.w * 4 - 1;
      const y = e.touches[0].clientY;
      const normalizedY = y / this.h * 4 - 1;
      mouseX = normalizedX;
      mouseY = -normalizedY;
    }
  }
};
</script>

<style lang="scss">
body {
  margin: 0;
  overflow: hidden;
}
#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
canvas {
  transform-origin: top left;
  transform: scale(0.5);
}
</style>
