<template>
  <div class="commodity-detail" v-if="commodity !== null">
    <img class="commodity-detail__image" :src="commodity.articleImage"></img>

    <goto-home></goto-home>
  </div>
</template>

<script>
import LoadingModal from '@/components/LoadingModal';
import GotoHome from '@/components/GotoHome';
import api from '@/api';

export default {
  components: {
    GotoHome
  },

  data () {
    return {
      commodity: null
    };
  },

  created () {
    LoadingModal.show();

    const o2oCommodityId = this.$route.params.id;
    api.recommendedCommodity.fetchRecommendedCommodity({ o2oCommodityId }).then(commodity => {
      this.commodity = commodity;

      LoadingModal.hide();
    });
  }
};
</script>

<style lang="scss" scoped>
@import '~style/variables.scss';

.commodity-detail {
  background-color: $background-color-gray;

  &__image {
    width: 100vw;
    height: auto;
  }
}
</style>