import axios from 'axios';
import signatureService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;
export default {
  fetchCampaignList ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/campaign/`,
        {
          params: { timestamp, nonce, signature, regionBlockCode, page: 1, pageSize: 1000 }
        }
      ).then(res => {
        const fetchCampaignListSuccess = res.data.code === 0 ? true : false;
        if (fetchCampaignListSuccess) {
          const campaignList = res.data.data.list;
          resolve(campaignList);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  fetchCampaignDetail ({ campaignDetailId, regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/campaign/${campaignDetailId}`,
        {
          params: { timestamp, nonce, signature, regionBlockCode }
        }
      ).then(res => {
        const fetchCampaignDetailSuccess = res.data.code === 0 ? true : false;
        if (fetchCampaignDetailSuccess) {
          const campaignDetail = res.data.data;
          resolve(campaignDetail);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  campaignGift ({ campaignId, stampId, regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.post(
        `${baseUrl}/app/v1/campaign/gift`,
        {
          timestamp, nonce, signature, regionBlockCode, campaignId, stampId
        }
      ).then(res => {
        const fetchCampaignGiftSuccess = res.data.code === 0 ? true : false;
        if (fetchCampaignGiftSuccess) {
          const campaignGift = res.data.data;
          resolve(campaignGift);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  campaignGiftSaveAddress ({ saveAddressInfo }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.post(
        `${baseUrl}/app/v1/campaign/saveAddress`,
        {
          timestamp, nonce, signature,...saveAddressInfo
        },
      ).then(res => {
        const fetchCampaignGiftSuccess = res.data.code === 0 ? true : false;
        if (fetchCampaignGiftSuccess) {
          const saveState = res.data;
          resolve(saveState);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  getCampaignProducts({ regionBlockCode, campaignDetailId }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/commodityAndType/${campaignDetailId}`,
        {
          params: { timestamp, nonce, signature, regionBlockCode }
        }
      ).then(res => {
        const getCampaignProducts = res.data.code === 0 ? true : false;
        if (getCampaignProducts) {
          const campaignProducts = res.data;
          resolve(campaignProducts);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },

  campaignBarrage({ regionBlockCode, campaignId }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/campaign/barrage`,
        {
          params: { timestamp, nonce, signature, regionBlockCode, campaignId }
        }
      ).then(res => {
        const campaignBarrageSuccess = res.data.code === 0 ? true : false;
        if (campaignBarrageSuccess) {
          const campaignBarrage = res.data.data;
          resolve(campaignBarrage);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  getSendAddress({ regionBlockCode, campaignId }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/campaign/address`,
        {
          params: { timestamp, nonce, signature, regionBlockCode, campaignId }
        }
      ).then(res => {
        const geSendtAddressSuccess = res.data.code === 0 ? true : false;
        if (geSendtAddressSuccess) {
          const geSendtAddress = res.data.data;
          resolve(geSendtAddress);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  getUserGiftInfo({ userGiftId }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${baseUrl}/app/v1/campaign/userGift?userGiftId=${userGiftId}`;
    return new Promise((resolve, reject) => {
      axios.get(url,
        {
          params: { timestamp, nonce, signature }
        }
      ).then(res => {
        const fetchUserGiftInfoSuccess = res.data.code === 0 ? true : false;
        if (fetchUserGiftInfoSuccess) {
          const userGiftInfo = res.data;
          resolve(userGiftInfo);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
};
