<template>
  <div class="edit-order" v-if="orderDetail">
    <div class="edit-order__info">
      <div class="edit-order__info__image">
        <img :src="orderDetail.imageSmall">
      </div>
      <div class="edit-order__info__content">
        <div class="edit-order__info__content__title">{{ orderDetail.commodityName }}</div>
        <div class="edit-order__info__content__desc">
          <div class="edit-order__info__content__desc__price"><span>￥</span>{{ orderDetail.sellPrice | toRMB }}</div>
          <div class="edit-order__info__content__desc__quantity">x{{ orderDetail.qty }}</div>
        </div>
      </div>
    </div>

    <div class="edit-order__notice">
      <div class="edit-order__notice__title">预约须知</div>
      <div class="edit-order__notice__content" v-html="notice"></div>
    </div>

    <div class="edit-order__select-take-date">
      <div class="edit-order__select-take-date__info">
        <div class="edit-order__select-take-date__info__label">请选择提货时间</div>
        <div class="edit-order__select-take-date__info__date" v-if="takeDate">{{ takeDate | formatDate }}</div>
        <select v-model="takeDate">
          <option disabled selected value>请选择提货时间</option>
          <option :value ="date" v-for="date of orderDetail.canTakeDate" :key="date">{{ date | formatDate }}</option>
        </select>
      </div>
      <div class="edit-order__select-take-date__icon"></div>
    </div>

    <div class="edit-order__select-take-shop" @click="gotoEcChooseShopList">
      <div class="edit-order__select-take-shop__info">
        <div class="edit-order__select-take-shop__info__label">提货门店</div>
        <template v-if="shopInfo">
          <div class="edit-order__select-take-shop__info__shop-address">{{ shopInfo.shopName }} {{ shopInfo.address }}</div>
          <div class="edit-order__select-take-shop__info__business-time">营业时间：{{ shopInfo.businessTime }}</div>
        </template>
      </div>
      <div class="edit-order__select-take-shop__icon"></div>
    </div>

    <div class="edit-order__confirm-edit-order-button" v-if="takeDate && shopId" @click="editOrder">
      确认修改
    </div>
  </div>
</template>

<script>
import api from '@/api';
import envService from '@/services/env';
import ecOrderInfoModuleOption from '@/store/ec-order-info/ec-order-info';
import LoadingModal from '@/components/LoadingModal';

export default {
  data () {
    return {
      orderDetail: null,
      notice: '1.支付后请按选择的日期和时间段前往指定的门店提货<br>2.非指定门店不可提货<br>3.选定日期和时间段前不可提货<br>4.选定日期和时间段内未前往门店提货时，门店无法保证商品库存和品质，因此造成的损失将由用户自己承担',
    }
  },

  created () {
    this.$store.registerModule('ecOrderInfo', ecOrderInfoModuleOption);
    this.fetchPayOrderDetail();
  },

  computed: {
    shopId() {
      return this.$store.state.ecOrderInfo.shopId;
    },

    shopInfo() {
      const regionBlockCode = envService.getRegionBlockCode();
      const shopsAvailableForRegion =  this.$store.getters['shop/shopsAvailableForRegion'](regionBlockCode);
      return shopsAvailableForRegion ? this.$store.getters['shop/shopsForRegion'](regionBlockCode).find(shop => shop.shopId === this.shopId) : null;
    },

    takeDate: {
      get() {
        return this.$store.state.ecOrderInfo.takeDate;
      },
      set(takeDate) {
        this.$store.commit('ecOrderInfo/setTakeDate', takeDate);
      }
    }
  },

  methods: {
    fetchPayOrderDetail() {
      LoadingModal.show();
      const orderId = this.$route.params.orderId;
      api.ec.fetchPayOrderDetail({ orderId }).then(res => {
        this.orderDetail = res.data.data;
        const shopId = this.orderDetail.shopId;
        if (!this.shopId) {
          this.$store.commit('ecOrderInfo/setShopId', shopId);
        }
        if (!this.takeDate) {
          const date = new Date(this.orderDetail.takeDate);
          const year = date.getFullYear();
          const month = ('0' + (date.getMonth()+1)).slice(-2);
          const getDate = ('0' + date.getDate()).slice(-2);
          const takeDate = `${year}-${month}-${getDate}`;
          this.$store.commit('ecOrderInfo/setTakeDate', takeDate);
        }
      }).finally(() => LoadingModal.hide());
    },

    editOrder() {
      const orderId = this.orderDetail.orderId;
      const takeDate = this.takeDate;
      const shopId = this.shopId;
      api.ec.editOrder({ orderId, takeDate, shopId }).then(res => {
        this.$router.back();
      })
    },

    gotoEcChooseShopList() {
      const shopUseFlg = this.orderDetail.shopUseFlg;
      const shopList = this.orderDetail.shopList;
      this.$router.push({ name: 'ec-choose-shop-list', params: { shopUseFlg, shopList } });
    }
  },

  filters: {
    toRMB(price) {
      return price.toFixed(1);
    },

    formatDate: function (time) {
      const weekday = ['日','一','二','三','四','五','六'];
      const date = new Date(time);
      const year = date.getFullYear();
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const day = ('0' + date.getDate()).slice(-2);
      const weekdayLength = date.getDay();
      return `${year}/${month}/${day}(星期${weekday[weekdayLength]})`;
    }
  }
}
</script>

