import axios from 'axios';
import signApiService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchJsSdkSignatureData ({ url }) {
    return new Promise((resolve, reject) => {
      const { timestamp, nonce, signature } = signApiService.generateMainApiSignature();

      axios.get(`${baseUrl}/app/v1/weixin/authorize/getsignature/`, {
        params: { timestamp, nonce, signature, url }
      }).then(res => {
        const isSuccess = (res.data.code === 0) ? true : false;

        if (isSuccess) {
          resolve(res.data.data);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
};
