<template>
  <div class="choose-address">
    <div class="choose-address__wrapper" v-if="addresses !== null">
      <div class="choose-address__container">
        <div class="choose-address__content" v-for="(address, index) in addresses" :key="index">
          <div class="choose-address__content__info" @click="chooseAddress(address)">
            <span class="choose-address__content__info__address-name">{{ address.addressName }}</span>
            <div class="choose-address__content__info__tag-container" v-if="address.tags[0] !== ''">
              <div class="choose-address__content__info__tag home" v-if="address.tags[0] === '家'">{{ address.tags[0] }}</div>
              <div class="choose-address__content__info__tag company" v-if="address.tags[0] === '公司'">{{ address.tags[0] }}</div>
              <div class="choose-address__content__info__tag school" v-if="address.tags[0] === '学校'">{{ address.tags[0] }}</div>
            </div>

            <span class="choose-address__content__info__detail-address">{{ address.addressExtraInfo}}</span>

            <span class="choose-address__content__info__contact-name">{{ address.name }}</span>
            <span class="choose-address__content__info__phone-number">{{ address.phoneNumber }}</span>
          </div>
          <span class="choose-address__edit" @click="editAddress(index)">编辑</span>
        </div>
      </div>
    </div>

    <div class="choose-address__empty" v-else>
      <p class="choose-address__empty__icon"></p>
      <p class="choose-address__empty__text">还未添加地址</p>
    </div>

    <div class="choose-address__add-address" @click="addAddress">新增常用地址</div>
  </div>
</template>

<script>
import Toast        from '@/components/Toast';
import qqLbsService from '@/services/qq-lbs';
import api          from '@/api/index';
import envService   from '@/services/env';
import LoadingModal from '@/components/LoadingModal';

export default {
  data() {
    return {
      isAddAddressVisible: false,
      isEditAddressVisible: false,

      isTagHome: false,
      isTagCompany: false,
      isTagSchool: false,

      addresses: null
    }
  },
  created() {
    qqLbsService.init();
    this.fetchUserAddressInfo();
    this.showAddOrEditAddressSuccessToast();
  },
  methods: {
    showAddOrEditAddressSuccessToast() {
      const needShowEditAddressSuccessToast = this.$store.state.campaignExchangeAddress.needShowEditAddressSuccessToast;
      const needShowAddAddressSuccessToast  = this.$store.state.campaignExchangeAddress.needShowAddAddressSuccessToast;
      if (needShowEditAddressSuccessToast) {
        Toast.show({ msg: '修改地址信息成功' });
      } else if (needShowAddAddressSuccessToast) {
        Toast.show({ msg: '新增地址信息成功' });
      }
      this.$store.commit('campaignExchangeAddress/hideEditAddressSuccessToast');
      this.$store.commit('campaignExchangeAddress/hideAddAddressSuccessToast');
    },
    fetchUserAddressInfo() {
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();

      api.address.fetch({ regionBlockCode }).then(res => {
        LoadingModal.hide();
        const isFetchAddressInfoSuccess = res.data.code === 0 ? true : false;
        const hasAddressInfo = res.data.data.length > 0 ? true : false;

        if (isFetchAddressInfoSuccess) {
          if (hasAddressInfo) {
            this.addresses = res.data.data;
          }
        } else {
          LoadingModal.hide();
          Toast.show({ msg: '获取地址信息失败, 请稍后再试' })
        }
      }).catch(err => {
        LoadingModal.hide();
        Toast.show({ msg: '获取地址信息失败, 请稍后再试' })
      })
    },
    chooseAddress(address) {
      this.$store.commit('campaignExchangeAddress/setChooseAddressInfo', address);
      this.$router.go(-1);
    },
    editAddress(index) {
      const editAddressInfo = this.addresses[index];
      this.$store.commit('campaignExchangeAddress/setEditAddressInfo', editAddressInfo);
      this.$router.push({ path: '/campaign-post-exchange/edit-address' });
    },
    addAddress() {
      this.$router.push({ path: '/campaign-post-exchange/add-address' });
    }
  }
};
</script>

