export default {
  getType(coupon) {
      if(coupon.couponType === 1) {
        return 'couponcouponType:freeOnItem'; // 免费兑换券
      }
      else if (coupon.couponType === 2) {
        return 'couponcouponType:discountOnReachingTotal'; // 满减券
      }
      else if (coupon.couponType === 3) {
        return 'couponType:discountOnItem'; // 商品立减券
      }
  },
};
