<template>
   <div class="compaign-detail__button-region">
      <div>
          <goto-home></goto-home>
      </div>
      <div>
        <button class="compaign-detail__button-region__transfer" v-if="isTransfer === true" @click="goToCampaignTransferPage">转赠</button>
        <button class="compaign-detail__button-region__no-transfer" v-else >转赠</button>
        <button class="compaign-detail__button-region__exchange" v-if="isExchange === true && rewardType === 1" @click="campaignnExchang">兑换</button>
        <button class="compaign-detail__button-region__no-exchange" v-else-if="!isExchange && rewardType === 1">兑换</button>
        <button class="compaign-detail__button-region__exchange" v-else-if="isExchange === true && rewardType === 0" @click="campaignnExchang">抽奖</button>
        <button class="compaign-detail__button-region__no-exchange " v-else-if="isExchange === false && rewardType === 0" >抽奖</button>
      </div>
    </div>
</template>

<script>
import _ from 'lodash';

import GotoHome from '@/components/GotoHome';

export default {
  props: [
    'isTransfer',
    'isExchange',
    'campaignId',
    'totalCampaignNum',
    'rewardType'
  ],
  data() {
    return {

    };
  },
  components: {
    GotoHome
  },
  methods: {
    goToCampaignTransferPage: _.debounce(function() {
      wx.miniProgram.navigateTo({
          url: `/pages/campaign-transfer/campaign-transfer?campaignId=${this.campaignId}&stampNum=${this.totalCampaignNum}`,
          success:() => {
            setTimeout(() => {
              this.$emit('isCampaignnTransferModal');
            }, 1000);
          }
        });
    }, 1000),
    campaignnExchang: function() {
      this.$emit('campaignnExchang');
    }
  }
};
</script>

