<template>
  <div class="pull-down-refresh" @touchstart="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd" @touchcancel="handleTouchCancel">
    <div :style="{ transform: 'translateY(' + wrapperYOffset + 'px)' }">
      <div class="pull-down-refresh__refreshing-hint" v-if="isRefreshing">
        <p>…</p>
        <p>正在刷新</p>
      </div>

      <div class="pull-down-refresh__release-hint" v-else-if="needRefresh">
        <p>⇡</p>
        <p>松开以刷新</p>
      </div>

      <div class="pull-down-refresh__pull-hint" v-else>
        <p>⇣</p>
        <p>下拉以刷新</p>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
const PULL_DOWN_DISTANCE_THRESHOLD = 200;
const PULL_DOWN_RESISTANCE = 0.5;

export default {
  props: {
    isRefreshing: Boolean
  },
  data() {
    return {
      touchStartY: null,
      touchMoveY: null,
      scrollTop: null,
      isPullingDown: null,
      lastRefreshTimeStamp: null,
      touchInitialYDirection: null
    };
  },
  computed: {
    yDistance() {
      return this.touchMoveY - this.touchStartY;
    },
    needRefresh() {
      return this.isPullingDown && this.yDistance > PULL_DOWN_DISTANCE_THRESHOLD;
    },
    wrapperYOffset() {
      if (this.isPullingDown) {
        return this.yDistance * (1 - PULL_DOWN_RESISTANCE);
      } else {
        return 0;
      }
    }
  },
  methods: {
    _resetData() {
      this.touchStartY = null;
      this.touchMoveY = null;
      this.scrollTop = null;
      this.isPullingDown = null;
      this.touchInitialYDirection = null;
    },

    handleTouchStart(e) {
      const y = e.touches[0].clientY;
      this.touchStartY = y;
      this.touchMoveY = y;
      this.scrollTop = this.$el.scrollTop;
    },
    handleTouchMove(e) {
      const y = e.touches[0].clientY;
      this.touchMoveY = y;
      this.scrollTop = this.$el.scrollTop;
      if (this.touchInitialYDirection === null) {
        if (this.yDistance > 0) {
          this.touchInitialYDirection = 'downward';
        } else {
          this.touchInitialYDirection = 'upward';
        }
      }
      if (this.isPullingDown === null) {
        if (this.touchInitialYDirection === 'downward' && this.scrollTop <= 0) {
          this.isPullingDown = true;
        } else {
          this.isPullingDown = false;
        }
      }
      if (this.isPullingDown) {
        e.preventDefault();
      }
    },
    handleTouchEnd(e) {
      if (this.needRefresh) {
        const lastRefreshTimeStamp = this.lastRefreshTimeStamp;
        const currentTimeStamp = (new Date()).getTime();
        const FIVE_SECONDS = 5000;
        console.log(currentTimeStamp - lastRefreshTimeStamp > FIVE_SECONDS);

        if (lastRefreshTimeStamp === null ||
              currentTimeStamp - lastRefreshTimeStamp > FIVE_SECONDS) {
          this.$emit("refresh");
          this.lastRefreshTimeStamp = currentTimeStamp;
        }
      }

      this._resetData();
    },
    handleTouchCancel(e) {
      this._resetData();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~style/variables.scss";

.pull-down-refresh {
  width: inherit;
  height: 100vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  &__release-hint, &__pull-hint, &__refreshing-hint {
    display: table-cell;
    width: 100vw;
    height: 20vw;
    text-align: center;
    vertical-align: middle;
    p:nth-child(1) {
      margin: 0;
      font-size: $font-size-large;
      color: #666666;
      line-height: 8vw;
    }
    p:nth-child(2) {
      margin: 0;
      font-size: $font-size-small;
      color: #666666;
      line-height: 8vw;
    }
  }
  &__release-hint, &__pull-hint {
    position: absolute;
    top: -20vw;
  }
}
</style>

