<template>
  <div class="talk-topic-detail" v-if="talkTopicDetail !== null">
    <div class="talk-topic-detail__banner">
      <img :src="talkTopicDetail.themeImage" />
    </div>

    <div class="talk-topic-detail__content">
      <div class="talk-topic-detail__title">{{ talkTopicDetail.subTitle }}</div>
      <div class="talk-topic-detail__lookers-count">{{ talkTopicDetail.onlookerCount }}人围观</div>
      <div class="talk-topic-detail__article" v-html="talkTopicDetail.article"></div>
    </div>

    <div
      v-if="talkTopicDetail.commodityList.length > 0"
      class="talk-topic-detail__show-commodity-button"
      @click="commodityListPopupVisible = true;">
      商品({{ talkTopicDetail.commodityList.length }})
    </div>

    <div v-if="commodityListPopupVisible">
      <commodity-list-popup
        :commodities="talkTopicDetail.commodityList"
        @close="commodityListPopupVisible = false;">
      </commodity-list-popup>
    </div>

    <goto-home></goto-home>
  </div>
</template>

<script>
import CommodityListPopup from './TalkTopicDetail/CommodityListPopup';
import GotoHome from '@/components/GotoHome';
import popularStatisticsService from '@/services/popular-statistics';
import api from '@/api';

export default {
  components: {
    CommodityListPopup,
    GotoHome
  },

  data () {
    return {
      talkTopicDetail: null,
      commodityListPopupVisible: false
    };
  },

  created () {
    const talkTopicId = this.$route.params.id;
    api.talkTopic.fetchTalkTopicDetail({ talkTopicId }).then(talkTopicDetail => {
      this.talkTopicDetail = talkTopicDetail;
    });

    popularStatisticsService.recordTalkTopicPopularStatistics(talkTopicId);
  }
}
</script>

