<template>
  <div class="campaign-products">
    <div class="campaign-products__wrapper" v-if="productList !== null && productList.length > 0">
      <div class="campaign-products__item" v-for="(item, index) in productList" :key="index">
        <img class="campaign-products__item__image" :src="item.imgUrl"/>
        <div class="campaign-products__item__title">{{ item.commodityName }}</div>
        <div class="campaign-products__item__price-wrapper" v-if="showSellPrice">
          <span class="campaign-products__item__price-wrapper__rmb-sign">¥ </span>
          <span class="campaign-products__item__price-wrapper__number">{{ item.sellPrice | toPrice }}</span>
        </div>
      </div>
    </div>
    <div class="campaign-products__empty" v-else>
      <p class="campaign-products__empty__icon"></p>
      <p class="campaign-products__empty__text">暂无活动商品</p>
    </div>
  </div>
</template>

<script>
import api from '@/api';
import envService from '@/services/env';
import LoadingModal from '@/components/LoadingModal';

export default {
  data() {
    return {
      productList: [],
      showSellPrice: true
    };
  },
  created() {
    this.fetchCampaignProducts();
  },
  methods: {
    fetchCampaignProducts() {
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();
      const campaignDetailId = this.$route.params.campaignId;
      api.campaign.getCampaignProducts({ regionBlockCode, campaignDetailId }).then(res => {
        LoadingModal.hide();
        const campaignProducts = res.data.list;
        campaignProducts.forEach(element => {
          this.showSellPrice = element.largeClassificationId ? false : true;
        });
        this.productList = campaignProducts;
      }).catch(err => {
        LoadingModal.hide();
      });
    }
  },
  filters: {
    toPrice(value) {
      return Number(value).toFixed(2);
    }
  }
}
</script>

