import axios from 'axios';
import signatureService from '@/services/sign-api';

const BASE_URL = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchRecommendedCommodities ({ regionBlockCode }) {
    return new Promise((resolve, reject) => {
      const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
      const url = `${BASE_URL}/app/v1/mina/buying?regionBlockCode=${regionBlockCode}`;

      axios.get(url, { params: { timestamp, nonce, signature } })
           .then(res => {
             const isSuccess = res.data.code === 0 ? true : false;
             if (isSuccess) {
               resolve(res.data.data);
             } else {
               reject(res);
             }
           })
           .catch(err => {
             reject(err);
           });
    });
  },

  fetchRecommendedCommodity ({ o2oCommodityId }) {
    return new Promise((resolve, reject) => {
      const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
      const url = `${BASE_URL}/app/v1/mina/buying/detail?o2oCommodityId=${o2oCommodityId}`;

      axios.get(url, { params: { timestamp, nonce, signature } })
           .then(res => {
             const isSuccess = res.data.code === 0 ? true : false;
             if (isSuccess) {
               resolve(res.data.data);
             } else {
               reject(res);
             }
           })
           .catch(err => {
             reject(err);
           });
    });
  }
};