<template>
  <div class="confirm-order" v-if="orderDetail">
    <div class="confirm-order__info">
      <div class="confirm-order__info__image">
        <img :src="orderDetail.imageSmall">
      </div>
      <div class="confirm-order__info__content">
        <div class="confirm-order__info__content__title">{{ orderDetail.commodityName }}</div>
        <div class="confirm-order__info__content__desc">
          <div class="confirm-order__info__content__desc__price"><span>￥</span>{{ orderDetail.sellPrice | toRMB }}</div>
          <div class="confirm-order__info__content__desc__quantity">x{{ orderDetail.qty }}</div>
        </div>
      </div>
    </div>

    <div class="confirm-order__notice">
      <div class="confirm-order__notice__title">预约须知</div>
      <div class="confirm-order__notice__content" v-html="notice"></div>
    </div>

    <div class="confirm-order__select-take-date">
      <div class="confirm-order__select-take-date__info">
        <div class="confirm-order__select-take-date__info__label">请选择提货时间</div>
        <div class="confirm-order__select-take-date__info__date" v-if="takeDate">{{ takeDate | formatDate }}</div>
        <select v-model="takeDate">
          <option disabled selected value>请选择提货时间</option>
          <option :value ="date" v-for="date of orderDetail.canTakeDate" :key="date">{{ date | formatDate }}</option>
        </select>
      </div>
      <div class="confirm-order__select-take-date__icon"></div>
    </div>

    <div class="confirm-order__select-take-shop" @click="gotoEcChooseShopList">
      <div class="confirm-order__select-take-shop__info">
        <template v-if="shopInfo">
          <div class="confirm-order__select-take-shop__info__label">提货门店</div>
          <div class="confirm-order__select-take-shop__info__shop-address">{{ shopInfo.shopName }} {{ shopInfo.address }}</div>
          <div class="confirm-order__select-take-shop__info__business-time">营业时间：{{ shopInfo.businessTime }}</div>
        </template>
        <template v-else>
          <div class="confirm-order__select-take-shop__info__label">请选择提货门店</div>
        </template>
      </div>
      <div class="confirm-order__select-take-shop__icon"></div>
    </div>

    <div class="confirm-order__pay">
      <div class="confirm-order__pay__descr">支付即代表您已阅读并同意预约须知</div>
      <div class="confirm-order__pay__content">
        <div class="confirm-order__pay__content__amount">
          <span class="confirm-order__pay__content__amount__label">应付：</span>
          <span class="confirm-order__pay__content__amount__total-price"><span>¥</span> {{ orderDetail.amt | toRMB }}</span>
        </div>
        <div class="confirm-order__pay__content__button">
          <div class="confirm-order__pay__content__button__primary" v-if="shopId && takeDate" @click="gotoOrderPayment">微信支付</div>
          <div class="confirm-order__pay__content__button__disabled" v-else>微信支付</div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
import api from '@/api';
import envService from '@/services/env';
import ecOrderInfoModuleOption from '@/store/ec-order-info/ec-order-info';
import LoadingModal from '@/components/LoadingModal';

export default {
  data () {
    return {
      orderDetail: null,
      notice: '1.支付后请按选择的日期和时间段前往指定的门店提货<br>2.非指定门店不可提货<br>3.选定日期和时间段前不可提货<br>4.选定日期和时间段内未前往门店提货时，门店无法保证商品库存和品质，因此造成的损失将由用户自己承担',
    }
  },

  created () {
    this.$store.registerModule('ecOrderInfo', ecOrderInfoModuleOption);
    this.fetchOrderDetail();
  },

  computed: {
    shopId() {
      return this.$store.state.ecOrderInfo.shopId;
    },

    shopInfo() {
      const regionBlockCode = envService.getRegionBlockCode();
      return this.$store.getters['shop/shopsForRegion'](regionBlockCode).find(shop => shop.shopId === this.shopId);
    },

    takeDate: {
      get() {
        return this.$store.state.ecOrderInfo.takeDate;
      },
      set(takeDate) {
        this.$store.commit('ecOrderInfo/setTakeDate', takeDate);
      }
    }
  },

  methods: {
    fetchOrderDetail() {
      LoadingModal.show();
      const orderId = this.$route.params.orderId;
      api.ec.fetchOrderDetail({ orderId }).then(res => {
        this.orderDetail = res.data.data;
        if (this.shopId) {
          this.orderDetail.shopId = this.shopId;
        }
        if (this.takeDate) {
          this.orderDetail.takeDate = this.takeDate;
        }
      }).finally(() => LoadingModal.hide());
    },

    gotoEcChooseShopList() {
      const shopUseFlg = this.orderDetail.shopUseFlg;
      const shopList = this.orderDetail.shopList;
      this.$router.push({ name: 'ec-choose-shop-list', params: { shopUseFlg, shopList } });
    },

    gotoOrderPayment() {
      const orderId = this.orderDetail.orderId;
      const pickShopId = this.shopId;
      const pickDate = this.takeDate;
      wx.miniProgram.redirectTo({ url: `/pages/order-payment/order-payment?orderId=${orderId}&pickShopId=${pickShopId}&pickDate=${pickDate}` });
      this.$store.unregisterModule('ecOrderInfo', ecOrderInfoModuleOption);
    },
  },

  filters: {
    toRMB(price) {
      return price.toFixed(1);
    },

    formatDate: function (time) {
      const weekday = ['日','一','二','三','四','五','六'];
      const date = new Date(time);
      const year = date.getFullYear();
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const day = ('0' + date.getDate()).slice(-2);
      const weekdayLength = date.getDay();
      return `${year}/${month}/${day}(星期${weekday[weekdayLength]})`;
    }
  }
}
</script>

