import Vue from 'vue';
import MessageBox from './MessageBox';

const MessageBoxConstructor = Vue.extend(MessageBox);
const MessageBoxInstance    = new MessageBoxConstructor({ el: document.createElement('div') });

document.body.appendChild(MessageBoxInstance.$el);

export default {
  alert ({ title = '提示', msg = '' }, onIknow = ()=>{}, onCancel = ()=>{}) {
    MessageBoxInstance.type = 'alert'

    MessageBoxInstance.title    = title;
    MessageBoxInstance.msg      = msg;
    MessageBoxInstance.onIknow  = onIknow;
    MessageBoxInstance.onCancel = onCancel;

    MessageBoxInstance.visible = true;
  },

  confirm ({ title = '提示', msg = '', confirmButtonText = '确认', cancelButtonText = '取消' }, onConfirm = ()=>{}, onCancel = ()=>{}) {
    MessageBoxInstance.type = 'confirm'

    MessageBoxInstance.title             = title;
    MessageBoxInstance.msg               = msg;
    MessageBoxInstance.confirmButtonText = confirmButtonText;
    MessageBoxInstance.cancelButtonText  = cancelButtonText;
    MessageBoxInstance.onConfirm         = onConfirm;
    MessageBoxInstance.onCancel          = onCancel;

    MessageBoxInstance.visible = true;
  }
};
