<template>
  <ul class="campaign-list" v-if="participateRound === null">
    <li v-for="index in stampNeeded" :key="index">
      <div v-if="userStampList.length <= 1">
        <img v-if="stampNeeded === totalCampaignNum" :src="iconNotOver || defaultIconNotOver">
        <img v-else-if="index-1 < totalCampaignNum && index !== stampNeeded" :src="iconOver || defaultIconOver">
        <img v-else-if="index > totalCampaignNum && index !== stampNeeded" :src="iconNotOver || defaultIconNotOver">
        <img v-else src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-not-last-campaign.png">

        <p v-if="stampNeeded === totalCampaignNum" class="campaign-list__not-campaign">{{__campaignNum(index)}}</p>
        <p v-else-if="totalCampaignNum % stampNeeded > 0 && index <= totalCampaignNum % stampNeeded " class="campaign-list__campaign">{{index}}</p>
        <p v-else class="campaign-list__not-campaign">{{index}}</p>
      </div>
      <div v-else-if="userStampList.length > 1">
        <img v-if="totalCampaignNum % stampNeeded === 0" src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-not-last-campaign.png"/>
        <img v-else-if="(totalCampaignNum % stampNeeded > 0 && index-1 < totalCampaignNum % stampNeeded)" :src="iconOver || defaultIconOver"/>
        <img v-else-if="index < stampNeeded" :src="iconNotOver || defaultIconNotOver">
        <img v-else src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-not-last-campaign.png">

        <p v-if="totalCampaignNum % stampNeeded === 0" class="campaign-list__not-campaign">{{__campaignNum(index)}}</p>
        <p v-else class="campaign-list__campaigned">{{__campaignNum(index)}}</p>

      </div>
    </li>
  </ul>

  <ul class="campaign-list" v-else-if="participateRound === userStampList.length">
    <li v-for="index in stampNeeded" :key="index">
      <div>
        <img v-if="totalCampaignNum % stampNeeded === 0 && (lastIndex) > __campaignNum(index) " :src="iconOver || defaultIconOver"/>
        <img v-else-if="totalCampaignNum % stampNeeded !== 0 && __campaignNum(index) <= totalCampaignNum " :src="iconOver || defaultIconOver"/>
        <img v-else-if="totalCampaignNum % stampNeeded !== 0 && __campaignNum(index) > totalCampaignNum && __campaignNum(index) !==lastIndex" :src="iconNotOver || defaultIconNotOver"/>
        <img v-else-if="totalCampaignNum % stampNeeded !== 0  && __campaignNum(index) ===lastIndex" :src="campaignLastIconNotOver" />

        <img class="3" v-else-if="totalCampaignNum % stampNeeded === 0 && __campaignNum(index) === lastIndex" :src="campaignLastIconOver">

        <p v-if="totalCampaignNum % stampNeeded === 0" class="campaign-list__not-campaign">{{__campaignNum(index)}}</p>
        <p v-else class="campaign-list__campaigned">{{__campaignNum(index)}}</p>
      </div>
    </li>
  </ul>

  <ul class="campaign-list" v-else>
    <li v-for="index in stampNeeded" :key="index">
      <div>
        <img v-if="totalCampaignNum % stampNeeded === 0 && index > stampNeeded" :src="campaignLastIconNotOver"/>
        <img v-else-if="(totalCampaignNum % stampNeeded > 0 && index-1 < totalCampaignNum % stampNeeded)" :src="iconOver || defaultIconOver"/>
        <img v-else-if="index < stampNeeded" :src="iconNotOver || defaultIconNotOver">
        <img v-else :src="campaignLastIconNotOver">
        <p v-if="totalCampaignNum % stampNeeded === 0" class="campaign-list__not-campaign">{{__campaignNum(index)}}</p>
        <p v-else class="campaign-list__campaigned">{{__campaignNum(index)}}</p>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: [
    'userStampList',
    'participateRound',
    'totalCampaignNum',
    'stampNeeded',
    'iconNotOver',
    'iconOver'
  ],
  data() {
    return {
      defaultIconNotOver: '//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-not-campaign.png',
      defaultIconOver: '//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-campaigned.png',
      campaignLastIconNotOver: '//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-not-last-campaign.png',
      campaignLastIconOver: '//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/campaign/campaign-gift-bag.png'
    };
  },
  computed: {
    lastIndex() {
      if(this.userStampList.length === this.participateRound) {
        return this.stampNeeded * this.userStampList.length;
      } else {
        return this.stampNeeded * this.userStampList.length + this.stampNeeded;
      }
    },
  },
  methods: {
    __campaignNum: function(index) {
      if (this.userStampList.length === 0) {
        return index;
      }
      else if (this.totalCampaignNum % this.stampNeeded > 0) {
        return (this.totalCampaignNum - (this.totalCampaignNum % this.stampNeeded)) + index;
      }
      else if (this.userStampList.length < this.participateRound) {
        return (this.stampNeeded * this.userStampList.length - this.stampNeeded) + this.stampNeeded + index;
      }
      else if (this.userStampList.length == this.participateRound) {
        return (this.stampNeeded * this.userStampList.length - this.stampNeeded) + index;
      }
      else {
        return (this.stampNeeded * this.userStampList.length - this.stampNeeded) + this.stampNeeded + index;
      }
    }
  }
}
</script>
