import Vue from 'vue';
import Toast from './Toast';

const ToastConstructor = Vue.extend(Toast);
const toastInstance    = new ToastConstructor({ el: document.createElement('div') });

document.body.appendChild(toastInstance.$el);

export default {
  show ({ msg = '', duration = 2500, iconName = ''}) {
    toastInstance.msg = msg;
    toastInstance.iconName = iconName;
    toastInstance.visible = true;
    setTimeout(() => toastInstance.visible = false, duration);
  }
};
