<template>
  <div class="multiple-coupon">
    <img :src="pickCouponActivity.backgroundImage" class="multiple-coupon__image">
    <div class="multiple-coupon__coupon-list" :style="{ top: couponShowOffset + 'px' }">
      <div class="multiple-coupon__coupon-list__item" v-for="(couponItem, index) in couponList" :key="index">
        <img :src="pickCouponActivity.couponImageList[0].couponImage"
             v-if="isActivityBegin">

        <img :src="pickCouponActivity.couponImageList[2].couponImage"
             v-else-if="isActivityEnd && !couponItem.hasPicked">

        <img :src="pickCouponActivity.couponImageList[3].couponImage"
             v-else-if="isActivityEnd && couponItem.hasPicked">

        <img :src="pickCouponActivity.couponImageList[2].couponImage"
             v-else-if="couponItem.noRemaining">

        <img :src="pickCouponActivity.couponImageList[1].couponImage"
             v-else-if="couponItem.hasPicked">

        <img :src="pickCouponActivity.couponImageList[0].couponImage"
             v-else-if="!couponItem.hasPicked">

        <div class="multiple-coupon__coupon-list__item__container" @click="pickCoupon(couponItem)">
          <div class="multiple-coupon__coupon-list__item__info">
            <div v-if="couponItem.couponType === 'totalPriceMinusSomeCoupon'">
              <div class="multiple-coupon__coupon-list__item__info__coupon-type">
                满{{ couponItem.originalPrice }}元可用
              </div>
              <div class="multiple-coupon__coupon-list__item__info__price">
                <span class="multiple-coupon__coupon-list__item__info__price__symbol">¥</span>
                <span class="multiple-coupon__coupon-list__item__info__price__discount">{{ couponItem.discountPrice }}</span>
              </div>
            </div>

            <div v-if="couponItem.couponType === 'productPriceMinusToFreeCoupon'">
              <div class="multiple-coupon__coupon-list__item__info__coupon-type">全额折扣</div>
              <div class="multiple-coupon__coupon-list__item__info__price">
                <span class="multiple-coupon__coupon-list__item__info__price__discount product-price-minus-to-free-coupon">兑换</span>
              </div>
            </div>

            <div v-if="couponItem.couponType === 'productPriceMinusSomeCoupon'">
              <div class="multiple-coupon__coupon-list__item__info__coupon-type">消费立减</div>
              <div class="multiple-coupon__coupon-list__item__info__price">
                <span class="multiple-coupon__coupon-list__item__info__price__symbol">¥</span>
                <span class="multiple-coupon__coupon-list__item__info__price__discount">{{ couponItem.discountPrice }}</span>
              </div>
            </div>

            <div v-if="couponItem.couponType === 'productPriceMinusToSomeCoupon'">
              <div class="multiple-coupon__coupon-list__item__info__coupon-type">
                消费立减
              </div>
              <div class="multiple-coupon__coupon-list__item__info__price">
                <span class="multiple-coupon__coupon-list__item__info__price__discount product-price-minus--to-some-coupon">减至</span>
                <span class="multiple-coupon__coupon-list__item__info__price__symbol">¥</span>
                <span class="multiple-coupon__coupon-list__item__info__price__discount product-price-minus--to-some-coupon__price">{{ couponItem.discountPrice }}</span>
              </div>
            </div>

            <div class="multiple-coupon__coupon-list__item__info__commodity-type" v-if="couponItem.couponType === 'totalPriceMinusSomeCoupon'">
              全品类商品
            </div>
            <div class="multiple-coupon__coupon-list__item__info__commodity-type" v-else>
              {{ couponItem.couponName }}
            </div>
          </div>
          <div class="multiple-coupon__coupon-list__item__status">
            <span v-if="isActivityBegin">即将开始</span>
            <span v-else-if="isActivityEnd">活动结束</span>
            <span v-else-if="couponItem.noRemaining">已领完</span>
            <span v-else-if="couponItem.hasPicked">已领取</span>
            <span v-else-if="!couponItem.hasPicked">立即领取</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import api from '@/api';
import envService from '@/services/env';
import couponTypeService from '@/services/coupon/coupon-type';

import LoadingModal from '@/components/LoadingModal';

export default {
  props: ['activityDetail'],

  computed: {
    couponList() {
      this.activityDetail.couponList.map(coupon => {
        coupon.noRemaining = coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0 ? true : false;
        coupon.couponType  = couponTypeService.getType(coupon);
      });

      return this.activityDetail.couponList;
    },

    pickCouponActivity() {
      return this.activityDetail.pickCouponActivity;
    },

    isActivityBegin() {
      const currentTime = Date.parse(new Date());
      return currentTime < this.pickCouponActivity.beginTime ? true : false;
    },

    isActivityEnd() {
      const currentTime = Date.parse(new Date());
      return currentTime > this.pickCouponActivity.endTime ? true : false;
    },

    couponShowOffset() {
      const deviceWidth = window.screen.width;
      const couponShowOffsetInIphone6 = this.pickCouponActivity.couponShowOffset;
      const deviceWidthIphone6 = 375;
      return (couponShowOffsetInIphone6 * deviceWidth) / deviceWidthIphone6;
    }
  },

  methods: {
    pickCoupon(coupon) {
      const currentTime = Date.parse(new Date());

      if( currentTime >= this.pickCouponActivity.beginTime
          && currentTime <= this.pickCouponActivity.endTime
          && !coupon.noRemaining
          && !coupon.hasPicked ) {

        this.$emit("pickCoupon", coupon.couponId);
      }
    }
  }
}
</script>
