import Vue from 'vue';
import App from './App.vue';
import router from '@/router';
import store  from '@/store';

import envService from '@/services/env';
import mtaService from '@/services/mta';

import api from '@/api';

import iconfont from '@/assets/fonts/iconfont.js';

Vue.config.productionTip = false;

const renderApp = () => {
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount('#app');
};

envService.init().then(() => {
  renderApp();
  mtaService.init();

  // 店铺数据初始化
  const regionBlockCode = envService.getRegionBlockCode();
  api.shop.fetchShops({ regionBlockCode }).then(res => {
    const fetchShopsSuccess = res.data.code === 0 ? true : false;
    if (fetchShopsSuccess) {
      const shops = res.data.data.list;
      store.commit('shop/updateShopsForRegion', { shops, regionBlockCode });
    }
  });
});
