import qs from 'qs';
import api from '@/api';
import wechatMiniProgramBridgeService from './wechat-mini-program-bridge';

export default {
  async __initEnvWechatMiniProgram () {
    const queryStr = window.location.search.slice(1);
    const { token, regionBlockCode } = qs.parse(queryStr);

    window.sessionStorage.setItem('token', token);
    window.sessionStorage.setItem('regionBlockCode', regionBlockCode);

    const isGuest = token === '';
    if (isGuest) {
      // 游客用户token值为空串，无需从API获取用户信息。
    } else {
      const userInfo = await api.user.fetchUserInfo({ regionBlockCode });
      window.sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
    }

    if (process.env.NODE_ENV === 'production') {
      await wechatMiniProgramBridgeService.init();
    }  else {
      wechatMiniProgramBridgeService.init();
    }
  },

  init () {
    return new Promise((resolve, reject) => {
      const isInWechatMiniProgram = (() => {
        const queryStr = window.location.search.slice(1);
        const { envType } = qs.parse(queryStr);
        return envType === 'wechatMiniProgram' ? true : false;
      })();

      if (isInWechatMiniProgram) {
        window.sessionStorage.setItem('envType', 'wechatMiniProgram');
        this.__initEnvWechatMiniProgram().then(() => resolve());
      }
      else {
        resolve();
      }
    });
  },

  getEnvType () {
    return window.sessionStorage.getItem('envType');
  },
  getRegionBlockCode () {
    return window.sessionStorage.getItem('regionBlockCode');
  },
  getToken () {
    return window.sessionStorage.getItem('token');
  },
  getUserInfo () {
    return JSON.parse(window.sessionStorage.getItem('userInfo'));
  }
};

