<template>
  <div class="coupon-item-none" @click="goToCouponDetail">
    <img :src="ossImageUrl + '/coupon/failure-coupons-background.png'" class="coupon-item-none__image">
    <div class="coupon-item-none__info">
      <div class="coupon-item-none__price" v-if="couponType === 'totalPriceMinusSomeCoupon'">
        <div class="coupon-item-none__price__total-price">
          满<span class="coupon-item-none__price__total-price__number">{{ coupon.originalPrice }}</span>元
        </div>
        <div class="coupon-item-none__price__discount-price">
          <span>减</span>
          <span class="coupon-item-none__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
      </div>
      <div class="coupon-item-none__price coupon-item-none__limit-product" v-if="couponType === 'productPriceMinusToFreeCoupon'">
        <div class="coupon-item-none__price__total-price">
          全额折扣
        </div>
        <div class="coupon-item-none__price__discount-price">
          兑换
        </div>
        <div class="coupon-item-none__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item-none__price" v-if="couponType === 'productPriceMinusSomeCoupon'">
        <div class="coupon-item-none__price__total-price">
          消费立减
        </div>
        <div class="coupon-item-none__price__discount-price">
          <span>减</span>
          <span class="coupon-item-none__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
        <div class="coupon-item-none__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item-none__price coupon-item-none__limit-product" v-if="couponType === 'productPriceMinusToSomeCoupon'">
        <div class="coupon-item-none__price__total-price">
          消费立减
        </div>
        <div class="coupon-item-none__price__discount-price product-price-minus-to-some-coupon">
          <span>减至</span>
          <span class="coupon-item-none__price__discount-price__number">{{ coupon.discountPrice }}</span>
        </div>
        <div class="coupon-item-none__price__product-name">
          {{ coupon.couponName }}
        </div>
      </div>
      <div class="coupon-item-none__description">
        <div class="coupon-item-none__description__title">
          {{ coupon.couponName }}
        </div>
        <div class="coupon-item-none__description__button" v-if="couponType === 'totalPriceMinusSomeCoupon'">
          全品类
        </div>
        <div class="coupon-item-none__description__button" v-if="couponType === 'productPriceMinusToFreeCoupon' || couponType === 'productPriceMinusSomeCoupon' || couponType === 'productPriceMinusToSomeCoupon'">
          限定商品
        </div>
        <div class="coupon-item-none__description__time" v-if="coupon.dynamicPeriodFlg === 0">
          有效期{{ coupon.useDateFrom | formatDate }}-{{ coupon.useDateTo | formatDate }}
        </div>
      </div>
      <div class="coupon-item-none__activity-progress">
        <div class="coupon-item-none__activity-progress__picked-up">
          <img :src="ossImageUrl + '/coupon/icon-picked-up-none.png'">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import couponTypeService from '@/services/coupon/coupon-type';

export default {
  props: ['coupon'],

  data () {
    return {
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL
    }
  },

  computed: {
    couponType() {
      return couponTypeService.getType(this.coupon);
    }
  },

  filters: {
    formatDate (timeStamp) {
      const y = (new Date(timeStamp)).getFullYear();
      const _m = (new Date(timeStamp)).getMonth() + 1;
      const _d = (new Date(timeStamp)).getDate();

      // 小于10的月和日前面需要加0，例如“3.2”需要显示为“03.02”。
      const month = (_m < 10) ? ('0' + _m) : String(_m);
      const day   = (_d < 10) ? ('0' + _d) : String(_d);
      return `${y}.${month}.${day}`;
    }
  },

  methods: {
    goToCouponDetail() {
      const couponId = this.coupon.couponId;

      this.$router.push({ name: 'coupon-detail', params: { couponId } });
    }
  }
}
</script>
