export default {
  namespaced: true,
  state: {
    collectedCapsules: []
  },
  getters: {
    collectedCapsulesCount(state) {
      return state.collectedCapsules.length;
    },
    collectedCapsulesFirstThree(state) {
      return state.collectedCapsules.slice(0, 3);
    }
  },
  mutations: {
    resetCollectedCapsules(state) {
      state.collectedCapsules = [];
    },
    addCollectedCapsule(state, capsule) {
      state.collectedCapsules.push(capsule);
    }
  }
}
