<script>
import _ from 'lodash';
import api from '@/api';
import calculateDistance from '@/utils/calculate-distance';
import envService from '@/services/env';
import wechatSdkService from '@/services/wechat-mini-program-bridge';
import popularStatisticsService from '@/services/popular-statistics';
import reginonService from '@/services/region';

import GotoHome from '@/components/GotoHome';
import FormatBusinessTime from '@/components/FormatBusinessTime';
import TimelineBar from './EcProductDetail/TimelineBar';
import AppointmentPopup from './EcProductDetail/AppointmentPopup';
import LoadingModal from '@/components/LoadingModal';
import MessageBox from '@/components/MessageBox';

export default {
  components: {
    GotoHome,
    FormatBusinessTime,
    TimelineBar,
    AppointmentPopup
  },

  data () {
    return {
      productDetail: null,
      currentDate: Date.now(),
      // inviteFriendsModalVisible: false,
      appointmentPopupVisible: false,
      currentLocation: null,
      avatars: [`${process.env.VUE_APP_OSS_BASE_URL}/avatar/1.png`, `${process.env.VUE_APP_OSS_BASE_URL}/avatar/2.png`, `${process.env.VUE_APP_OSS_BASE_URL}/avatar/3.png`]
    }
  },

  async created () {
    this.setCurrentLocation();
    await this.fetchEcProductShopDetail();

    const regionBlockCode = envService.getRegionBlockCode();
    if(regionBlockCode !== this.productDetail.regionBlockCode) {
      this.showRegionDiffModal(this.productDetail.regionBlockCode);
    }

    popularStatisticsService.recordEcProductPopularStatistics(this.$route.params.commodityId);
  },

  computed: {
    soldOut() {
      return this.productDetail.remainedQty === 0;
    },

    shopIdList() {
      return this.productDetail.shopList;
    },

    shopList() {
      const regionBlockCode = envService.getRegionBlockCode();
      const isAllShop = this.productDetail.shopUseFlg === 0;
      if (isAllShop) {
        const shopsAvailableForRegion = this.$store.getters['shop/shopsAvailableForRegion'](regionBlockCode);
        return shopsAvailableForRegion ? this.$store.getters['shop/shopsForRegion'](regionBlockCode) : [];
      } else {
        const shops = this.$store.state.shop.shops[regionBlockCode];
        const shopIdList = this.shopIdList;
        return _.intersectionWith( shops, shopIdList, (shopInfo, shopId) => shopInfo.shopId === shopId );
      }
    },

    sortedShopsWithDistance() {
      const currentLocation = this.currentLocation;

      if (currentLocation) {
        const shopsWithDistance =  this.shopList.map(shop => {
          const _shop = Object.assign({}, shop);
          _shop.distance = calculateDistance(shop.latitude, shop.longitude, currentLocation.latitude, currentLocation.longitude);
          return _shop;
        });
  
        shopsWithDistance.sort((aShop, bShop) => aShop.distance - bShop.distance);
        return shopsWithDistance;
      } else {
        return this.shopList;
      }
    }
  },

  methods: {
    fetchEcProductShopDetail() {
      return new Promise((resolve, reject) => {
        LoadingModal.show();
        const commodityId = this.$route.params.commodityId;
        api.ec.fetchEcProductShopDetail({commodityId}).then(res => {
          if (res.status === 200) {
            //expired 0-未开始；1-已开始；2-已结束
            this.productDetail = res.data.data;
  
            wx.miniProgram.postMessage({ data: { shareTitle: this.productDetail.commodityName } });
          }
          resolve();
        })
        .catch(err => reject(err))
        .finally(() => LoadingModal.hide());
      })
    },
    // showInviteFriendsModal() {
    //   this.inviteFriendsModalVisible = true;
    // },

    // hideInviteFriendsModal() {
    //   this.inviteFriendsModalVisible = false;
    // },

    showAppointmentPopup() {
      const isGuest = envService.getUserInfo() === null;
      if (isGuest) {
        const commodityId = this.productDetail.commodityId;
        wx.miniProgram.reLaunch({ url: `/pages/index/index?pageKey=ecProductDetail&commodityId=${commodityId}&sourceType=internal` });
      } else {
        this.appointmentPopupVisible = true;
      }
    },

    hideAppointmentPopup() {
      this.appointmentPopupVisible = false;
    },

    // gotoInviteFriends() {
    //   console.log('gotoInviteFriends');
    // },

    gotoEcAvailableShopList() {
      const isGuest = envService.getUserInfo() === null;
      if (isGuest) {
        const commodityId = this.productDetail.commodityId;
        wx.miniProgram.reLaunch({ url: `/pages/index/index?pageKey=ecProductDetail&commodityId=${commodityId}&sourceType=internal` });
      } else {
        const shopUseFlg = this.productDetail.shopUseFlg;
        const shopList = this.productDetail.shopList;
        this.$router.push({ name: 'ec-available-shop-list', params: { shopUseFlg, shopList } });
      }
    },

    setCurrentLocation() {
      wechatSdkService.getLocation().then(res => {
        const latitude = res.latitude;
        const longitude = res.longitude;
        this.currentLocation = { latitude, longitude };
      });
    },

    showRegionDiffModal(campaignRegionBlockCode) {
      let campaignRegionBlockName = reginonService.getCurrentRegionName(campaignRegionBlockCode);

      const isGuest = envService.getUserInfo() === null;
      if (isGuest) {
        MessageBox.alert({ title: '友情提示', msg: '你当前查阅内容，可能受区域限制有所不同' });
      } else {
        MessageBox.alert({ title: '友情提示', msg: `你所在区域无法参与，当前内容仅限${campaignRegionBlockName}` });
      }
    }
  },

  filters: {
    toRMB(price) {
      return price.toFixed(1);
    },

    toDays(timestamp) {
      return Math.ceil(timestamp/(1000*60*60*24));
    },

    formatOnlookerCount(onlookerCount) {
      if (onlookerCount >= 10000) {
        if (onlookerCount % 10000 >= 1000) {
          return (onlookerCount / 10000).toFixed(1) + '万';
        } else {
          return (onlookerCount / 10000).toFixed(0) + '万';
        }
      } else {
        return onlookerCount;
      }
    },

    toKilometer(number) {
      if (number > 20) {
        return '> 20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  }
}
</script>

