const MILLISECONDS_OF_ONE_DAY = 24 * 60 * 60 * 1000;

function isOnGoing(campaign) {
  const now = new Date().getTime();
  const isOnGoing = now > campaign.campaignDateFrom && now < campaign.campaignDateTo;
  return isOnGoing;
}

function isSameWeek(campaign) {
	const old_count = parseInt(campaign.campaignDateFrom / MILLISECONDS_OF_ONE_DAY);
  const now_other = parseInt(new Date().getTime() / MILLISECONDS_OF_ONE_DAY);
  return !isOutOfDate(campaign) && parseInt((old_count + 4) / 7) == parseInt((now_other + 4) / 7) && campaign.campaignDateFrom > new Date().getTime();
}

function isNotSameWeekSmallThan5Days(campaign) {
  const now = new Date().getTime();
  return now < campaign.campaignDateFrom && !isSameWeek(campaign) && campaign.campaignDateFrom - now < 5 * MILLISECONDS_OF_ONE_DAY && campaign.campaignDateFrom - now > 0;
}

function isNotSameWeekBigThan5Days(campaign) {
  const now = new Date().getTime();
  return now < campaign.campaignDateFrom && campaign.campaignDateFrom - now > 5 * MILLISECONDS_OF_ONE_DAY;
}

function isAboutToReach(campaign) {
  const stampNumForEachRound = campaign.userTotalStampNum % campaign.stampNeeded;
  return isOnGoing(campaign) && (stampNumForEachRound / campaign.stampNeeded) >= 0.7;
}

function isAlreadyReached(campaign) {
  const now = new Date().getTime();
  return (now < campaign.openEndDt && (campaign.userTotalStampNum / campaign.stampNeeded) >= 1);
}

function isOutOfDate(campaign) {
  const now = new Date().getTime();
  return (now > campaign.campaignDateTo && (campaign.userTotalStampNum / campaign.stampNeeded) < 1) ||
  (now > campaign.openEndDt);
}

function isOutOfGift(campaign) {
  return campaign.giftNums === 0;
}

function isAlmostOutOfDate(campaign) {
  const now = new Date().getTime();
  return ((campaign.userTotalStampNum / campaign.stampNeeded) >= 0.7
            && (campaign.userTotalStampNum / campaign.stampNeeded) < 1
            && (campaign.campaignDateTo - now) < (5 * MILLISECONDS_OF_ONE_DAY)
            && campaign.giftNums > 0);
}

function isAlmostOutOfDateAndReached(campaign) {
  const now = new Date().getTime();
  return (now > campaign.campaignDateTo
            && now < campaign.openEndDt
            && (campaign.userTotalStampNum / campaign.stampNeeded) >= 1
            && campaign.giftNums > 0);
}

export default {
  isAlreadyReached: function (campaign) {
    return isAlreadyReached(campaign);
  },
  isAboutToReach: function (campaign) {
    return isAboutToReach(campaign);
  },
  canTransferCampaignToFriend: function (campaign) {
    return !isOutOfDate(campaign) && ((campaign.openEndDt - new Date().getTime()) / MILLISECONDS_OF_ONE_DAY < 7) && (campaign.userTotalStampNum / campaign.stampNeeded) >= 0.5;
  },
  getStatus: function (campaign) {
    if (isOutOfGift(campaign)) {
      return '已结束';
    }
    else if (isAlmostOutOfDate(campaign) || isAlmostOutOfDateAndReached(campaign)) {
      const daysBeforeEnd = (campaign.campaignDateTo - new Date().getTime()) / MILLISECONDS_OF_ONE_DAY;
      if (daysBeforeEnd >= 0) {
        return `进行中 ${parseInt(daysBeforeEnd, 0)}天后结束`;
      } else {
        return '活动截止';
      }
    }
    else if (isAboutToReach(campaign)) {
      const daysBeforeEnd = (campaign.campaignDateTo - new Date().getTime()) / MILLISECONDS_OF_ONE_DAY;
      return `进行中 ${parseInt(daysBeforeEnd, 0)}天后结束`;
    }
    else if (isAlreadyReached(campaign)) {
      const daysBeforeEnd = (campaign.campaignDateTo - new Date().getTime()) / MILLISECONDS_OF_ONE_DAY;
      if (daysBeforeEnd >= 0) {
        return `进行中 ${parseInt(daysBeforeEnd, 0)}天后结束`;
      } else {
        return '已结束';
      }
    }
    else if (isSameWeek(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const beginMonth = campaignBeginDate.getMonth() + 1;
      const beginDate = campaignBeginDate.getDate() >= 10 ? campaignBeginDate.getDate() : `0${campaignBeginDate.getDate()}`;
      return `${beginMonth}月${beginDate}日开始`;
    }
    else if (isNotSameWeekSmallThan5Days(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const beginMonth = campaignBeginDate.getMonth() + 1;
      const beginDate = campaignBeginDate.getDate() >= 10 ? campaignBeginDate.getDate() : `0${campaignBeginDate.getDate()}`;
      return `${beginMonth}月${beginDate}日开始`;
    }
    else if (isNotSameWeekBigThan5Days(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const beginMonth = campaignBeginDate.getMonth() + 1;
      const beginDate = campaignBeginDate.getDate() >= 10 ? campaignBeginDate.getDate() : `0${campaignBeginDate.getDate()}`;
      return `${beginMonth}月${beginDate}日开始`;
    }
    else if (isOnGoing(campaign)) {
      const daysBeforeEnd = (campaign.campaignDateTo - new Date().getTime()) / MILLISECONDS_OF_ONE_DAY;
      return `进行中 ${parseInt(daysBeforeEnd, 0)}天后结束`;
    }
    else if (isOutOfDate(campaign)) {
      const campaignEndDate = new Date(campaign.campaignDateTo);
      const beginMonth = campaignEndDate.getMonth() + 1;
      const beginDate = campaignEndDate.getDate() >= 10 ? campaignEndDate.getDate() : `0${campaignEndDate.getDate()}`;
      return `${beginMonth}月${beginDate}日结束`;
    }
  },
  getCampaignStatus: function (campaign) {
    if (isOutOfGift(campaign)) {
      return {
        campaignStatus: 'outOfDate',
        campaignStatusText: '已结束'
      };
    }
    else if (isAlmostOutOfDate(campaign)) {
      return {
        campaignStatus: 'almostOutOfDate',
        campaignStatusText: '即将过期'
      };
    }
    else if (isAlmostOutOfDateAndReached(campaign)) {
      return {
        campaignStatus: 'almostOutOfDateAndReached',
        campaignStatusText: '即将过期'
      };
    }
    else if (isAlreadyReached(campaign)) {
      return {
        campaignStatus: 'reached',
        campaignStatusText: '可兑换'
      };
    }
    else if (isAboutToReach(campaign)) {
      return {
        campaignStatus: 'aboutToReach',
        campaignStatusText: '即将达成'
      };
    }
    else if (isSameWeek(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const beginDay = campaignBeginDate.getDay();
      const beginHour = campaignBeginDate.getHours() >= 10 ? campaignBeginDate.getHours() : `0${campaignBeginDate.getHours()}`;
      const beginMinute = campaignBeginDate.getMinutes() >= 10 ? campaignBeginDate.getMinutes() : `0${campaignBeginDate.getMinutes()}`;

      switch (beginDay) {
        case 1:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周一 ${beginHour}:${beginMinute}开始`
          };
        case 2:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周二 ${beginHour}:${beginMinute}开始`
          };
        case 3:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周三 ${beginHour}:${beginMinute}开始`
          };
        case 4:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周四 ${beginHour}:${beginMinute}开始`
          };
        case 5:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周五 ${beginHour}:${beginMinute}开始`
          };
        case 6:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周六 ${beginHour}:${beginMinute}开始`
          };
        case 0:
          return {
            campaignStatus: 'aboutToStart',
            campaignStatusText: `本周日 ${beginHour}:${beginMinute}开始`
          };
      }
    }
    else if (isNotSameWeekSmallThan5Days(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const curDate = new Date();
      const beginHour = campaignBeginDate.getHours() >= 10 ? campaignBeginDate.getHours() : `0${campaignBeginDate.getHours()}`;
      const beginMinute = campaignBeginDate.getMinutes() >= 10 ? campaignBeginDate.getMinutes() : `0${campaignBeginDate.getMinutes()}`;
      const afterDays = (campaignBeginDate.getTime() - curDate.getTime()) / MILLISECONDS_OF_ONE_DAY;
      return {
        campaignStatus: 'aboutToStart',
        campaignStatusText: `${parseInt(afterDays)}天后 ${beginHour}:${beginMinute}开始`
      };
    }
    else if (isNotSameWeekBigThan5Days(campaign)) {
      const campaignBeginDate = new Date(campaign.campaignDateFrom);
      const beginMonth = campaignBeginDate.getMonth() + 1 >= 10 ? campaignBeginDate.getMonth() + 1 : `0${campaignBeginDate.getMonth() + 1}`;
      const beginDate = campaignBeginDate.getDate() >= 10 ? campaignBeginDate.getDate() : `0${campaignBeginDate.getDate()}`;
      const beginHour = campaignBeginDate.getHours() >= 10 ? campaignBeginDate.getHours() : `0${campaignBeginDate.getHours()}`;
      const beginMinute = campaignBeginDate.getMinutes() >= 10 ? campaignBeginDate.getMinutes() : `0${campaignBeginDate.getMinutes()}`;
      return {
        campaignStatus: 'aboutToStart',
        campaignStatusText: `${beginMonth}月${beginDate}日 ${beginHour}:${beginMinute}开始`
      };
    }
    else if (isOnGoing(campaign)) {
      return {
        campaignStatus: 'onGoing',
        campaignStatusText: '进行中'
      };
    }
    else if (isOutOfDate(campaign)) {
      return {
        campaignStatus: 'outOfDate',
        campaignStatusText: '已结束'
      };
    }
  }
}