<template>
  <div class="available-store">
    <div v-if="shopUseFlg === 1 && shops !== null">
      <div class="available-store__title" @click="goToCouponShopList">
        <div class="available-store__title__left">
          <img :src="ossImageUrl + '/coupon/icon-shop.png'" class="available-store__title__left__image">
          <span class="available-store__title__left__text">适用门店</span>
        </div>
        <div class="available-store__title__right">
          <img :src="ossImageUrl +'/coupon/icon-arrows.png'">
          <span>查看全部({{ shops.length }})</span>
        </div>
      </div>
      <div class="available-store__content">
        <div class="available-store__content__title">
          <div class="available-store__content__title__shop">
            <span class="available-store__content__title__shop__name">{{ shops[0].shopName }}</span>
            <format-business-time
            v-if="shops[0].businessTime !== ''"
            :businessTime="shops[0].businessTime"></format-business-time>
          </div>
          <div class="available-store__content__title__distance" v-if="sortedShopsWithDistance[0].distance">
            距您{{ sortedShopsWithDistance[0].distance | toKilometer }}
          </div>
        </div>
        <div class="available-store__content__address">
          {{ shops[0].address }}
        </div>
      </div>
    </div>
    <div v-else>
      <div class="available-store__title">
        <div class="available-store__title__left">
          <img :src="ossImageUrl + '/coupon/icon-shop.png'" class="available-store__title__left__image">
          <span class="available-store__title__left__text">适用门店</span>
        </div>
      </div>
      <div class="available-store__all-shop">
        {{ regionName }}地区所有门店
      </div>
    </div>
  </div>
</template>
<script>
import api from '@/api';
import _ from 'lodash';

import envService from '@/services/env';
import reginonService from '@/services/region';
import wechatSdkService from '@/services/wechat-mini-program-bridge';

import calculateDistance from '@/utils/calculate-distance';

import FormatBusinessTime from '@/components/FormatBusinessTime';

export default {
  props: ['couponDetails'],

  components: {
    FormatBusinessTime
  },

  data () {
    return {
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL,
      shops: null,
      shopUseFlg: this.couponDetails.shopUseFlg,
      regionName: null,
      currentLocation: null
    }
  },

  created() {
    const regionBlockCode = envService.getRegionBlockCode();
    this.regionName = reginonService.getCurrentRegionName(regionBlockCode);
    this.filterShops();
    this.getLocation();
  },

  filters: {
    toKilometer(number) {
      if (number > 20) {
        return '>20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  },

  computed: {
    sortedShopsWithDistance() {
      const currentLocation = this.currentLocation;
      if (currentLocation) {
        const shopsWithDistance =  this.shops.map(shop => {
          const _shop = Object.assign({}, shop);
          _shop.distance = calculateDistance(shop.latitude, shop.longitude, currentLocation.latitude, currentLocation.longitude);
          return _shop;
        });
  
        shopsWithDistance.sort((aShop, bShop) => aShop.distance - bShop.distance);
        return shopsWithDistance;
      } else {
        return this.shops;
      }
    },
  },

  methods: {
    goToCouponShopList() {
      const shopList = this.couponDetails.shopList;

      this.$router.push({ name: 'coupon-available-shop-list', params: { shopList } });
    },

    filterShops() {
      const regionBlockCode = envService.getRegionBlockCode();
      const shopIdList =this.couponDetails.shopList;
      const shopUseFlg = this.shopUseFlg;

      const isAllShop = shopUseFlg === 0;
      if (isAllShop) {
        const shopsAvailableForRegion = this.$store.getters['shop/shopsAvailableForRegion'](regionBlockCode);
        this.shops = shopsAvailableForRegion ? this.$store.getters['shop/shopsForRegion'](regionBlockCode) : [];
      } else {
        const shops = this.$store.state.shop.shops[regionBlockCode];
        this.shops = _.intersectionWith( shops, shopIdList, (shopInfo, shopId) => shopInfo.shopId === shopId );
      }
    },

    getLocation() {
      wechatSdkService.getLocation().then(res => {
        const latitude = res.latitude;
        const longitude = res.longitude;
        this.currentLocation = { latitude, longitude };
      })
    }
  }
}
</script>
