<template>
  <div class="order-list-item">
    <template v-if="order.status === 1">
      <div class="order-list-item__head">
        <div class="order-list-item__head__order-id">订单号：{{ order.orderId }}</div>
        <div class="order-list-item__head__status">待取货</div>
      </div>
      <div class="order-list-item__content">
        <div class="order-list-item__content__left">
          <img :src="order.imageSmall">
        </div>
        <div class="order-list-item__content__right">
          <div class="order-list-item__content__right__title">{{ order.commodityName }}</div>
          <div class="order-list-item__content__right__quantity">x{{ order.qty }}</div>
        </div>
        <div class="order-list-item__content__info">共{{ order.qty }}件商品  应付款：
          <span class="order-list-item__content__info__price">¥{{ order.amt | toRMB }}</span>
        </div>
      </div>
      <div class="order-list-item__pickup-date">
        提货时间：{{ order.takeDate | formatDate }}  00:00 - 23:59
      </div>
      <div class="order-list-item__pickup-address">
        <div class="order-list-item__pickup-address__address">提货地址：{{ order.shop.shopName }} {{ order.shop.address }}</div>
        <div class="order-list-item__pickup-address__goto-map" @click="openMap">
          地图
          <span class="order-list-item__pickup-address__goto-map__icon"></span>
        </div>
      </div>
      <div class="order-list-item__button-list">
        <div class="order-list-item__button-list__modify-order" v-if="order.canModifyFlg" @click="gotoEditOrder(order.orderId)">修改订单</div>
        <div class="order-list-item__button-list__pickup-explain" v-if="order.orderStatus === 'normal'" @click="showPickupExplainMessageBox">提货说明</div>
        <div class="order-list-item__button-list__view-pickup-coupon" @click="gotoCouponDetail(order.couponId, order.userCouponArr)">查看提货券</div>
      </div>
    </template>
    <template v-else>
      <div class="order-list-item__head">
        <div class="order-list-item__head__order-id">订单号：{{ order.orderId }}</div>
        <div class="order-list-item__head__status">待支付</div>
      </div>
      <div class="order-list-item__content">
        <div class="order-list-item__content__left">
          <img :src="order.imageSmall">
        </div>
        <div class="order-list-item__content__right">
          <div class="order-list-item__content__right__title">{{ order.commodityName }}</div>
          <div class="order-list-item__content__right__quantity">x{{ order.qty }}</div>
        </div>
        <div class="order-list-item__content__info">共{{ order.qty }}件商品  应付款：
          <span class="order-list-item__content__info__price">¥{{ order.amt | toRMB }}</span>
        </div>
      </div>
      <div class="order-list-item__can-appointment-date">
        可预约时间：{{ order.canOrderStart | formatDate }} - {{ order.canOrderEnd | formatDate }}
      </div>
      <div class="order-list-item__button-list">
        <div class="order-list-item__button-list__cancel-order" @click="showCancelOrderMessageBox(order.orderId)">取消订单</div>
        <div class="order-list-item__button-list__goto-pay" @click="gotoConfirmOrder(order.orderId)">去支付</div>
      </div>
    </template>
  </div>
</template>

<script>
import api from '@/api';
import MessageBox from '@/components/MessageBox';
import LoadingModal from '@/components/LoadingModal';

export default {
  // order.status === 1已支付,0待支付
  props: ['order'],

  methods: {
    openMap() {
      wx.openLocation({
          latitude: this.order.shop.latitude ,
          longitude: this.order.shop.longitude,
          name: this.order.shop.shopName,
          address: this.order.shop.address
      });
    },

    deleteOrder(orderId) {
      LoadingModal.show();
      api.ec.deleteOrder({ orderId }).then(() => {
        this.$emit('refreshMyOrderList');
      }).finally(() => LoadingModal.hide());
    },

    gotoCouponDetail(couponId, userCouponArr) {
      const userCouponId = userCouponArr[0];
      this.$router.push({ path: `/user-coupon-detail/${couponId}/${userCouponId}` });
    },

    gotoConfirmOrder(orderId) {
      this.$router.push({ path: `/ec-confirm-order/${orderId}` })
    },

    gotoEditOrder(orderId) {
      this.$router.push({ path: `/ec-edit-order/${orderId}` })
    },

    showPickupExplainMessageBox() {
      MessageBox.alert({ title: '提货说明', msg: '该号码仅供提货时出示给门店核销使用请妥善保管勿泄漏给他人。' });
    },

    showCancelOrderMessageBox(orderId) {
      MessageBox.confirm({ title: '取消订单？', msg: '取消后该订单即被删除，且无法恢复', confirmButtonText:'确定取消', cancelButtonText: '暂不取消' },
        () => {
          this.deleteOrder(orderId);
        }
      );
    }
  },

  filters: {
    formatDate: function (time) {
      const date = new Date(time)
      const year = date.getFullYear();
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const day = ('0' + date.getDate()).slice(-2);
      return `${year}/${month}/${day}`;
    },

    toRMB(price) {
      return price.toFixed(1);
    },
  }
}
</script>

