<template>
  <div class="order-payment-result">
    <template v-if="orderPaymentResult">
      <div class="order-payment-result__icon-success"></div>
      <div class="order-payment-result__status-text">支付成功</div>
      <div class="order-payment-result__amount" v-if="totalFee">订单金额：<span>¥ {{ totalFee | toRMB }}</span></div>
      <div class="order-payment-result__notice">预约商品以提货券的形式发送到您的账户可前往查看</div>
      <div class="order-payment-result__view-order" @click="gotoMyCouponList">前往查看</div>
      <div class="order-payment-result__my-order" @click="gotoMyOrderList">我的订单</div>
    </template>

    <template v-else>
      <div class="order-payment-result__icon-fail"></div>
      <div class="order-payment-result__status-text">支付失败</div>
      <div class="order-payment-result__order-detail" @click="gotoMyOrderList">订单详情</div>
      <div class="order-payment-result__go-home" @click="gotoHome">返回首页</div>
    </template>
  </div>
</template>

<script>
export default {
  data () {
    return {
      orderPaymentResult: null,
      totalFee: null
    }
  },

  created () {
    this.setOrderPaymentResult();
  },

  methods: {
    gotoMyCouponList() {
      wx.miniProgram.switchTab({ url: '/pages/my-coupon-list/my-coupon-list' });
    },

    gotoMyOrderList() {
      this.$router.replace({ path: '/my-order-list' });
    },

    gotoHome() {
      wx.miniProgram.switchTab({ url: '/pages/homepage/homepage' });
    },

    setOrderPaymentResult() {
      const orderPaymentResult = this.$route.query.orderPaymentResult === "true" ? true : false;
      const totalFee = this.$route.query.totalFee;
      this.orderPaymentResult = orderPaymentResult;
      this.totalFee = totalFee;
    }
  },

  filters: {
    toRMB(price) {
      return parseInt(price).toFixed(1);
    }
  }
}
</script>

