export default {
  getMissionType(mission) {
    if (mission.missionType === 0) {
      return 'comeToShopWeekly';
    } else if (mission.missionType === 1) {
      return 'comeToShopMonthly';
    } else if (mission.missionType === 2) {
      return 'purchaseAmountWeekly';
    } else if (mission.missionType === 3) {
      return 'purchaseAmountMonthly';
    } else if (mission.missionType === 4) {
      return 'firstGotStamp';
    } else if (mission.missionType === 5) {
      return 'firstGotCampaignReward';
    } else if (mission.missionType === 6) {
      return 'firstPurchaseAnyCommodity';
    } else if (mission.missionType === 7) {
      return 'firstPurchaseDiscountCommodity';
    } else if (mission.missionType === 8) {
      return 'firstRedeemPoint';
    } else if (mission.missionType === 9) {
      return 'firstPurchaseAnyCommodityByNewUser';
    }
  },
  getMissionRewardType(mission) {
    if (mission.lotteryType === 0) {
      return 'point';
    } else if (mission.lotteryType === 1) {
      return 'coupon';
    } else if (mission.lotteryType === 2) {
      return 'lotteryCoin';
    }
  },
  getMissionIconUrl(mission) {
    const ossImagesBaseUrl = process.env.VUE_APP_OSS_BASE_URL;
    if (mission.missionType === 4) {
      return ossImagesBaseUrl + '/mission-center/head.png';
    } else if (mission.missionType === 5) {
      return ossImagesBaseUrl + '/mission-center/coupon.png';
    } else if (mission.missionType === 6) {
      return ossImagesBaseUrl + '/mission-center/shopping-bag.png';
    } else if (mission.missionType === 7) {
      return ossImagesBaseUrl + '/mission-center/diamond.png';
    } else if (mission.missionType === 8) {
      return ossImagesBaseUrl + '/mission-center/exchange.png';
    } else if (mission.missionType === 9) {
      return ossImagesBaseUrl + '/mission-center/shopping-bag.png';
    }
  },
  getGotoMissionHandler(mission) {
    if (mission.missionType === 4) {
      return () => console.log('去集点');
    } else if (mission.missionType === 5) {
      return () => console.log('去兑换集点');
    } else if (mission.missionType === 6) {
      return () => console.log('去预约购买');
    } else if (mission.missionType === 7) {
      return () => console.log('去会员价格商品');
    } else if (mission.missionType === 8) {
      return () => console.log('去积分商城');
    } else if (mission.missionType === 9) {
      return () => console.log('去会员价格商品');
    }
  }
};
