<template>
  <div></div>
</template>

<script>
import _ from 'lodash';

export default {
  beforeRouteEnter (to, from, next) {
    if (from.path !== '/choose-location') {
      wx.miniProgram.navigateTo({ url: '/pages/webview-choose-location/webview-choose-location' });
    }

    next();
  },

  watch: {
    '$route' () {
      const locationInfo = JSON.parse(this.$route.query.location);
      const isNotEmpty = _.isEmpty(locationInfo) ? false : true;
      if (isNotEmpty) {
        this.$store.commit('location/setLocationInfo', locationInfo);
      }
      history.go(-2);
    }
  }
}
</script>

