<template>
  <div class="other-mission">
    <div class="other-mission__icon">
      <img :src="missionIconUrl">
    </div>
    <div class="other-mission__info">
      <div class="other-mission__info__title-and-reward">
        <span class="other-mission__info__title-and-reward__title">{{ mission.missionName }}</span>
        <span class="other-mission__info__title-and-reward__reward" v-if="missionRewardType === 'point'">+{{ mission.lotteryPoint }}积分</span>
        <span class="other-mission__info__title-and-reward__reward" v-else-if="missionRewardType === 'coupon'">+1礼券</span>
        <span class="other-mission__info__title-and-reward__reward" v-else-if="missionRewardType === 'lotteryCoin'">+{{ mission.lotteryTicketNum }}抽奖币</span>
      </div>
      <div class="other-mission__info__description">{{ mission.missionContent }}</div>
    </div>
    <div class="other-mission__right">
      <button @click="gotoMission" v-if="mission.receiveFlg === 2 && missionType !== 'firstRedeemPoint' && missionType !== 'firstPurchaseDiscountCommodity'">去完成</button>
      <div v-else-if="mission.receiveFlg === 1">已达成</div>
    </div>
  </div>
</template>

<script>
import missionService from '@/services/mission';

export default {
  props: {
    mission: Object
  },
  computed: {
    missionIconUrl() {
      return missionService.getMissionIconUrl(this.mission);
    },
    missionRewardType() {
      return missionService.getMissionRewardType(this.mission);
    },
    missionType() {
      return missionService.getMissionType(this.mission);
    }
  },
  methods: {
    gotoMission() {
      const missionType = this.missionType;
      switch (missionType) {
        case 'firstGotStamp': {
          this.gotoCampaign();
          break;
        }
        case 'firstGotCampaignReward': {
          this.gotoCampaign();
          break;
        }
        case 'firstPurchaseAnyCommodity': {
          this.backToHomepage();
          break;
        }
        case 'firstPurchaseAnyCommodityByNewUser': {
          this.backToHomepage();
          break;
        }
        default: {
          throw 'unknown mission type';
        }
      }
    },
    gotoCampaign() {
      this.$router.push('/campaign-list-view');
    },
    backToHomepage() {
      wx.miniProgram.switchTab({ url: '/pages/homepage/homepage' });
    }
  }
}
</script>

