import axios from 'axios';
import signatureService from '@/services/sign-api';

const BASE_URL = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchShops({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${BASE_URL}/app/v1/shop/`;
    return axios.get(url, { params: {
      timestamp,
      nonce,
      signature,
      regionBlockCode,
      pageSize: 3000
    } });
  },
  fetchOftenShoplist ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${BASE_URL}/app/v1/mina/oftenShoplist`;
    return axios.get(url, { params: { timestamp, nonce, signature, regionBlockCode } });
  }
};
