import axios from 'axios';

import signatureService from '@/services/sign-api';

const BASE_URL = process.env.VUE_APP_API_BASE_URL;

export default {
  fetch ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${BASE_URL}/app/v1/mina/user/address`
    return axios.get(url, {
      params: { timestamp, nonce, signature, regionBlockCode }
    });
  },
  add ({ addressInfo, regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${BASE_URL}/app/v1/mina/user/address/add`
    return axios.post(url, {
      timestamp, nonce, signature, regionBlockCode, ...addressInfo
    });
  },
  edit ({ addressInfo, regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    const url = `${BASE_URL}/app/v1/mina/user/address/edit`
    return axios.post(url, {
      timestamp, nonce, signature, regionBlockCode, ...addressInfo
    });
  }
}