const KEY = 'QVPBZ-YBMHV-KYMPX-UIXW2-7AM7Z-BNFNU';

function loadScript(url) {
  return new Promise((resolve, reject) => {
    const $script = document.createElement('script');
    $script.src = url;
    $script.addEventListener('error', reject);
    document.body.appendChild($script);
  });
}

export default {
  init() {
    return new Promise((resolve, reject) => {
      window.onQqLbsLoad = () => {
        resolve();
      };

      const qqLbsServiceInitialized = window.qq && window.qq.maps ? true : false;

      if (qqLbsServiceInitialized) {
        resolve();
      } else {
        loadScript(`https://map.qq.com/api/js?v=2.exp&key=${KEY}&callback=onQqLbsLoad`).catch(err => {
          reject(err);
        });
      }
    });
  },
  // 返回指定经纬度的地址信息
  getAddressByLatLng(latitude, longitude) {
    return new Promise((resolve, reject) => {
      new qq.maps.Geocoder({
        complete(result) {
          const addressInfo = result.detail;
          resolve(addressInfo);
        },
        error(err) {
          reject(err);
        }
      }).getAddress(new qq.maps.LatLng(latitude, longitude));
    });
  }
};
