<template>
  <div class="commodity-list" v-if="commodities !== null">
    <pull-down-refresh @refresh="fetchRecommendedCommodities" :isRefreshing="isRefreshing">
      <div class="commodity-list__item" v-for="(commodity, index) in commodities" :key="index" @click="toDetailView(commodity)">
        <img class="commodity-list__item__image" :src="commodity.pictureKeys[0]" />
        <div class="commodity-list__item__detail">
          <div class="commodity-list__item__detail__name">{{ commodity.commodityName }}</div>
          <div class="commodity-list__item__detail__popular" v-if="commodity.onlookerCount > 10">
            {{ commodity.onlookerCount }}人围观
          </div>
          <div class="commodity-list__item__detail__price">
            <span class="commodity-list__item__detail__price__rmb-sign">¥</span>
            <span class="commodity-list__item__detail__price__number">{{ commodity.price | formatRMB }}</span>
          </div>
        </div>
      </div>
    </pull-down-refresh>
  </div>
</template>

<script>
import api from '@/api';
import LoadingModal from '@/components/LoadingModal';
import PullDownRefresh from '@/components/PullDownRefresh';

export default {
  components: {
    PullDownRefresh
  },
  data () {
    return {
      commodities: null,
      isRefreshing: false
    };
  },

  methods: {
    toDetailView (commodity) {
      this.$router.push({ path: `/recommended-commodity-detail/${commodity.id}` });
    },
    fetchRecommendedCommodities () {
      this.isRefreshing = true;
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();
      api.recommendedCommodity.fetchRecommendedCommodities({ regionBlockCode })
      .then(commodities => {
        this.commodities = commodities;
        LoadingModal.hide();
        this.isRefreshing = false;
      })
      .catch(error => {
        this.isRefreshing = false;
      });
    }
  },

  filters: {
    formatRMB (rmb) {
      return rmb.toFixed(2);
    }
  },

  created () {
    this.fetchRecommendedCommodities();
  }
};
</script>

