<template>
  <div class="appointment-popup">
    <div class="appointment-popup__container">
      <div class="appointment-popup__container__head">
        <div class="appointment-popup__container__head__image">
          <img :src="productDetail.imageSmall">
        </div>
        <div class="appointment-popup__container__head__info">
          <div class="appointment-popup__container__head__info__price"><span>￥</span>{{ productDetail.sellPrice | toRMB }}</div>
          <div class="appointment-popup__container__head__info__stock">剩余库存：{{ productDetail.remainedQty }}</div>
        </div>
      </div>
      <div class="appointment-popup__container__content">
        <div class="appointment-popup__container__content__left">
          合计：
          <span class="appointment-popup__container__content__left__total-price"><span>¥</span> {{ totalPrice | toRMB }}</span>
        </div>
        <div class="appointment-popup__container__content__right">
          <div class="appointment-popup__container__content__right__quantity">
            <span class="appointment-popup__container__content__right__quantity__label">数量:</span>
            <div class="appointment-popup__container__content__right__quantity__icon-decrease" @click="decreaseQuantity"></div>
            <span class="appointment-popup__container__content__right__quantity__number">{{ purchaseQuantity }}</span>
            <div class="appointment-popup__container__content__right__quantity__icon-increase" @click="increaseQuantity"></div>
          </div>
          <div class="appointment-popup__container__content__right__descr">每个用户最多可预约{{ productDetail.singleLimited }}个</div>
        </div>
      </div>
      <div class="appointment-popup__container__confirm" @click="gotoConfirmOrder">确定</div>
      <div class="appointment-popup__container__close" @click="hideAppointmentPopup"></div>
    </div>
  </div>
</template>

<script>
import api from '@/api';
import LoadingModal from '@/components/LoadingModal';

export default {
  data () {
    return {
      purchaseQuantity: 1,
    }
  },

  props: ['productDetail'],

  computed: {
    totalPrice() {
      return this.productDetail.sellPrice * this.purchaseQuantity;
    }
  },

  methods: {
    decreaseQuantity() {
      if (this.purchaseQuantity > 1) {
        this.purchaseQuantity -= 1;
      }
    },

    increaseQuantity() {
      if (this.purchaseQuantity < this.productDetail.singleLimited && this.purchaseQuantity < this.productDetail.remainedQty) {
        this.purchaseQuantity += 1;
      }
    },

    hideAppointmentPopup() {
      this.$emit('hideAppointmentPopup');
    },

    gotoConfirmOrder() {
      LoadingModal.show();
      const commodityId = this.productDetail.commodityId;
      const quantity = this.purchaseQuantity;
      api.ec.createOrder({ commodityId, quantity }).then(res => {
        const orderId = res.data.data;
        this.$router.push({ path: `/ec-confirm-order/${orderId}` });
      }).finally(() => LoadingModal.hide());
    }
  },

  filters: {
    toRMB(price) {
      return price.toFixed(1);
    },

    toDays(timestamp) {
      return Math.ceil(timestamp/(1000*60*60*24));
    }
  }
}
</script>

