<template>
  <div class="campaign-exchange__modal">
    <div class="campaign-exchange__modal__wrapper">
      <div class="campaign-exchange__modal__wrapper__title">兑换成功</div>
      <div class="campaign-exchange__modal__wrapper__description-top">
        <div class="campaign-exchange__modal__wrapper__description-top__name-wrapper">
          <div class="campaign-exchange__modal__wrapper__description-top__name">{{campaignGift.giftName}}</div>
          <div class="campaign-exchange__modal__wrapper__description-top__tip">{{couponType}}</div>
        </div>
        <div class="campaign-exchange__modal__wrapper__description-top__condition">无使用门槛</div>
      </div>
      <div class="campaign-exchange__modal__wrapper__description-bottom">您已成功兑换了{{campaignGift.giftName}}</div>

      <div class="campaign-exchange__modal__wrapper__button-wrapper">
        <div class="campaign-exchange__modal__wrapper__button-wrapper__gotoHome" @click="gotoHomePage">返回首页</div>
        <div class="campaign-exchange__modal__wrapper__button-wrapper__gotoCoupons" @click="gotoCouponDetail">查看</div>
      </div>
    </div>
  </div>
</template>

<script>
import couponType from '@/services/campaign-exchange-coupon-type-service';
export default {
  props: [
    'campaignGift'
  ],
  computed: {
    couponType: function() {
      if(couponType.getType(this.campaignGift) === 'couponcouponType:discountOnReachingTotal') {
        return '免费券';
      }
      else if(couponType.getType(this.campaignGift) === 'couponcouponType:discountOnReachingTotal') {
         return '满减券';
      }
      else if(couponType.getType(this.campaignGift) === 'couponcouponType:discountOnItem') {
        return '立减券';
      }
    }
  },
  methods: {
    gotoHomePage() {
      this.$emit('close');
      wx.miniProgram.switchTab({ url: '/pages/homepage/homepage' });
    },
    gotoCouponDetail() {
      const couponId = this.campaignGift.couponId;
      const userCouponId = this.campaignGift.userCouponId;
      this.$router.push({ path: `/user-coupon-detail/${couponId}/${userCouponId}` });
      this.$emit('close');
    }
  }
};
</script>

