<template>
  <div class="lottery">
    <div class="lottery__gachapon">
      <div class="lottery__gachapon__background"></div>
      <div class="lottery__gachapon__logo" :style="{ 'background-image': 'url(' + logoImgUrl + ')' }"></div>
      <div class="lottery__gachapon__capsules">
        <div class="lottery__gachapon__capsules__wrapper" v-if="capsules !== null && capsulePhysicsInfo !== null">
          <capsule v-for="(capsule, i) in capsules" :key="i" :capsule="capsule" :x="capsulePhysicsInfo[i].x" :y="capsulePhysicsInfo[i].y" :r="capsulePhysicsInfo[i].r"></capsule>
        </div>
      </div>
      <div class="lottery__gachapon__collected-count">{{ collectedCapsulesCount }}</div>
      <div class="lottery__gachapon__collected" v-if="collectedCapsules !== null">
        <div class="lottery__gachapon__collected__capsule" v-for="capsule in collectedCapsules" :class="capsule.color"></div>
      </div>
      <div class="lottery__gachapon__goto-collected" @click="gotoCollected"></div>
      <div class="lottery__gachapon__broadcast">
        <div>
          <span>133****0000获得草莓8折券一张🎉</span>
          <span>138****0000获得草莓7折券一张🎉</span>
          <span>139****0000获得草莓6折券一张🎉</span>
          <span>131****0000获得草莓5折券一张🎉</span>
          <span>135****0000获得草莓4折券一张🎉</span>
        </div>
      </div>
      <div class="lottery__gachapon__dropped-capsule" :class="droppedCapsule.color" v-if="droppedCapsule !== null"></div>
      <div class="lottery__gachapon__try-dropped-capsule blue" v-else-if="tryDroppedCapsuleVisible"></div>
      <div class="lottery__gachapon__knob rotating" v-if="isFetchingResult"></div>
      <div class="lottery__gachapon__knob" v-else-if="isFetchingLotteryDetail"></div>
      <div class="lottery__gachapon__knob" @click="startGachapon" v-else></div>
      <div class="lottery__gachapon__current-mode-selection-wrapper" :style="{ left: currentModeSelectionWrapperLeft }"></div>
      <div class="lottery__gachapon__mode try" @click="useModeTry">
        <p class="lottery__gachapon__mode__title try" :class="{ selected: currentMode === 'try' }">试一试</p>
        <p class="lottery__gachapon__mode__subtitle try" :class="{ selected: currentMode === 'try' }">免费试手气</p>
      </div>
      <div class="lottery__gachapon__mode once" @click="useModeOnce">
        <p class="lottery__gachapon__mode__title once" :class="{ selected: currentMode === 'once' }">扭一次</p>
        <p class="lottery__gachapon__mode__subtitle once" :class="{ selected: currentMode === 'once' }">{{ lotteryCoinForOneTrial }}抽奖币</p>
      </div>
      <div class="lottery__gachapon__mode three-times" @click="useModeThreeTimes">
        <p class="lottery__gachapon__mode__title three-times" :class="{ selected: currentMode === 'threeTimes' }">扭三次</p>
        <p class="lottery__gachapon__mode__subtitle three-times" :class="{ selected: currentMode === 'threeTimes' }">{{ lotteryCoinForOneTrial * 3 }}抽奖币</p>
      </div>
      <div class="lottery__gachapon__goto-mission-center" @click="gotoMissionCenter"></div>
      <div class="lottery__gachapon__lottery-coin-count">{{ lotteryCoinCount }}</div>
    </div>
    <div class="lottery__rules">
      <div class="lottery__rules__wrapper">
        <p class="lottery__rules__wrapper__title">
          <span class="lottery__rules__wrapper__title__left-bar"></span>
          <span class="lottery__rules__wrapper__title__text">游戏说明</span>
          <span class="lottery__rules__wrapper__title__right-bar"></span>
        </p>
        <p class="lottery__rules__wrapper__rule">❶ 每次抽奖都消耗{{ lotteryCoinForOneTrial }}枚抽奖币，每个用户每天最多抽奖{{ maxLotteryTimeForOneDay }}次</p>
        <p class="lottery__rules__wrapper__rule">❷ 每次扭蛋都可以获得一个随机颜色的蛋壳，蛋壳颜色共3种</p>
        <p class="lottery__rules__wrapper__capsule-intro">
          <span class="lottery__rules__wrapper__capsule-intro__capsule red"></span>
          <span class="lottery__rules__wrapper__capsule-intro__text">红色扭蛋 会随机出现优惠券</span>
        </p>
        <p class="lottery__rules__wrapper__capsule-intro">
          <span class="lottery__rules__wrapper__capsule-intro__capsule blue"></span>
          <span class="lottery__rules__wrapper__capsule-intro__text">蓝色扭蛋 会随机出现会员积分</span>
        </p>
        <p class="lottery__rules__wrapper__capsule-intro">
          <span class="lottery__rules__wrapper__capsule-intro__capsule yellow"></span>
          <span class="lottery__rules__wrapper__capsule-intro__text">黄色扭蛋 会随机出现抽奖币</span>
        </p>
      </div>
    </div>
    <div class="lottery__result-modal" v-if="gachaponResultVisible">
      <div class="lottery__result-modal__mask"></div>
      <div class="lottery__result-modal__spark-left"></div>
      <div class="lottery__result-modal__spark-right"></div>
      <div class="lottery__result-modal__opened-capsule-right" :class="droppedCapsule.color"></div>
      <div class="lottery__result-modal__opened-capsule-left" :class="droppedCapsule.color"></div>
      <div class="lottery__result-modal__wrapper">
        <div class="lottery__result-modal__wrapper__reward-icon empty" v-if="droppedCapsule.reward === null"></div>
        <div class="lottery__result-modal__wrapper__reward-icon" :class="droppedCapsule.reward.type" v-else></div>

        <div class="lottery__result-modal__wrapper__reward-title" v-if="droppedCapsule.reward === null">这个扭蛋是空的</div>
        <div class="lottery__result-modal__wrapper__reward-title" v-else-if="droppedCapsule.reward.type === 'coupon'">{{ droppedCapsule.reward.title }}</div>
        <div class="lottery__result-modal__wrapper__reward-title" v-else-if="droppedCapsule.reward.type === 'point'">赠送积分 <span>+{{ droppedCapsule.reward.pointCount }}</span></div>
        <div class="lottery__result-modal__wrapper__reward-title" v-else-if="droppedCapsule.reward.type === 'lotteryCoin'">赠送抽奖币 <span>+{{ droppedCapsule.reward.lotteryCoinCount }}</span></div>

        <div class="lottery__result-modal__wrapper__reward-subtitle" v-if="droppedCapsule.reward === null">加油，说不定下一次就是大奖哦</div>
        <div class="lottery__result-modal__wrapper__reward-subtitle" v-else-if="droppedCapsule.reward.type === 'coupon'">详情可至“优惠券”查看</div>
        <div class="lottery__result-modal__wrapper__reward-subtitle" v-else-if="droppedCapsule.reward.type === 'point'">请下载并安装APP，可至我的查看</div>
        <div class="lottery__result-modal__wrapper__reward-subtitle" v-else-if="droppedCapsule.reward.type === 'lotteryCoin'">已经收入你的帐户，可至任务中心查看</div>
      </div>
      <button class="lottery__result-modal__try-again" v-if="trialLeft > 0" @click="continueGachapon">继续</button>
      <button class="lottery__result-modal__try-again" @click="hideGachaponResult" v-else>去扭蛋</button>
    </div>
    <div class="lottery__try-result-modal" v-else-if="gachaponTryResultVisible">
      <div class="lottery__try-result-modal__mask"></div>
      <div class="lottery__try-result-modal__spark-left"></div>
      <div class="lottery__try-result-modal__spark-right"></div>
      <div class="lottery__try-result-modal__opened-capsule-right blue"></div>
      <div class="lottery__try-result-modal__opened-capsule-left blue"></div>
      <div class="lottery__try-result-modal__wrapper">
        <div class="lottery__try-result-modal__wrapper__reward-icon try-success"></div>

        <div class="lottery__try-result-modal__wrapper__reward-title">免费尝试成功</div>

        <div class="lottery__try-result-modal__wrapper__reward-subtitle">马上去实践吧，试试运气呦~~</div>
      </div>
      <button class="lottery__result-modal__try-again" @click="hideGachaponTryResult">去扭蛋</button>
    </div>
    <div class="lottery__lottery-coin-not-enough-modal" v-if="lotteryCoinNotEnoughModalVisible">
      <div class="lottery__lottery-coin-not-enough-modal__mask"></div>
      <div class="lottery__lottery-coin-not-enough-modal__wrapper">
        <div class="lottery__lottery-coin-not-enough-modal__wrapper__close" @click="hideLotteryCoinNotEnoughModal"></div>
        <div class="lottery__lottery-coin-not-enough-modal__wrapper__icon"></div>
        <div class="lottery__lottery-coin-not-enough-modal__wrapper__title">余额不足</div>
        <div class="lottery__lottery-coin-not-enough-modal__wrapper__subtitle">剩余：{{ lotteryCoinCount }}抽奖币</div>
      </div>
      <button class="lottery__lottery-coin-not-enough-modal__get-lottery-coin" @click="getLotteryCoin">获取抽奖币</button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import _ from 'lodash';
import { Body, Bodies, World, Engine, Composite, Vector } from 'matter-js';

import api from '@/api';
import envService from '@/services/env';

import lotteryModuleOption from '@/store/lottery/lottery';

import MessageBox from '@/components/MessageBox';
import LoadingModal from '@/components/LoadingModal';
import Capsule from './Lottery/Capsule';

export default {
  components: { Capsule },
  data() {
    return {
      logoImgUrl: null,
      lotteryCoinCount: null,
      lotteryId: null,
      lotteryCoinForOneTrial: null,
      maxLotteryTimeForOneDay: null,
      currentMode: 'try',
      trialLeft: 0,
      isFetchingResult: false,
      capsules: null,
      lotteryHistory: null,
      gachaponResultVisible: false,
      gachaponTryResultVisible: false,
      lotteryCoinNotEnoughModalVisible: false,
      droppedCapsule: null,
      tryDroppedCapsuleVisible: false,
      capsulePhysicsInfo: null,
      circles: []
    };
  },
  computed: {
    currentModeSelectionWrapperLeft() {
      if (this.currentMode === 'try') {
        return '18vw';
      } else if (this.currentMode === 'once') {
        return '39vw';
      } else if (this.currentMode === 'threeTimes') {
        return '60vw';
      }
    },
    isFetchingLotteryDetail() {
      return this.lotteryId === null;
    },
    collectedCapsules() {
      return this.$store.getters['lottery/collectedCapsulesFirstThree'];
    },
    collectedCapsulesCount() {
      return this.$store.getters['lottery/collectedCapsulesCount'];
    }
  },
  methods: {
    gotoCollected () {
      this.$router.push('/lottery/collected');
    },
    gotoMissionCenter () {
      this.$router.replace('/mission-center');
    },
    useModeTry () {
      this.currentMode = 'try';
    },
    useModeOnce() {
      this.currentMode = 'once';
    },
    useModeThreeTimes() {
      this.currentMode = 'threeTimes';
    },
    getLotteryCoin() {
      this.lotteryCoinNotEnoughModalVisible = false;
      this.$router.replace('/mission-center');
    },
    startGachapon() {
      if (this.currentMode === 'try') {
        this.trialLeft = 0;
        this.startGachaponForTry();
      } else if (this.currentMode  === 'once') {
        if (this.lotteryCoinCount >= this.lotteryCoinForOneTrial) {
          this.trialLeft = 0;
          this.startGachaponForOnce();
        } else {
          this.showLotteryCoinNotEnoughModal();
        }
      } else if (this.currentMode  === 'threeTimes') {
        if (this.lotteryCoinCount >= this.lotteryCoinForOneTrial * 3) {
          this.trialLeft = 2;
          this.startGachaponForOnce();
        } else {
          this.showLotteryCoinNotEnoughModal();
        }
      }
    },
    startGachaponForTry() {
      this.isFetchingResult = true;
      // 请求期间使得扭蛋受向上的力而运动
      const timer = setInterval(() => {
        this.circles.forEach(circle => {
          Body.applyForce(circle, circle.position, Vector.create(0, -0.002));
        });
      }, 500);
      setTimeout(() => {
        clearInterval(timer);
        this.isFetchingResult = false;
        this.showGachaponTryResult();
      }, 3000);
    },
    startGachaponForOnce() {
      this.isFetchingResult = true;
      // 请求期间使得扭蛋受向上的力而运动
      const timer = setInterval(() => {
        this.circles.forEach(circle => {
          Body.applyForce(circle, circle.position, Vector.create(0, -0.002));
        });
      }, 500);
      this.fetchGachaponResult().then(res => {
        clearInterval(timer);
        this.isFetchingResult = false;
        this.$store.commit('lottery/addCollectedCapsule', res);
        this.showGachaponResult(res);
      }).catch(err => {
        clearInterval(timer);
        this.isFetchingResult = false;
        if (err === 'reachMaxLotteryTimeForOneDay') {
          MessageBox.alert({ msg: '今日抽奖次数已达上限，明天再来哦' });
        } else if (err === 'rewardNoRemaining') {
          MessageBox.alert({ msg: '奖品发光了，下次早点来哦' });
        }
      });
    },
    continueGachapon() {
      this.fetchLotteryDetail();
      this.hideGachaponResult();
      this.trialLeft--;
      this.startGachaponForOnce();
    },
    showGachaponResult(result) {
      this.droppedCapsule = result;
      this.gachaponResultVisible = true;
    },
    showGachaponTryResult() {
      this.tryDroppedCapsuleVisible = true;
      this.gachaponTryResultVisible = true;
    },
    hideGachaponResult() {
      this.fetchLotteryDetail();
      this.gachaponResultVisible = false;
      this.droppedCapsule = null;
    },
    hideGachaponTryResult() {
      this.tryDroppedCapsuleVisible = false;
      this.gachaponTryResultVisible = false;
    },
    showLotteryCoinNotEnoughModal() {
      this.lotteryCoinNotEnoughModalVisible = true;
    },
    hideLotteryCoinNotEnoughModal() {
      this.lotteryCoinNotEnoughModalVisible = false;
    },
    getCapsulesWithLogos(logos) {
      const CAPSULE_COLORS = ['yellow', 'blue', 'red'];
      const NUM_OF_CAPSULES = 10;

      const capsules = [];
      for (let i = 0; i < NUM_OF_CAPSULES; i++) {
        const capsule = {
          color: _.sample(CAPSULE_COLORS),
          logo: _.sample(logos)
        };
        capsules.push(capsule);
      }
      return capsules;
    },
    getCollectedCapsules(lotteryHistory) {
      return lotteryHistory.filter(lotteryHistoryEntry => {
        // lotteryType为0,1,2时表示中奖，其余情况为未中奖，在显示抽奖历史时需过滤掉
        const gotRewardLotteryTypes = [0, 1, 2];
        const gotReward = gotRewardLotteryTypes.indexOf(lotteryHistoryEntry.lotteryType) > -1 ? true : false;
        return gotReward;
      }).map(lotteryHistoryEntry => {
        const rewardType = (() => {
          if (lotteryHistoryEntry.lotteryType === 0) {
            return 'point';
          } else if (lotteryHistoryEntry.lotteryType === 1) {
            return 'coupon';
          } else if (lotteryHistoryEntry.lotteryType === 2) {
            return 'lotteryCoin';
          }
        })();

        if (rewardType === 'point') {
          return { color: 'blue' };
        } else if (rewardType === 'coupon') {
          return { color: 'red' }
        } else if (rewardType === 'lotteryCoin') {
          return { color: 'yellow' }
        }
      });
    },
    fetchLotteryDetail() {
      return new Promise((resolve, reject) => {
        const regionBlockCode = envService.getRegionBlockCode();

        api.lottery.fetchLotteryDetail({ regionBlockCode }).then(lotteryDetail => {
          this.lotteryId = lotteryDetail.lotteryId;
          this.lotteryCoinCount = lotteryDetail.userTicketNum;
          this.lotteryCoinForOneTrial = lotteryDetail.lotteryCouponCount;
          this.maxLotteryTimeForOneDay = lotteryDetail.lotteryTimes;

          this.logoImgUrl = lotteryDetail.bigLogo;
          this.capsules = this.getCapsulesWithLogos(lotteryDetail.smallLogoArr);
          resolve();
        });
      });
    },
    fetchGachaponResult() {
      return new Promise((resolve, reject) => {
        const regionBlockCode = envService.getRegionBlockCode();
        api.lottery.startLottery({ lotteryId: this.lotteryId, regionBlockCode }).then(lotteryResult => {
          const noReward = lotteryResult.lotteryType === 3 ? true : false;
          if (noReward) {
            resolve({
              color: 'blue',
              reward: null
            });
          } else {
            const rewardType = (() => {
              if (lotteryResult.lotteryType === 0) {
                return 'point';
              } else if (lotteryResult.lotteryType === 1) {
                return 'coupon';
              } else if (lotteryResult.lotteryType === 2) {
                return 'lotteryCoin';
              }
            })();

            if (rewardType === 'point') {
              resolve({
                color: 'blue',
                reward: {
                  type: 'point',
                  pointCount: lotteryResult.lotteryNum
                }
              });
            } else if (rewardType === 'coupon') {
              resolve({
                color: 'red',
                reward: {
                  type: 'coupon',
                  title: lotteryResult.lotteryName
                }
              });
            } else if (rewardType === 'lotteryCoin') {
              resolve({
                color: 'yellow',
                reward: {
                  type: 'lotteryCoin',
                  lotteryCoinCount: lotteryResult.lotteryNum
                }
              });
            }
          }
        }).catch(err => {
          const rewardNoRemaining = err.data.errors[0].code === "26004" ? true : false;
          const reachMaxLotteryTimeForOneDay = err.data.errors[0].code === "26003" ? true : false;
          if (rewardNoRemaining) {
            reject('rewardNoRemaining');
          } else if (reachMaxLotteryTimeForOneDay) {
            reject('reachMaxLotteryTimeForOneDay');
          } else {
            reject(err);
          }
        });
      });
    },
    createCapsulePhysicsWorld() {
      // create an engine
      const engine = Engine.create();
      engine.enableSleeping = true;

      // create surrounding bounds
      const leftBound = Bodies.rectangle(0.5, 22.5, 1, 45, { isStatic: true });
      const rightBound = Bodies.rectangle(91.5, 22.5, 1, 45, { isStatic: true });
      const bottomBound = Bodies.rectangle(46, 44.5, 92, 1, { isStatic: true });
      const topBound = Bodies.rectangle(46, 0.5, 92, 1, { isStatic: true });

      // add all of the bounds to the world
      World.add(engine.world, [topBound, bottomBound, leftBound, rightBound]);

      // create all capsules and add to the world
      for (let i = 0; i < this.capsules.length; i++) {
        const capsule = Bodies.circle(92 / this.capsules.length * (i % 5) + 7.5, (Math.floor(i / 5) + 1) * 10, 7.5, { restitution: 0.99 });
        World.add(engine.world, [capsule]);
        this.circles.push(capsule);
      }

      // run the engine
      Engine.run(engine);

      const self = this;
      (function render() {
        window.requestAnimationFrame(render);
        const bodies = Composite.allBodies(engine.world);
        const circles = bodies.slice(4);
        const circlesPhysicsInfo = circles.map(circle => {
          return {
            x: (circle.position.x - 7.5) / 92,
            y: (circle.position.y - 7.5) / 45,
            r: circle.angle
          };
        });
        self.capsulePhysicsInfo = circlesPhysicsInfo;
      })();
    }
  },
  created() {
    LoadingModal.show();
    if (!this.$store.state.lottery) {
      this.$store.registerModule('lottery', lotteryModuleOption, { preserveState: false });
      this.$store.commit('lottery/resetCollectedCapsules');
    }
    this.fetchLotteryDetail().then(() => {
      LoadingModal.hide();
      this.createCapsulePhysicsWorld();
    });
  },
  beforeRouteLeave(to, from, next) {
    if (to.path !== '/lottery/collected') {
      this.$store.unregisterModule('lottery')
    }
    next();
  }
};
</script>

