import api from '@/api';

function loadScript(url) {
  return new Promise((resolve, reject) => {
    const $script = document.createElement('script');
    $script.src = url;
    $script.addEventListener('load', resolve);
    $script.addEventListener('error', reject);
    document.body.appendChild($script);
  });
}

export default {
  init() {
    return new Promise((resolve, reject) => {
      loadScript('https://res.wx.qq.com/open/js/jweixin-1.3.2.js').then(() => {
        const url = window.location.href.split('#')[0];
        return api.wechat.fetchJsSdkSignatureData({ url });
      }).then(jsSdkSignatureData => {
        wx.config({
          appId: process.env.VUE_APP_WECHAT_APPID,
          timestamp: jsSdkSignatureData.timestamp,
          nonceStr: jsSdkSignatureData.nonceStr,
          signature: jsSdkSignatureData.signature,
          jsApiList: ['getLocation', 'openLocation']
        });
        wx.ready(() => {
          resolve();
        });
        wx.error(err => {
          reject(err);
        });
      }).catch(err => {
        reject(err);
      });
    });
  },
  getLocation() {
    return new Promise((resolve, reject) => {
      wx.getLocation({
        success(res) {
          resolve(res);
        },
        fail(err) {
          reject(err);
        }
      });
    });
  },
  openLocation(latitude, longitude, name, address ) {
    wx.openLocation({ latitude, longitude, name, address });
  }
}
