<template>
  <div class="timeline-bar">
    <div class="timeline-bar__line"></div>

    <div :class="[
      'timeline-bar__start-date',
      {
        'is-middle': startDate > currentDate && startDate < endDate,
        'is-hidden': startDate <= currentDate
      }
    ]">
      <div class="timeline-bar__start-date__title">活动开始</div>
      <div class="timeline-bar__start-date__circle-icon"></div>
      <div class="timeline-bar__start-date__date">{{ startDate | date }}</div>
    </div>

    <div :class="[
      'timeline-bar__end-date',
      {
        'is-left': endDate <= currentDate,
        'is-right': endDate > currentDate
      }
    ]">
      <div class="timeline-bar__end-date__title">活动结束</div>
      <div class="timeline-bar__end-date__circle-icon"></div>
      <div class="timeline-bar__end-date__date">{{ endDate | date }}</div>
    </div>

    <div :class="[
      'timeline-bar__current-date',
      {
        'is-left': currentDate < startDate,
        'is-right': currentDate >= endDate
      }
    ]">
      <div class="timeline-bar__current-date__title">今天</div>
      <div class="timeline-bar__current-date__circle-icon"></div>
      <div class="timeline-bar__current-date__date">{{ currentDate | date }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['couponDetails'],

  data () {
    return {
      startDate: this.couponDetails.useDateFrom,
      endDate: this.couponDetails.useDateTo,
      currentDate: Date.now()
    };
  },

  filters: {
    date (timeStamp) {
      const year = (new Date(timeStamp)).getFullYear();
      const _m   = (new Date(timeStamp)).getMonth() + 1;
      const _d   = (new Date(timeStamp)).getDate();

      // 小于10的月和日前面需要加0，例如“3月2日”需要显示为“03月02日”。
      const month = (_m < 10) ? ('0' + _m) : String(_m);
      const day   = (_d < 10) ? ('0' + _d) : String(_d);
      return `${year}-${month}-${day}`;
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~style/variables.scss';

.timeline-bar {
  position: relative;
  height: 20.13vw;
  font-size: $font-size-small;

  &__line {
    position: absolute;
    top: 11vw;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #eeeeee;
  }

  &__start-date,
  &__end-date,
  &__current-date {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 2vw;
    height: 18vw;

    &__circle-icon {
      width: 2.4vw;
      height: 2.4vw;
      background-color: #ffffff;
      border: 2px solid #eeeeee;
      border-radius: 50%;
    }
  }

  &__start-date,
  &__end-date,
  &__current-date {
    &.is-left {
      align-items: flex-start;
      left: 0;
    }

    &.is-middle {
      align-items: center;
      left: calc(50% - 10vw);
    }

    &.is-right {
      align-items: flex-end;
      right: 0;
    }

    &.is-hidden {
      display: none;
    }
  }

  &__current-date {
    &__circle-icon {
      border-color: #006cb8;
    }
  }
}
</style>
