<template>
  <div class="message-box">
    <div class="message-box__mask" v-if="visible"></div>
    <transition name="bounce">
      <div class="message-box__content" v-if="visible">
        <div class="message-box__title">{{ title }}</div>
        <div class="message-box__msg" v-html="msg"></div>

        <div v-if="type === 'alert'">
          <div class="message-box__operation">
            <div class="message-box__i-know-button" @click="handleIknow">我知道了</div>
          </div>
        </div>
        <div v-else-if="type === 'confirm'">
          <div class="message-box__operation">
            <div class="message-box__confirm-button" @click="handleConfirm">{{ confirmButtonText }}</div>
            <div class="message-box__cancel-button" @click="handleCancel">{{ cancelButtonText }}</div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    props: {
      type: String,

      title: String,
      msg: String,
      confirmButtonText: String,
      cancelButtonText: String,

      onIknow: Function,
      onConfirm: Function,
      onCancel: Function
    },

    data () {
      return {
        visible: false
      };
    },

    methods: {
      handleIknow () {
        this.onIknow();
        this.visible = false;
      },
      handleCancel () {
        this.onCancel();
        this.visible = false;
      },
      handleConfirm () {
        this.onConfirm();
        this.visible = false;
      }
    },

    mounted () {
      window.addEventListener('popstate', e => this.visible = false);
    }
  };
</script>

