      font-size: 3.6vw;
      line-height: 5.5vw;
    }

    &__operation {
      display: flex;
      width: 100%;
      height: 12vw;
      border-top: 1px solid #ececec;
      box-sizing: border-box;
      line-height: 12vw;
    }

    &__i-know-button,
    &__cancel-button,
    &__confirm-button {
      flex: 1;
      box-sizing: border-box;
    }

    &__confirm-button {
      border-right: 1px solid #ececec;
    }

    &__i-know-button,
    &__confirm-button {
      color: #006cb8;
    }

  .bounce-enter-active {
    animation: bounce-in .5s;
  }
  @keyframes bounce-in {
    0% {
      transform: scale(0);
