<template>
  <div class="campaign-list">
    <div class="campaign-list__loading" v-if="campaignList === null">
      <div class="campaign-list__loading__skeleton-screen"></div>
    </div>

    <pull-down-refresh @refresh="fetchCampaignList" :isRefreshing="isRefreshing" v-else>
      <div class="campaign-list__error" v-if="campaignList.length === 0">
        <div class="campaign-list__error__image"></div>
        <div class="campaign-list__error__no-data">暂无集点活动</div>
      </div>

      <div class="campaign-list__success" v-else>
        <div :class="[
          'campaign-list__title-tip',
          {
            'close-title-tip-title': isCloseTitleTip
          }
        ]" v-if="canShowTitleTip" >
          <div class="campaign-list__title-tip__image"></div>
          <div class="campaign-list__title-tip__text-wrapper">
            <div class="campaign-list__title-tip__text" v-if="canTransferCampaignToFriend">你可通过转赠集点与朋友共同达成目标</div>
            <div class="campaign-list__title-tip__text" v-else>您有 {{ aboutToReachQuatity }} 项集点即将达成目标，加油！！</div>
            <div class="campaign-list__title-tip__close-wrapper" @click="closeTitleTip">
              <div class="campaign-list__title-tip__close"></div>
            </div>
          </div>
        </div>
          <div :class="[
            'campaign-list__list-body',
            {
              'close-title-tip-body': isCloseTitleTip
            }
          ]">
            <campaign-list-view-item
              v-for="campaign in campaignList" :campaign=campaign :key="campaign.campaignId">
            </campaign-list-view-item>
          </div>
      </div>
    </pull-down-refresh>
    <div class="campaign-list__list-foot">
      <goto-home></goto-home>
    </div>
  </div>
</template>

<script>
import api from '@/api';

import GotoHome from '@/components/GotoHome';
import PullDownRefresh from '@/components/PullDownRefresh';

import envService from '@/services/env';
import campaignStatusService from '@/services/campaign-status';
import CampaignListViewItem from '@/entries/campaign/CampaignList/CampaignListViewItem';

export default {
  components: {
    GotoHome,
    PullDownRefresh,
    CampaignListViewItem
  },
  data () {
    return {
      campaignList: null,
      isRefreshing: false,
      isCloseTitleTip: false,
      canShowTitleTip: false,
      aboutToReachQuatity: 0,
      canTransferCampaignToFriend: false
    };
  },
  created() {
    this.fetchCampaignList();
  },
  methods: {
    parseRegionBlockCode(regionBlockCode){
      switch (regionBlockCode) {
        case 'sh-lawson':
          return '江浙沪';
        case 'bj-lawson':
          return '京津冀';
        case 'ah-lawson':
          return '安徽';
        case 'dl-lawson':
          return '大连';
        case 'wh-lawson':
          return '武汉';
        case 'cq-lawson':
          return '重庆';
        default:
          return '';
      }
    },
    closeTitleTip() {
      this.isCloseTitleTip = true;
    },
    fetchCampaignList() {
      this.isRefreshing = true;
      const regionBlockCode = envService.getRegionBlockCode();
      api.campaign.fetchCampaignList({ regionBlockCode })
      .then(campaignList => {
        this.campaignList = campaignList.map((campaign, index) => {
          const { campaignStatus, campaignStatusText } = campaignStatusService.getCampaignStatus(campaign);
          const isAboutToReach = campaignStatusService.isAboutToReach(campaign);
          if (isAboutToReach) {
            this.aboutToReachQuatity += 1;
          }
          const canTransferCampaignToFriend = campaignStatusService.canTransferCampaignToFriend(campaign);
          if (canTransferCampaignToFriend) {
            this.canTransferCampaignToFriend = canTransferCampaignToFriend;
          }
          const name = campaign.campaignName;
          const image = campaign.imagePathList;
          const status = campaignStatusService.getStatus(campaign);
          const region = this.parseRegionBlockCode(regionBlockCode);
          const needCount = campaign.stampNeeded;
          const campaignId = campaign.campaignId;
          const surplusCount = campaign.userTotalStampNum % campaign.stampNeeded;
          const onlookerCount = campaign.onlookerCount;
          return {
            name,
            image,
            status,
            region,
            needCount,
            campaignId,
            surplusCount,
            onlookerCount,
            campaignStatus,
            campaignStatusText
          };
        });
        this.canShowTitleTip = this.aboutToReachQuatity > 0;
        this.isRefreshing = false;
      })
      .catch(error => {
        this.campaignList = [];
        this.isRefreshing = false;
      });
    }
  },
}

</script>

