<template>
  <div class="new-shop-item">
    <div class="new-shop-item__shop">
      <div class="new-shop-item__shop__name">
        <span class="new-shop-item__shop__name__title">{{ newShop.shopInfo.shopName }}</span>
        <format-business-time v-if="newShop.shopInfo.businessTime !== ''" :businessTime="newShop.shopInfo.businessTime"></format-business-time>
      </div>
      <div class="new-shop-item__shop__address">
        <span class="new-shop-item__shop__address__title">{{ newShop.shopInfo.address }}</span>
        <span class="new-shop-item__shop__address__km" v-if="shopDistance">距您{{ shopDistance | toKilometer }}</span>
      </div>
    </div>
    <div class="new-shop-item__coupon-list" v-if="couponList.length > 0">
      <div v-for="(couponItem, index) in couponList" :key="index">
        <coupon-item-not-begin
          :coupon="couponItem"
          v-if="couponItem.notBegin">
        </coupon-item-not-begin>
        <coupon-item-picked
          :coupon="couponItem"
          v-else-if="couponItem.pickFlg === 1">
        </coupon-item-picked>
        <coupon-item-no-remaining
          :coupon="couponItem"
          v-else-if="couponItem.couponRemain <= 0 && couponItem.sendUpperLimit > 0">
          </coupon-item-no-remaining>
        <coupon-item-unclaimed
          :coupon="couponItem"
          v-else-if="couponItem.pickFlg === 0"
          @click="pickNewShopCoupon(couponItem.couponId)">
        </coupon-item-unclaimed>
      </div>
      <div v-if="newShop.shopCoupons.length > 2">
        <div class="new-shop-item__coupon-list__unfold" v-if="!isUnfold" @click="unfoldCouponList">
          <span>展开剩余{{ unfoldCouponListLength }}张</span>
          <img :src="ossImageUrl + '/coupon/icon-forward-copy.png'" class="new-shop-item__coupon-list__forward__image">
        </div>
        <div class="new-shop-item__coupon-list__unfold" v-else @click="unfoldCouponList">
          <span>收起</span>
          <img :src="ossImageUrl + '/coupon/icon-unfold.png'" class="new-shop-item__coupon-list__unfold__image">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import FormatBusinessTime    from '@/components/FormatBusinessTime';
import CouponItemPicked      from './NewShopItem/CouponItemPicked';
import CouponItemNotBegin    from './NewShopItem/CouponItemNotBegin';
import CouponItemUnclaimed   from './NewShopItem/CouponItemUnclaimed';
import CouponItemNoRemaining from './NewShopItem/CouponItemNoRemaining';

import api from '@/api';
import envService from '@/services/env';
import wechatSdkService from '@/services/wechat-mini-program-bridge';
import calculateDistance from '@/utils/calculate-distance';

export default {
  props: ['newShop'],
  data () {
    return {
      couponList: [],
      unfoldCouponListLength: null,
      isUnfold: false,
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL,
      currentLocation: null
    }
  },
  components: {
    CouponItemPicked,
    CouponItemNotBegin,
    CouponItemUnclaimed,
    CouponItemNoRemaining,
    FormatBusinessTime
  },

  filters: {
    toKilometer(number) {
      if (number > 20) {
        return '>20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  },

  computed: {
    shopDistance() {
      const shopLocation = {
        latitude: this.newShop.shopInfo.latitude,
        longitude: this.newShop.shopInfo.longitude
      }
      const currentLocation = this.currentLocation;

      if (currentLocation) {
        return calculateDistance(shopLocation.latitude, shopLocation.longitude, currentLocation.latitude, currentLocation.longitude);
      }
    },
  },

  created() {
    this.shopCoupons();
    this.getLocation();
  },

  watch: {
    newShop(val, oldVal) {
      this.couponList = [];
      this.newShop.shopCoupons.forEach(coupon => {
        coupon.notBegin    = coupon.currentDate < coupon.useDateFrom ? true : false;
        coupon.percentage  = Math.round((coupon.sendUpperLimit - coupon.couponRemain) / coupon.sendUpperLimit * 100);
        coupon.isRemaining = coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0 ? true : false;
      });
      this.couponList = this.newShop.shopCoupons;
    }
  },

  methods: {
    shopCoupons() {
      this.newShop.shopCoupons.forEach(coupon => {
        coupon.notBegin    = coupon.currentDate < coupon.useDateFrom ? true : false;
        coupon.percentage  = Math.round((coupon.sendUpperLimit - coupon.couponRemain) / coupon.sendUpperLimit * 100);
        coupon.isRemaining = coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0 ? true : false;
      });
      if(this.newShop.shopCoupons.length > 2) {
        this.couponList.push(this.newShop.shopCoupons[0]);
        this.couponList.push(this.newShop.shopCoupons[1]);
        this.unfoldCouponListLength = this.newShop.shopCoupons.length - 2;
      }
      else {
        this.couponList = this.newShop.shopCoupons;
      }
    },
    unfoldCouponList() {
      this.couponList = [];

      if(this.isUnfold) {
        this.isUnfold = false;
        this.couponList.push(this.newShop.shopCoupons[0]);
        this.couponList.push(this.newShop.shopCoupons[1]);
        this.unfoldCouponListLength = this.newShop.shopCoupons.length - 2;
      }
      else {
        this.isUnfold = true;
        this.couponList = this.newShop.shopCoupons;
      }
    },

    pickNewShopCoupon(couponId) {
      const shopId = this.newShop.shopId;

      this.$emit('pickNewShopCoupon', { couponId, shopId })
    },

    getLocation() {
      wechatSdkService.getLocation().then(res => {
        const latitude = res.latitude;
        const longitude = res.longitude;
        this.currentLocation = { latitude, longitude };
      })
    }
  }
}
</script>
