<template>
    <div class="my-order-list" v-if="orderList">
        <div class="my-order-list__order-list">
          <template v-if="orderList.length > 0">
            <pull-down-refresh @refresh="fetchMyOrderList" :isRefreshing="isRefreshing">
              <order-item v-for="order of orderList" :key="order.orderId" :order="order" @refreshMyOrderList="fetchMyOrderList"></order-item>
            </pull-down-refresh>
          </template>
          <template v-else>
            <div class="my-order-list__order-list__no-data">
              <div class="my-order-list__order-list__no-data__icon"></div>
              <div class="my-order-list__order-list__no-data__text">暂无内容</div>
            </div>
          </template>
        </div>

      <goto-home></goto-home>
    </div>
</template>

<script>
import _ from 'lodash';
import api from '@/api';
import envService from '@/services/env';
import ecOrderInfoModuleOption from '@/store/ec-order-info/ec-order-info';
import GotoHome from '@/components/GotoHome';
import OrderItem from './MyOrderList/OrderItem';
import LoadingModal from '@/components/LoadingModal';
import PullDownRefresh from '@/components/PullDownRefresh';

export default {
  components: {
    GotoHome,
    OrderItem,
    PullDownRefresh
  },

  data () {
    return {
      orderList: null,
      isRefreshing: false
    }
  },

  created () {
    if (!this.$store.state['ecOrderInfo']) {
      this.$store.registerModule('ecOrderInfo', ecOrderInfoModuleOption);
    }
    this.$store.commit('ecOrderInfo/initState');
    this.fetchMyOrderList();
  },

  methods: {
    fetchMyOrderList() {
      this.isRefreshing = true;
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();
      api.ec.fetchMyOrderList({ regionBlockCode }).then(res => {
        this.orderList = res.data.data;
      }).finally(() => {
        LoadingModal.hide();
        this.isRefreshing = false;
      });
    }
  }
}
</script>

