@import '~style/variables.scss';

.my-order-list {
  min-height: 100vh;
  background-color: $background-color-gray;
  &__order-list {
    padding: 2.13vw 0;
    box-sizing: border-box;
    &__no-data {
      position: absolute;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      background-color: #ffffff;
      &__icon {
        margin: 26.6vw auto 0 auto;
