<template>
  <div class="business-time">
    <div class="business-time__content" v-if="formatBusinessTime === 0">
      <span class="business-time__content__time">6:00-22:00</span>
    </div>
    <div class="business-time__content" v-if="formatBusinessTime === 1">
      <span class="business-time__content__time">7:00-23:00</span>
    </div>
    <div class="business-time__content" v-if="formatBusinessTime === 2">
      <span class="business-time__content__time">24h</span>
    </div>
    <div class="business-time__content" v-if="formatBusinessTime === 3">
      <span class="business-time__content__time">24h</span>
      <span class="business-time__content__closed-time">周六、周日休业</span>
    </div>
  </div>
</template>
<script>
export default {
  props: ['businessTime'],

  data() {
    return {
      dateType: null
    }
  },

  computed: {
    formatBusinessTime() {
      const time = this.businessTime;

      if(time.indexOf('6:00') > -1) {
        this.dateType = 0;
      }
      else if(time.indexOf('7:00') > -1) {
        this.dateType = 1;
      }
      else if(time.indexOf('每日24小时') > -1) {
        this.dateType = 2;
      }
      else if(time.indexOf('平日24') > -1) {
        this.dateType = 3;
      }

      return this.dateType;
    }
  }
}
</script>
