<template>
  <div class="shop-item">
    <div class="shop-item__info">
      <div class="shop-item__basic-info">
        <div class="shop-item__shop-name">
          {{ shop.shopName }}
          <span class="shop-item__shop-name__24h-mark" v-if="shop.is24h">24h</span>
        </div>
        <div class="shop-item__shop-address">{{ shop.address }}</div>
      </div>

      <div class="shop-item__map-info" v-if="shop.distance">
        <div class="shop-item__distance">距您{{ shop.distance | toKilometer }}</div>
        <div class="shop-item__show-in-map" @click="openMap">地图 ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    shop: Object
  },
  methods: {
    openMap() {
      wx.openLocation({
          latitude: this.shop.latitude ,
          longitude: this.shop.longitude,
          name: this.shop.shopName,
          address: this.shop.address
      });
    }
  },

  filters: {
    toKilometer(number) {
      if (number > 20) {
        return '> 20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  }
};
</script>

