import axios from 'axios';
import signApiService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchUserInfo ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signApiService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/user/getUserInfo/`,
        {
          params: { timestamp, nonce, signature, regionBlockCode }
        }
      ).then(res => {
        const fetchUserInfoSuccess = res.data.code === 0 ? true : false;
        if (fetchUserInfoSuccess) {
          const userInfo = res.data.data;
          resolve(userInfo);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  fetchUserGroupInfo({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signApiService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(
        `${baseUrl}/app/v1/user/group`,
        {
          params: { timestamp, nonce, signature, regionBlockCode }
        }
      ).then(res => {
        const fetchUserGroupInfoSuccess = res.data.code === 0 ? true : false;
        if (fetchUserGroupInfoSuccess) {
          const userGroupInfo = res.data.data;
          resolve(userGroupInfo);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
};
