<template>
  <div class="user-coupon-detail" v-if="couponDetails !== null">
    <coupon-type :couponDetails="couponDetails"></coupon-type>
    <div class="user-coupon-detail__timeline">
      <div class="user-coupon-detail__timeline__title">
        <img :src="ossImageUrl + '/coupon/icon-time.png'" class="user-coupon-detail__timeline__title__image">
        <span class="user-coupon-detail__timeline__title__text">使用时间</span>
      </div>
      <div class="user-coupon-detail__timeline__bar">
        <timeline-bar :couponDetails="couponDetails"></timeline-bar>
      </div>
    </div>
    <available-shop :couponDetails="couponDetails"></available-shop>

    <available-commodity :couponDetails="couponDetails"></available-commodity>

    <coupon-instruction :couponExplain="couponDetails.couponExplain"></coupon-instruction>

    <use-condition
      v-if="couponDetails.applicationScope1 !== '' && couponDetails.applicationScope1"
      :applicationScope1="couponDetails.applicationScope1">
    </use-condition>

    <other-condition
      v-if="couponDetails.applicationScope2 !== '' && couponDetails.applicationScope2"
      :applicationScope2="couponDetails.applicationScope2">
    </other-condition>
    <goto-home></goto-home>
  </div>
</template>
<script>
import CouponType         from './UserCouponDetail/CouponType';
import TimelineBar        from './UserCouponDetail/TimelineBar';
import UseCondition       from './UserCouponDetail/UseCondition';
import OtherCondition     from './UserCouponDetail/OtherCondition';
import AvailableShop      from './UserCouponDetail/AvailableShop';
import CouponInstruction  from './UserCouponDetail/CouponInstruction';
import AvailableCommodity from './UserCouponDetail/AvailableCommodity';
import GotoHome           from '@/components/GotoHome';
import LoadingModal       from '@/components/LoadingModal';

import api from '@/api';
import envService from '@/services/env';

export default {
  data() {
    return {
      couponDetails: null,
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL
    }
  },

  components: {
    CouponType,
    TimelineBar,
    UseCondition,
    OtherCondition,
    AvailableShop,
    CouponInstruction,
    AvailableCommodity,
    GotoHome
  },

  created() {
    this.fetchUserCouponDetails();
  },

  methods: {
    fetchUserCouponDetails() {
      LoadingModal.show();

      const userCouponId = this.$route.params.userCouponId;
      const couponId = this.$route.params.couponId;
      const regionBlockCode = envService.getRegionBlockCode();

      api.coupon.fetchUserCouponDetail({ couponId, userCouponId, regionBlockCode }).then(userCouponDetail => {
        this.couponDetails = userCouponDetail;
      }).finally(() => LoadingModal.hide());
    }
  }
}
</script>
