<template>
  <div class="available-commodity">
    <div class="available-commodity__title">
      <img :src="ossImageUrl + '/coupon/icon-available-commodity.png'" class="available-commodity__title__image">
      <span class="available-commodity__title__text">适用商品</span>
    </div>
    <div class="available-commodity__content">
      <div v-if="couponType === 'totalPriceMinusSomeCoupon'">适用所有商品</div>
      <div v-else-if="couponType === 'productPriceMinusToFreeCoupon' || couponType === 'productPriceMinusToSomeCoupon' || couponType === 'productPriceMinusSomeCoupon'">{{ couponDetails.commodityList[0].commodityName }}</div>
    </div>
  </div>
</template>
<script>
import couponTypeService from '@/services/coupon/coupon-type';

export default {
  props: ['couponDetails'],

  data () {
    return {
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL
    }
  },

  computed: {
    couponType() {
      return couponTypeService.getType(this.couponDetails);
    },
  }

}
</script>
