<template>
  <div class="come-to-shop-mission">
    <div class="come-to-shop-mission__corner-circle"></div>
    <div class="come-to-shop-mission__corner-icon"></div>
    <div class="come-to-shop-mission__before-mission-complete-hint" v-if="mission.receiveFlg === 1">已成功领取奖励 {{ pickedType }} <span>{{ pickedCount }}</span></div>
    <div class="come-to-shop-mission__before-mission-complete-hint" v-else>还差到店 <span>{{ beforeMissionCompleteComeToShopCount }}</span> 次即可领取奖励，加油~</div>
    <div class="come-to-shop-mission__requirement-hint">本周到店达到{{ mission.visitsNumber }}次即可获得奖励</div>
    <div class="come-to-shop-mission__progress-bar">
      <mission-progress-bar-discrete :total="mission.visitsNumber" :current="mission.userComeShopTimes"></mission-progress-bar-discrete>
    </div>
  </div>
</template>

<script>
import MissionProgressBarDiscrete from './ComeToShopMission/MissionProgressBarDiscrete';
import missionService from '@/services/mission';

export default {
  components: { MissionProgressBarDiscrete },
  props: {
    mission: Object
  },
  computed: {
    beforeMissionCompleteComeToShopCount() {
      return this.mission.visitsNumber - this.mission.userComeShopTimes;
    },
    pickedType() {
      const pickedRewardType = missionService.getMissionRewardType(this.mission);
      if (pickedRewardType === 'point') {
        return '积分';
      } else if (pickedRewardType === 'coupon') {
        return '礼券';
      } else if (pickedRewardType === 'lotteryCoin') {
        return '抽奖币';
      }
    },
    pickedCount() {
      const pickedRewardType = missionService.getMissionRewardType(this.mission);
      if (pickedRewardType === 'point') {
        return `+${this.mission.lotteryPoint}`;
      } else if (pickedRewardType === 'coupon') {
        return '+1';
      } else if (pickedRewardType === 'lotteryCoin') {
        return `+${this.mission.lotteryTicketNum}`;
      }
    }
  }
}
</script>

