<template>
  <div class="purchase-amount-mission">
    <div class="purchase-amount-mission__corner-circle"></div>
    <div class="purchase-amount-mission__corner-icon"></div>
    <div class="purchase-amount-mission__before-mission-complete-hint" v-if="mission.receiveFlg === 1">已成功领取奖励 {{ pickedType }} <span>{{ pickedCount }}</span></div>
    <div class="purchase-amount-mission__before-mission-complete-hint" v-else>还差消费 <span>{{ beforeMissionCompletePurchaseAmount }}</span> 元即可领取奖励，加油~</div>
    <div class="purchase-amount-mission__requirement-hint">本周消费{{ mission.visitsConsumptionAmount }}元即可获得奖励</div>
    <div class="purchase-amount-mission__progress-bar">
      <mission-progress-bar :total="mission.visitsConsumptionAmount" :current="mission.userPurchaseAmount"></mission-progress-bar>
    </div>
  </div>
</template>

<script>
import MissionProgressBar from './PurchaseAmountMission/MissionProgressBar';
import missionService from '@/services/mission';

export default {
  components: { MissionProgressBar },
  props: {
    mission: Object
  },
  computed: {
    beforeMissionCompletePurchaseAmount() {
      return this.mission.visitsConsumptionAmount - this.mission.userPurchaseAmount;
    },
    pickedType() {
      const pickedRewardType = missionService.getMissionRewardType(this.mission);
      if (pickedRewardType === 'point') {
        return '积分';
      } else if (pickedRewardType === 'coupon') {
        return '礼券';
      } else if (pickedRewardType === 'lotteryCoin') {
        return '抽奖币';
      }
    },
    pickedCount() {
      const pickedRewardType = missionService.getMissionRewardType(this.mission);
      if (pickedRewardType === 'point') {
        return `+${this.mission.lotteryPoint}`;
      } else if (pickedRewardType === 'coupon') {
        return '+1';
      } else if (pickedRewardType === 'lotteryCoin') {
        return `+${this.mission.lotteryTicketNum}`;
      }
    }
  }

}
</script>

