import signApiService from '@/services/sign-api';
import axios from 'axios';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchMissions({ regionBlockCode }) {
    return new Promise((resolve, reject) => {
      axios.get(`${baseUrl}/app/v1/mission/`, {
        params: {
          regionBlockCode,
          ...signApiService.generateMainApiSignature()
        },
      }).then(res => {
        const fetchMissionsSuccess = res.data.code === 0 ? true : false;
        if (fetchMissionsSuccess) {
          const missions = res.data.data.list;
          resolve(missions);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  pickReward({ missionHistoryId }) {
    return new Promise((resolve, reject) => {
      axios.post(`${baseUrl}/app/v1/mission/receive`, {
        missionHistoryId,
        ...signApiService.generateMainApiSignature()
      }).then(res => {
        const pickRewardSuccess = res.data.code === 0 ? true : false;
        if (pickRewardSuccess) {
          const reward = res.data.data;
          resolve(reward);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
}
