import Vue from 'vue';

const isShopLocationDataError = (shop) => typeof shop.latitude !== 'number' || typeof shop.longitude !== 'number';
const isShopNotInBusiness = (shop) => shop.businessStatus !== 0;
const isShopDeleted = (shop) => shop.delFlg !== 0;

const isShopInvalid = (shop) => isShopLocationDataError(shop) || isShopNotInBusiness(shop) || isShopDeleted(shop);

export default {
  namespaced: true,
  state: {
    shops: {}
  },
  getters: {
    shopsForRegion: (state) => (regionBlockCode) => {
      // 按一定规则过滤了一些不应显示的店铺，并非后端返回的全部店铺
      return state.shops[regionBlockCode].filter(shop => !isShopInvalid(shop));
    },
    shopsAvailableForRegion: (state) => (regionBlockCode) => {
      return state.shops[regionBlockCode] !== undefined;
    }
  },
  mutations: {
    updateShopsForRegion(state, { shops, regionBlockCode }) {
      Vue.set(state.shops, regionBlockCode, shops);
    }
  }
};
