<template>
  <div class="capsule" :class="capsule.color" :style="transforms">
    <img class="capsule__logo" :src="capsule.logo" v-if="capsule.logo">
  </div>
</template>

<script>
export default {
  props: {
    capsule: Object,
    x: Number,
    y: Number,
    r: Number
  },
  computed: {
    transforms() {
      return {
        left: this.x * 100 + '%',
        top: this.y * 100 + '%',
        transform: `rotate(${this.r}rad)`
      };
    }
  }
}
</script>

<style lang="scss" scoped>
.capsule {
  position: absolute;
  width: 15vw;
  height: 15vw;
  background-size: contain;
  background-repeat: no-repeat;
  &.red {
    background-image: url($oss-images + '/lottery/capsule__red.png');
  }
  &.blue {
    background-image: url($oss-images + '/lottery/capsule__blue.png');
  }
  &.yellow {
    background-image: url($oss-images + '/lottery/capsule__yellow.png');
  }
