import user                 from './user';
import ec                   from './ec';
import wechat               from './wechat';
import campaign             from './campaign';
import address              from './address';
import coupon               from './coupon';
import newShops             from './new-shops';
import lottery              from './lottery';
import mission              from './mission';
import cityList             from './cityList';
import popularStatistics    from './popular-statistics';
import shop                 from './shop';
import talkTopic            from './talk-topic';
import recommendedCommodity from './recommended-commodity';

import axios from 'axios';
import envService from '@/services/env';

axios.defaults.timeout = 4000;

axios.interceptors.request.use(config => {
  const token = envService.getToken();
  config.headers['Api-Access-Token'] = token ? token : '';
  return config;
});

// api出现前端超时时，跳转回小程序'网络不稳定'页面
axios.interceptors.response.use(res => {
  return res;
}, err => {
  const isTimeout = (/timeout of \d+ms exceeded/).test(err.message) ? true : false;
  if (isTimeout) {
    const url = window.location.hash.slice(1);
    const options = { url: window.location.hash.slice(1) };
    const route = 'pages/webview-entry/webview-entry';
    const optionsStr = encodeURIComponent(JSON.stringify(options));
    const routeStr = encodeURIComponent(route);
    if (wx) {
      wx.miniProgram.redirectTo({
        url:`/pages/error-page_network-unstable/network-unstable?refreshPageOptions=${optionsStr}&refreshPageRoute=${routeStr}`
      });
    }
  }
  return Promise.reject(err);
})

export default {
  user,
  wechat,
  campaign,
  address,
  ec,
  coupon,
  mission,
  newShops,
  lottery,
  cityList,
  popularStatistics,
  shop,
  talkTopic,
  recommendedCommodity
};

