<template>
  <div class="edit-address">
    <div class="edit-address__contact-content">
      <span class="edit-address__contact">联系人</span><input class="edit-address__contact-name" v-model.trim="name">
    </div>
    <div class="edit-address__gender-info">
      <span :class="isSelectMale?'edit-address__gender-mr tagSelect':'edit-address__gender-mr'" @click="selectMale">先生</span>
      <span :class="isSelectFemale?'edit-address__gender-miss tagSelect':'edit-address__gender-miss'"  @click="selectFemale">女士</span>
    </div>

    <div class="edit-address__phone-content">
      <span class="edit-address__phone">手机号</span>
      <input class="edit-address__phone-number"
            @input="handlePhoneNumberInputChange"
            placeholder="请输入你的手机号"
            v-model="phoneNumber" type="number">
    </div>
    <div class="edit-address__phone-number-suggestion" v-if="showPhoneNumberSuggestion && suggestedPhoneNumbers !== null">
      <div class="edit-address__phone-number-suggestion__item" v-for="(item, index) in suggestedPhoneNumbers" :key="index"  @click="handleSuggestedPhoneNumberTap(item)">{{ item }}</div>
    </div>


    <div class="edit-address__address-content" @click="chooseAddress">
      <span class="edit-address__address">地址</span><input class="edit-address__address-description" v-model.trim="addressDetailName" onfocus="this.blur()">
      <span class="edit-address__address-description__icon"></span>
    </div>

    <div class="edit-address__address-detail-content">
      <span class="edit-address__address-detail">门牌号</span>
      <div class="edit-address__address-detail-show" contenteditable="true">{{ addressExtraInfo }}</div>
      <textarea class="edit-address__address-detail-description" v-model.trim="addressExtraInfo"></textarea>
    </div>

    <div class="edit-address__tag-info">
      <span class="edit-address__tag">标签</span>
      <span :class="isSelectHome?'edit-address__tag-home tagSelect':'edit-address__tag-home'" @click="selectHome">家</span>
      <span :class="isSelectCompany?'edit-address__tag-company tagSelect':'edit-address__tag-company'" @click="selectCompany">公司</span>
      <span :class="isSelectSchool?'edit-address__tag-school tagSelect':'edit-address__tag-school'" @click="selectSchool">学校</span>
    </div>

    <div class="edit-address__save-address" @click="saveAddress">确定</div>
  </div>
</template>

<script>
import _            from 'lodash';
import Toast        from '@/components/Toast';
import qqLbsService from '@/services/qq-lbs';
import api          from '@/api/index';
import envService   from '@/services/env';
import CommittingModal from '@/components/CommittingModal';

export default {
  data() {
    return {
      name: null,
      gender: null,
      phoneNumber: null,
      addressName: null,
      addressDetailName: null,
      addressExtraInfo: null,
      addressId: null,
      tags: [],

      editAddress: null,

      isSelectMale: false,
      isSelectFemale: false,

      isSelectHome: false,
      isSelectCompany: false,
      isSelectSchool: false,

      isLocationPickerVisible: false,

      locationInfo: null,

      showPhoneNumberSuggestion: false,
      userAllPhoneNumbers: null,
      suggestedPhoneNumbers: null
    }
  },
  created() {
    this.setPhoneNumberSuggestion();

    this.editAddress = this.$store.state.campaignExchangeAddress.editAddressInfo;

    this.name              = this.editAddress.name;
    this.gender            = this.editAddress.gender;
    this.phoneNumber       = this.editAddress.phoneNumber;
    this.addressName       = this.editAddress.addressName;
    this.addressDetailName = this.editAddress.addressDetailName;
    this.addressExtraInfo  = this.editAddress.addressExtraInfo;
    this.addressId         = this.editAddress.addressId;

    const latitude    = this.editAddress.latitude;
    const longitude   = this.editAddress.longitude;
    this.locationInfo = { latitude, longitude };

    const hasLocationInfo = (this.$store.state.location.locationInfo !== null) ? true : false;

    // 小程序原生地图选择地址后, 当addressName字段为空字符串时, 显示为addressDetailName字段
    if (hasLocationInfo) {
      this.addressName = this.$store.state.location.locationInfo.addressName;
      this.addressDetailName = this.$store.state.location.locationInfo.addressDetailName;

      const latitude    = this.$store.state.location.locationInfo.latitude;
      const longitude   = this.$store.state.location.locationInfo.longitude;
      this.locationInfo = { latitude, longitude };

      this.$store.commit('location/cleanupLocationInfo'); // 清除小程序选择地图后的地址信息
    }

    var isMale   = (this.editAddress.gender === 'MALE') ? true : false;
    var isFemale = (this.editAddress.gender === 'FEMALE') ? true : false;
    if (isMale) {
      this.isSelectMale = true;
      this.gender = 'MALE';
    } else if (isFemale) {
      this.isSelectFemale = true;
      this.gender = 'FEMALE';
    }

    const hasTags = this.editAddress.tags ? true : false;
    if (hasTags) {
      var isTagHome    = (this.editAddress.tags[0] === '家') ? true : false;
      var isTagCompany = (this.editAddress.tags[0] === '公司') ? true : false;
      var isTagSchool  = (this.editAddress.tags[0] === '学校') ? true : false;
      if (isTagHome) {
        this.isSelectHome = true;
        this.tags = ['家'];
      } else if (isTagCompany) {
        this.isSelectCompany = true;
        this.tags = ['公司'];
      } else if (isTagSchool) {
        this.isSelectSchool = true;
        this.tags = ['学校'];
      }
    }
  },
  methods: {
    filterPhoneNumberForSuggestion (value, phoneNumbers) {
      if (value.length === 0) {
        return phoneNumbers.slice();
      } else {
        return phoneNumbers.filter(phoneNumber => phoneNumber.slice(0, value.length) === value);
      }
    },
    setPhoneNumberSuggestion() {
      const regionBlockCode = envService.getRegionBlockCode();

      api.address.fetch({ regionBlockCode }).then(res => {
        const isFetchAddressInfoSuccess = res.data.code === 0 ? true : false;
        const hasAddressInfo = res.data.data.length > 0 ? true : false;
        const addresses = res.data.data;
        const userAllPhoneNumbers = _.uniq(addresses.map(address => address.phoneNumber));

        if (isFetchAddressInfoSuccess) {
          if (hasAddressInfo) {
            this.userAllPhoneNumbers = userAllPhoneNumbers;
            this.suggestedPhoneNumbers = userAllPhoneNumbers;
          }
        }
      })
    },
    handlePhoneNumberInputChange() {
      const phoneNumber = this.phoneNumber;
      if (phoneNumber.length > 11) {
        this.phoneNumber = phoneNumber.slice(0,11);
      }

      this.showPhoneNumberSuggestion = true;
      this.suggestedPhoneNumbers = this.filterPhoneNumberForSuggestion(phoneNumber, this.userAllPhoneNumbers);
    },
    handleSuggestedPhoneNumberTap(item) {
      this.phoneNumber = item;
      this.showPhoneNumberSuggestion = false;
    },
    chooseAddress() {
      const name              = this.name;
      const gender            = this.gender;
      const phoneNumber       = this.phoneNumber;
      const addressName       = this.addressName;
      const addressDetailName = this.addressDetailName;
      const addressId         = this.addressId
      const addressExtraInfo  = this.addressExtraInfo;
      const tags              = this.editAddress.tags;

      const editAddressInfoForChosen = {
        name,
        gender,
        phoneNumber,
        addressName,
        addressDetailName,
        addressExtraInfo,
        addressId,
        tags,
        latitude: this.editAddress.location,
        longitude: this.editAddress.location
      };

      this.$store.commit('campaignExchangeAddress/setEditAddressInfo', editAddressInfoForChosen);

      this.$router.push({ path: '/choose-location' });
    },
    saveAddress() {
      var isnameValid                = (this.name !== '') ? false : true;
      var isGenderValid              = (this.gender !== '') ? false : true;
      var isContactPhoneNumberValid  = (this.phoneNumber != '') ? false: true;
      var isAddressNameValid         = (this.addressName !== '') ? false : true;
      var isAddressExtraInfoValid    = (this.addressExtraInfo !== '') ? false : true;
      var isPhoneNumberValid         = this.checkPhoneValid(this.phoneNumber) ?false : true;

      if (isnameValid) {
        Toast.show({ msg:'请输入你的姓名' });
      } else if (isGenderValid) {
        Toast.show({ msg:'请选择性别' });
      } else if (isContactPhoneNumberValid) {
        Toast.show({ msg:'请输入你的手机号' });
      } else if (isPhoneNumberValid) {
        Toast.show({ msg:'请输入有效的手机号' });
      } else if (isAddressNameValid) {
        Toast.show({ msg:'请选择地址' });
      } else if (isAddressExtraInfoValid) {
        Toast.show({ msg:'请输入门牌号' });
      } else {
        CommittingModal.show();
        const regionBlockCode = envService.getRegionBlockCode();

        const addressInfo = {
          regionBlockCode,
          addressId: this.editAddress.addressId,
          name: this.name,
          gender: this.gender,
          phoneNumber: this.phoneNumber,
          addressName: this.addressName,
          addressDetailName: this.addressDetailName,
          addressExtraInfo: this.addressExtraInfo,
          latitude: this.locationInfo.latitude,
          longitude: this.locationInfo.longitude,
          tags: this.tags
        }

        api.address.edit({ addressInfo, regionBlockCode }).then(res => {
          CommittingModal.hide();
          const isFetchAddressInfoSuccess = res.data.code === 0 ? true : false;
          if (isFetchAddressInfoSuccess) {
            this.$router.go(-1);
            this.$store.commit('campaignExchangeAddress/cleanupEditAddressInfo');
            this.$store.commit('campaignExchangeAddress/setNeedShowEditAddressSuccessToast', true);
          } else {
            CommittingModal.hide();
            Toast.show({ msg: '修改地址信息失败, 请稍后再试' });
          }
        }).catch(err => {
          CommittingModal.hide();
          Toast.show({ msg: '修改地址信息失败, 请稍后再试' });
        })
      }
    },
    checkPhoneValid(value) {
      if((/^1\d{10}$/.test(value))){
        return true;
      } else {
        return false;
      }
    },
    selectMale() {
      this.isSelectMale = true;
      this.isSelectFemale = false;
      this.gender = 'MALE';
    },
    selectFemale() {
      this.isSelectFemale = true;
      this.isSelectMale = false;
      this.gender = 'FEMALE';
    },
    selectHome() {
      if (this.isSelectHome) {
        this.isSelectHome = false;
        this.tags = [];
      } else {
        this.isSelectHome = true;
        this.tags = ['家'];
      }
      this.isSelectCompany = false;
      this.isSelectSchool = false;
      this.editAddress.tags = this.tags;
    },
    selectCompany() {
      if (this.isSelectCompany) {
        this.isSelectCompany = false;
        this.tags = [];
      } else {
        this.isSelectCompany = true;
        this.tags = ['公司'];
      }
      this.isSelectHome = false;
      this.isSelectSchool = false;
      this.editAddress.tags = this.tags;
    },
    selectSchool() {
      if (this.isSelectSchool) {
        this.isSelectSchool = false;
        this.tags = [];
      } else {
        this.isSelectSchool = true;
        this.tags = ['学校'];
      }
      this.isSelectHome = false;
      this.isSelectCompany = false;
      this.editAddress.tags = this.tags;
    }
  },
};
</script>

