<template>
  <div class="ec-product-list">
    <pull-down-refresh @refresh="fetchEcProductList" :isRefreshing="isRefreshing">
      <div class="ec-product-list__bg">
        <div class="ec-product-list__select-city">
          <div class="ec-product-list__select-city__button" v-if="cityList && currentCity">
            <div class="ec-product-list__select-city__button__current-city">{{ currentCity.cityName }}</div>
            <select v-model="currentCity">
              <option :value ="city" v-for="city of cityList" :key="city.cityId">{{ city.cityName }}</option>
            </select>
          </div>
        </div>

        <div class="ec-product-list__tab-list" v-if="ecProductTabs.length > 0">
          <div
            class="ec-product-list__tab-list__item"
            :class="currentTab === tab ? 'active' : ''"
            v-for="(tab, index) of ecProductTabs"
            :key="index"
            @click="setCurrentTab(tab)">
            {{ tab }}
          </div>
        </div>

        <div class="ec-product-list__list" v-if="filterEcProductList">
          <template v-if="filterEcProductList.length > 0">
            <product-item v-for="item of filterEcProductList" :key="item.commodityId" :product="item" @click.native="gotoEcProductDetail(item.commodityId)"></product-item>
          </template>
          <template v-else>
            <div class="ec-product-list__list__no-data">
              <div class="ec-product-list__list__no-data__icon"></div>
              <div class="ec-product-list__list__no-data__text">暂无内容</div>
            </div>
          </template>
        </div>
      </div>
    </pull-down-refresh>

    <goto-home></goto-home>
    <div class="ec-product-list__my-order-list-button" @click="gotoMyOrderList"></div>
  </div>
</template>

<script>
import api from '@/api';
import envService from '@/services/env';
import GotoHome from '@/components/GotoHome';
import ProductItem from './EcProductList/ProductItem';
import LoadingModal from '@/components/LoadingModal';
import PullDownRefresh from '@/components/PullDownRefresh';

export default {
  components: {
    GotoHome,
    ProductItem,
    PullDownRefresh
  },

  data () {
    return {
      ecProductTabs: [],
      currentTab: null,
      ecProductList: null,
      cityList: null,
      currentCity: null,
      isRefreshing: false
    }
  },

  async created () {
    await this.fetchCityList();
    this.fetchEcProductList();
  },

  computed: {
    filterEcProductList() {
      if (this.currentTab) {
        return this.ecProductList.filter(item => item.tagName === this.currentTab);
      } else {
        return this.ecProductList;
      }
    }
  },

  methods: {
    initCurrentTab() {
      if (this.ecProductTabs.length > 0) {
        this.currentTab = this.ecProductTabs[0];
      }
    },

    fetchEcProductList() {
      this.isRefreshing = true;
      LoadingModal.show();
      const regionBlockCode = envService.getRegionBlockCode();
      const cityId = this.currentCity.cityId;
      api.ec.fetchEcProductShopList({ regionBlockCode, cityId }).then(res => {
        if (res.data.code === 200) {
          this.ecProductList = res.data.data;
          // this.ecProductList.expired 0-未开始；1-已开始；2-已结束
          // this.ecProductList.remainedQty库存数量
          const ecProductTabs = this.ecProductList.reduce((accumulator, currentValue) => {
              if (accumulator.includes(currentValue.tagName) === false && currentValue.tagName !== undefined) {
                  return [...accumulator, currentValue.tagName];
              } else {
                  return accumulator;
              }
          }, []);

          this.ecProductTabs = ecProductTabs;
          this.initCurrentTab();
        }
      }).finally(() => {
        LoadingModal.hide();
        this.isRefreshing = false;
      });
    },

    fetchCityList() {
      return new Promise((resolve, reject) => {
        LoadingModal.show();
        const regionBlockCode = envService.getRegionBlockCode();
        api.cityList.fetchCityList({ regionBlockCode }).then(res => {
          this.cityList = res.data;
          this.currentCity = this.cityList[0];
          resolve();
        }).finally(() => LoadingModal.hide());
      })
    },

    setCurrentTab(tab) {
      this.currentTab = tab;
    },

    gotoEcProductDetail(commodityId) {
      this.$router.push({ path: `/ec-product-detail/${commodityId}` });
    },

    gotoMyOrderList() {
      this.$router.push({ path: '/my-order-list' });
    }
  },

  watch: {
    currentCity(val, oldVal) {
      if (oldVal) {
        this.fetchEcProductList();
      }
    }
  }
}
</script>

