import axios from 'axios';
import signatureService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  recordPopularStatistics ({ type, key }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.post(
        `${baseUrl}/app/v1/mina/look`,
        {
          timestamp, nonce, signature, type, key
        }
      ).then(res => {
        const recordPopularStatisticsSuccess = res.data.code === 0 ? true : false;
        if (recordPopularStatisticsSuccess) {
          const popularStatistics = res.data.data;
          resolve(popularStatistics);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
};
