export default {
  getType(coupon) {
    if (coupon.type === 1) {
      return 'productPriceMinusToFreeCoupon'; // 商品免费券
    } else if (coupon.type === 2) {
      return 'totalPriceMinusSomeCoupon'; // 金额满减券
    } else if (coupon.type === 3) {
      return 'productPriceMinusSomeCoupon'; // 商品立减券
    } else if (coupon.type === 0) {
      return 'productPriceMinusToSomeCoupon'; // 商品折扣券
    }
  }
};