<template>
  <div>
    <div class="express-address" @click="goToCampaignSendAddress" v-if="isSendAddressShow">
      <div class="express-address__name">寄送地址({{sendAddressNum}})</div>
      <div class="express-address__set-address">
        <span v-if="notSendAddressNum">有{{notSendAddressNum}}未设置地址</span>
        <span class="express-address__set-address__right-arrow"></span>
      </div>
    </div>
    <div class="express-address" @click="goToCampaignPostExchanged" v-else-if="myExchangedQty">
      <div class="express-address__name">已兑换：{{myExchangedQty}}</div>
      <div class="express-address__set-address">
        <span class="express-address__set-address__right-arrow"></span>
      </div>
    </div>
    <div class="express-exchanged-and-address" v-else-if="isShowExchangedAndExchangedAdress">
      <div class="express-exchanged-and-address__warpper" @click="goToCampaignSendAddress">
        <div class="express-address__name">寄送地址({{sendAddressNum}})</div>
        <div class="express-address__set-address">
          <span v-if="true">有{{notSendAddressNum}}未设置地址</span>
          <span class="express-address__set-address__right-arrow"></span>
        </div>
      </div>
      <div class="express-exchanged-and-address__warpper"  @click="goToCampaignPostExchanged">
        <div class="express-exchanged-and-address__name">已兑换：{{myExchangedQty}}</div>
        <div class="express-exchanged-and-address__set-address">
          <span class="express-exchanged-and-address__set-address__right-arrow"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import _ from "lodash";
import envService from "@/services/env";

export default {
  props: [
    "userStampList",
    "myExchangedQty",
  ],
  computed: {
    notSendAddressNum: function() {
      const userStampList = this.userStampList;
      return userStampList.filter(
        item => item.saveAddressFlg === 2 && item.giftType === 2
      ).length;
    },
    sendAddressNum: function() {
      const userStampList = this.userStampList;
      return userStampList.filter(item => item.giftType === 2).length;
    },
    isSendAddressShow() {
      const userStampList = this.userStampList;
      return userStampList.filter(
        item => item.status === 2 && item.giftType === 2
      ).length;
    },
    isShowExchangedAndExchangedAdress() {
      const isCoupon = _.find(this.userStampList, { giftType: 1 });
      const isGift = _.find(this.userStampList, { giftType: 2 });
      return isCoupon && isGift;
    }
  },
  methods: {
    goToCampaignSendAddress() {
      const userInfo = envService.getUserInfo();
      const campaignId = this.$route.params.campaignId;
      if (userInfo) {
        this.$router.push({ path: `/campaign-send-address/${campaignId}` });
      } else {
        wx.miniProgram.reLaunch({
          url: `/pages/index/index?pageKey=campaignDetail&commodityId=${campaignId}&sourceType=internal`
        });
      }
    },
    goToCampaignPostExchanged() {
      const userInfo = envService.getUserInfo();
      const campaignId = this.$route.params.campaignId;
      if (userInfo) {
        const userStampList = this.userStampList;
        this.$router.push({ name: 'campaign-post-exchanged', params: {userStampList} });
      } else {
        wx.miniProgram.reLaunch({
          url: `/pages/index/index?pageKey=campaignDetail&commodityId=${campaignId}&sourceType=internal`
        });
      }
    }
  }
};
</script>

