<template>
  <div class="product-item">
    <div class="product-item__image">
      <img :src="product.imageSmall">
    </div>
    <div class="product-item__content">
      <div class="product-item__content__top">
        <div class="product-item__content__top__status">
          <template v-if="product.remainedQty === 0 && product.expired === 1">
            <div class="product-item__content__top__status-sold-out">
              <div class="product-item__content__top__status-sold-out__container">
                <span>抢光了</span>
              </div>
            </div>
          </template>

          <template v-else>
            <span class="product-item__content__top__status-not-begin" v-if="product.expired === 0">
              <template v-if="isThisWeek(product.canOrderStart)">本{{ product.canOrderStart | formatThisWeekDate }}开始</template>
              <template v-else>{{ product.canOrderStart | formatNotThisWeekDate }}开始</template>
            </span>
            <span class="product-item__content__top__status-begin" v-else-if="product.expired === 1">进行中</span>
            <div class="product-item__content__top__status-end" v-else-if="product.expired === 2">
              <div class="product-item__content__top__status-end__container">
                <span>预约结束</span>
              </div>
            </div>
          </template>
        </div>
        <div class="product-item__content__top__title">{{ product.commodityName }}</div>
      </div>

      <div class="product-item__content__bottom">
        <div class="product-item__content__bottom-time">
          <div class="product-item__content__bottom-time__icon-time"></div>
          <div class="product-item__content__bottom-time__start-time">{{ product.canOrderStart | formatDate }}开始</div>
        </div>
        <div class="product-item__content__bottom-info">
          <div class="product-item__content__bottom-info__price"><span>￥</span>{{ product.sellPrice | toRMB }}</div>
          <div class="product-item__content__bottom-info__watch-number">{{ product.onlookerCount | formatOnlookerCount }}人围观过</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['product'],

  methods: {
    isThisWeek(canOrderStart) {
      const now = new Date();
      const nowDayOfWeek = (now.getDay() === 0) ? 7 : now.getDay();
      const weekEndDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + (7 - nowDayOfWeek));
      const weekStartDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1 - nowDayOfWeek);
      return canOrderStart > weekStartDate.getTime() && canOrderStart < weekEndDate.getTime() ;
    },
  },

  filters: {
    toRMB(price) {
      return price.toFixed(1);
    },

    formatDate(timestamp) {
      const date = new Date(timestamp);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const day = ('0' + date.getDate()).slice(-2);
      return `${month}月${day}日`;
    },

    formatThisWeekDate(timestamp) {
      const weekday = ['周日','周一','周二','周三','周四','周五','周六'];
      const date = new Date(timestamp);
      const hours = ('0' + date.getHours()).slice(-2);
      const minutes = ('0' + date.getMinutes()).slice(-2);
      const weekdayLength = date.getDay();
      return `${weekday[weekdayLength]} ${hours}:${minutes}`;
    },

    formatNotThisWeekDate(timestamp) {
      const date = new Date(timestamp);
      const month = ('0' + (date.getMonth() + 1)).slice(-2);
      const day = ('0' + date.getDate()).slice(-2);
      const hours = ('0' + date.getHours()).slice(-2);
      const minutes = ('0' + date.getMinutes()).slice(-2);
      return `${month}月${day}日 ${hours}:${minutes}`;
    },

    formatOnlookerCount(onlookerCount) {
      if (onlookerCount >= 10000) {
        if (onlookerCount % 10000 >= 1000) {
          return (onlookerCount / 10000).toFixed(1) + '万';
        } else {
          return (onlookerCount / 10000).toFixed(0) + '万';
        }
      } else {
        return onlookerCount;
      }
    }
  }
}
</script>

