  <div class="coupon-type">
    <div v-if="couponType === 'productPriceMinusToFreeCoupon'">
      <div class="coupon-type__price">
        <img :src="ossImageUrl + '/coupon/coupon-detail-background-t.png'" class="coupon-type__price__image">
