            <div class="coupon-type__price__text__total-price">全额抵扣</div>
          </div>
          <div class="coupon-type__price__text__button">兑换券</div>
        </div>
      </div>
    </div>
    <div v-if="couponType === 'productPriceMinusSomeCoupon'">
      <div class="coupon-type__price">
        <img :src="ossImageUrl + '/coupon/coupon-detail-background-t.png'" class="coupon-type__price__image">
        <div class="coupon-type__price__text">
          <div class="coupon-type__price__text__price">
            <div class="coupon-type__price__text__discount-price">减<span class="coupon-type__price__text__discount-price__number">{{ couponDetails.discountPrice }}</span>
            </div>
            <div class="coupon-type__price__text__total-price">消费立减</div>
          </div>
          <div class="coupon-type__price__text__button">代金券</div>
        </div>
      </div>
    </div>
    <div v-if="couponType === 'totalPriceMinusSomeCoupon'">
      <div class="coupon-type__price">
        <img :src="ossImageUrl + '/coupon/coupon-detail-background-t.png'" class="coupon-type__price__image">
        <div class="coupon-type__price__text">
          <div class="coupon-type__price__text__price">
            <div class="coupon-type__price__text__discount-price">减<span class="coupon-type__price__text__discount-price__number">{{ couponDetails.discountPrice }}</span>
            </div>
            <div class="coupon-type__price__text__total-price">满<span class="coupon-type__price__text__total-price__number">{{ couponDetails.originalPrice }}</span>元可用
            </div>
          </div>
          <div class="coupon-type__price__text__button">代金券</div>
        </div>
      </div>
    </div>
    <div v-if="couponType === 'productPriceMinusToSomeCoupon'">
      <div class="coupon-type__price">
        <img :src="ossImageUrl + '/coupon/coupon-detail-background-t.png'" class="coupon-type__price__image">
        <div class="coupon-type__price__text">
          <div class="coupon-type__price__text__price">
            <div class="coupon-type__price__text__discount-price">减至<span class="coupon-type__price__text__discount-price__number">{{ couponDetails.discountPrice }}</span></div>
            <div class="coupon-type__price__text__total-price">消费立减</div>
          </div>
          <div class="coupon-type__price__text__button">代金券</div>
        </div>
      </div>
    </div>
    <div class="coupon-type__title">
      <div class="coupon-type__title__content">
        <span class="coupon-type__title__content__name">{{ couponDetails.couponName }}</span>
        <span class="coupon-type__title__content__will-expired-time" v-if="couponDetails.dynamicPeriodFlg === 0 && calculateCouponUseTime">{{ calculateCouponUseTime }}</span>
      </div>
      <div class="coupon-type__title__region">
        <img :src="ossImageUrl + '/coupon/icon-region.png'" class="coupon-type__title__region__image">
        <span class="coupon-type__title__region__name">限{{ reginonName }}使用</span>
      </div>
    </div>
  </div>
</template>
<script>
import envService from '@/services/env';
import reginonService from '@/services/region';
import couponTypeService from '@/services/coupon/coupon-type';

const fiveDays = 432000000; //提示过期和开始使用 时间的显示节点(5天)
const oneDay = 86400000; //提示即将过期和即可使用 时间的显示节点(1天)

export default {
  props: ['couponDetails'],

  data() {
    return {
      currentDate: Date.now(),
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL
    }
  },

  computed: {
    couponType() {
      return couponTypeService.getType(this.couponDetails);
    },

    reginonName() {
      const regionBlockCode = envService.getRegionBlockCode();
      return reginonService.getCurrentRegionName(regionBlockCode);
    },

    calculateCouponUseTime() {
      const useDateFrom = this.couponDetails.useDateFrom;
      const useDateTo =this.couponDetails.useDateTo;

      if(this.currentDate < useDateFrom) {
        const diffTime = useDateFrom - this.currentDate;
        const diffDate = Math.floor(diffTime / (24*3600*1000));

        if(diffTime <= fiveDays) {
          if(diffTime < oneDay) {
            return '即可使用';
          }
          else {
            return diffDate + '天后可用';
          }
        }
      }
      else {
        const diffTime = useDateTo - this.currentDate;
        const diffDate = Math.floor(diffTime / (24*3600*1000));

        if(diffTime <= fiveDays) {
          if(diffTime < oneDay) {
            return '即将过期';
          }
          else {
            return diffDate + '天后过期';
          }
        }
      }
    }
  }

}
</script>
