<template>
  <div class="campaign-detail"
    v-if="campaignDetail != null"
    :class="{'no-scroll' : isShowCampaignExchangeCouponSuccessModal === 'show' || isShowCampaignExchangeNotWinningModal === 'show' }">
    <div class="campaign-detail__banner-region">
      <img class="campaign-detail__banner-region__img" :src="campaignDetail.imagePathDetail">
      <div class="campaign-detail__banner-region__sub">
        <swiper :options="swiperOption" v-if="barrageList">
            <swiper-slide  v-for="(item, index) in barrageList" :key="index">
              <p v-if="index % 2" class="campaign-detail__banner-region__sub__odd">
              </p>
              <p v-else  class="campaign-detail__banner-region__sub__even">
                <img class="campaign-detail__banner-region__sub__profile" :src="item.userPhoto || defaultAvatar"/>
                <span class="campaign-detail__banner-region__sub__name">{{item.nickName}}</span>
                <span class="campaign-detail__banner-region__sub__tweets">刚刚获得了{{item.giftName}}</span>
              </p>
           </swiper-slide>
          </swiper>
      </div>
    </div>
    <div class="campaign-detail__title-region">
      <div class="campaign-detail__title-region__name-region">
        <p class="campaign-detail__title-region__name-region__name">{{campaignDetail.campaignName}}</p>

        <p v-if="isCampaignStart"></p>
        <p class="campaign-detail__title-region__name-region__end" v-else-if="isLessThanFivedays">
          <span>{{ campaignDetail.openEndDt | formatLessFiveDay }}</span>
        </p>
        <p class="campaign-detail__title-region__name-region__end" v-else-if="isThisWeed">
          <span>{{ campaignDetail.campaignDateFrom | formatThisWeekDate }}</span>
        </p>
        <p class="campaign-detail__title-region__name-region__end" v-else-if="isNotThisWeed">
          <span>{{ campaignDetail.campaignDateFrom | formatNotThisWeekDate }}</span>
        </p>
      </div>
      <div class="campaign-detail__title-region__description">
        <p>
          <span>已兑换:</span>
          <span>{{campaignDetail.totalExchangedQty}}</span>
        </p>
        <p>
          <span>库存:</span>
          <span>{{campaignDetail.giftNums}}</span>
        </p>
        <div v-if=" campaignDetail.onlookerCount > 10 && campaignDetail.onlookerCount < 10000" class="campaign-detail__title-region__description__profiles">
          <div>
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
          </div>
          <div class="campaign-detail__title-region__description__concerns">{{ campaignDetail.onlookerCount }}人围观过</div>
        </div>
        <div v-else-if=" campaignDetail.onlookerCount >= 10000 " class="campaign-detail__title-region__description__profiles">
          <div>
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
            <img src="//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/2.png">
          </div>
          <div class="campaign-detail__title-region__description__concerns">{{ campaignDetail.onlookerCount | formatOnLookCount }}万人围观过</div>
        </div>
      </div>
    </div>
    <div class="campaign-detail__campaign-region">
      <div class="campaign-detail__campaign-region__title">
        <div class="campaign-detail__campaign-region__title__impress">集点印章 {{currentCompaign}}/{{campaignDetail.stampNeeded}}</div>
        <div class="campaign-detail__campaign-region__title__completed">
          已达成
          <span>{{completeRounds}}</span>轮
        </div>
      </div>
      <div class="campaign-detail__campaign-region__rounds">
        <campaign-detail-list
          :userStampList = "campaignDetail.userStampList"
          :participateRound = "campaignDetail.participateRound"
          :totalCampaignNum = "campaignDetail.totalCampaignNum"
          :iconNotOver = "campaignDetail.iconNotOver"
          :iconOver = "campaignDetail.iconOver"
          :stampNeeded = "campaignDetail.stampNeeded" ></campaign-detail-list>
      </div>
      <campaign-detail-expressed-and-address
       :userStampList = "campaignDetail.userStampList"
       :myExchangedQty = "campaignDetail.myExchangedQty"
      ></campaign-detail-expressed-and-address>
    </div>
    <div class="campaign-detail__appointment-time-limit-region">
      <div class="campaign-detail__appointment-time-limit-region__title">
        <div class="campaign-detail__appointment-time-limit-region__title__activity-time">活动时间</div>
      </div>
      <div class="campaign-detail__appointment-time-limit-region__title__timeline-bar">
        <timeline-bar :startDate="campaignDetail.campaignDateFrom" :endDate="campaignDetail.campaignDateTo"></timeline-bar>
      </div>
    </div>
    <div class="campaign-detail__shop-region" v-if='sortedShopsWithDistance[0]'>
      <div class="campaign-detail__shop-region__title" @click="goToCampaignAvailableShopList">
        <div class="campaign-detail__shop-region__title__apply-shop">适用门店</div>
        <div class="campaign-detail__shop-region__title__all-shop">查看全部({{shopNum}})</div>
      </div>
      <div class="campaign-detail__shop-region__shop-name">
        <p>{{sortedShopsWithDistance[0].shopName}}
          <format-business-time
            v-if="sortedShopsWithDistance[0].businessTime !== ''"
            :businessTime="sortedShopsWithDistance[0].businessTime">
          </format-business-time>
        </p>
        <p class="campaign-detail__shop-region__shop-distance" v-if="sortedShopsWithDistance[0].distance">距您{{ sortedShopsWithDistance[0].distance | toKilometer }}</p>
      </div>
      <div class="campaign-detail__shop-region__shop-address">
        <p>{{sortedShopsWithDistance[0].address}}</p>
      </div>
    </div>
    <div class="campaign-detail__commodity-region" v-if="campaignDetail.commodityList.length">
      <div class="campaign-detail__commodity-region__title">
        <div class="campaign-detail__commodity-region__title__apply-commodity">活动商品</div>
        <div class="campaign-detail__commodity-region__title__all-commoditys"
        @click="goToCampaignProducts">查看全部({{campaignDetail.commodityList.length}})</div>
      </div>
      <div class="campaign-detail__commodity-region__commodity-img">
        <apply-commodity :campaignProducts="campaignDetail.commodityList"></apply-commodity>
      </div>
    </div>
    <div class="campaign-detail__explain-region">
      <div class="campaign-detail__explain-region__title">
        <div class="campaign-detail__explain-region__title__instructions">活动说明</div>
      </div>
      <div class="campaign-detail__explain-region__describe">
        <div>兑换时间：{{ campaignDetail.campaignDateFrom | formatDate }}-{{ campaignDetail.openEndDt | formatDate }}</div>
        <hr>
        <p v-html="campaignDetail.description"></p>
      </div>
    </div>
    <campaign-exchangeAnd-transfer-button
      :isTransfer="isTransfer"
      :isExchange="isExchange"
      :campaignId="this.campaignDetail.campaignId"
      :totalCampaignNum="this.campaignDetail.totalCampaignNum"
      :image="this.campaignDetail.imagePathDetail || this.campaignDetail.imagePathList"
      :rewardType = this.campaignDetail.rewardType
      @campaignnExchang="__campaignnExchang"
      @isCampaignnTransferModal = campaignnTransferModal
    >
    </campaign-exchangeAnd-transfer-button>
    <campaign-exchange-not-winning-modal v-if="isShowCampaignExchangeNotWinningModal === 'show'"  @hideCampaignExchangeNotWinningModal="hideCampaignExchangeNotWinningModal"></campaign-exchange-not-winning-modal>
    <campaign-exchange-coupon-success-modal v-if="isShowCampaignExchangeCouponSuccessModal === 'show'" :campaignGift="campaignGift"></campaign-exchange-coupon-success-modal>
  </div>
</template>

<script>
function getRanNum(){
  const result = [];
  for(var i=0;i<1;i++){
      var ranNum = Math.ceil(Math.random() * 25); //生成一个0到25的数字
      //大写字母'A'的ASCII是65,A~Z的ASCII码就是65 + 0~25;然后调用String.fromCharCode()传入ASCII值返回相应的字符并push进数组里
      result.push(String.fromCharCode(97+ranNum));
  }
  return  result.join('');
}
import _ from 'lodash';

import { swiper, swiperSlide } from 'vue-awesome-swiper';
import api from '@/api';
import envService   from '@/services/env';
import popularStatisticsService   from '@/services/popular-statistics';
import calculateDistance from '@/utils/calculate-distance';
import reginonService from '@/services/region';
import wechatSdkService from '@/services/wechat-mini-program-bridge';

import TimelineBar                        from "./CampaignDetail/TimelineBar";
import ApplyCommodity                     from "./CampaignDetail/ApplyCommodity";
import CampaignDetailExpressedAndAddress  from "./CampaignDetail/CampaignDetailExpressedAndAddress";
import CampaignDetailList                 from "./CampaignDetail/CampaignDetailList";
import CampaignExchangeAndTransferButton  from "./CampaignDetail/CampaignExchangeAndTransferButton";
import AvailableStore                     from "./CampaignDetail/AvailableStore";
import CampaignExchangeNotWinningModal    from "./CampaignDetail/CampaignExchangeNotWinningModal";
import CampaignExchangeCouponSuccessModal from "./CampaignDetail/CampaignExchangeCouponSuccessModal";
import FormatBusinessTime                 from '@/components/FormatBusinessTime';

import 'swiper/dist/css/swiper.css';
import MessageBox from '@/components/MessageBox';
import LoadingModal from '@/components/LoadingModal';
import CommittingModal from '@/components/CommittingModal';

export default {
  components: {
    swiper,
    swiperSlide,
    TimelineBar,
    ApplyCommodity,
    CampaignExchangeAndTransferButton,
    CampaignDetailList,
    CampaignExchangeNotWinningModal,
    CampaignExchangeCouponSuccessModal,
    AvailableStore,
    FormatBusinessTime,
    CampaignDetailExpressedAndAddress
  },
  data() {
    return {
      campaignDetail: null,
      campaignProducts: null,
      campaignGift: null,
      currentLocation: null,
      barrageList: null,

      isShowCampaignExchangeCouponSuccessModal: 'hide',
      isShowCampaignExchangeNotWinningModal: 'hide',

      swiperOption: {
        direction: 'vertical',
        initialSlide: 1,
        zoom : true,
        look: true,
        loop : true,
        autoplay : true,
        autoHeight: true,
        spaceBetween : 20,
      },
    };
  },
  async created() {
    const regionBlockCode = envService.getRegionBlockCode();
    this.setCurrentLocation();
    this.recordCampaignPopularStatistics();
    await this.__fetchCampaignDetail();
    if(regionBlockCode !== this.campaignDetail.regionBlockCode) {
      this.showRegionDiffModal(this.campaignDetail.regionBlockCode);
    }
  },
  mounted() {
    this.fetchCampaignBarrage();
  },
  computed: {
    defaultAvatar() {
      return '//lawson-app.oss-cn-hangzhou.aliyuncs.com/miniprogram-new/avatar/default-man.png';
    },
    getShops() {
      const shopUseFlg = this.campaignDetail.shopUseFlg;
      const shopIdList = this.campaignDetail.shopid;
      const regionBlockCode = envService.getRegionBlockCode();
      const isAllShop = shopUseFlg === 0;
      if (isAllShop) {
        const shopsAvailableForRegion = this.$store.getters['shop/shopsAvailableForRegion'](regionBlockCode);
        return shopsAvailableForRegion ? this.$store.getters['shop/shopsForRegion'](regionBlockCode) : [];
      } else {
        const shops = this.$store.state.shop.shops[regionBlockCode];
        return _.intersectionWith( shops, shopIdList, (shopInfo, shopId) => shopInfo.shopId === shopId );
      }
    },
    sortedShopsWithDistance() {
      const currentLocation = this.currentLocation;
      if (currentLocation) {
        const shopsWithDistance =  this.getShops.map(shop => {
          const _shop = Object.assign({}, shop);
          _shop.distance = calculateDistance(shop.latitude, shop.longitude, currentLocation.latitude, currentLocation.longitude);
          return _shop;
        });

        shopsWithDistance.sort((aShop, bShop) => aShop.distance - bShop.distance);
        return shopsWithDistance;
      } else {
        return this.getShops.length ? this.getShops : [];
      }
    },
    shopNum() {
      const regionBlockCode = envService.getRegionBlockCode();
      const isAllShops = (this.campaignDetail.shopUseFlg === 0) ? true : false;
      const isshopsAvailableForRegion = this.$store.getters['shop/shopsAvailableForRegion'](regionBlockCode);

      if(isAllShops) {
        if(isshopsAvailableForRegion) {
          return this.$store.getters['shop/shopsForRegion'](regionBlockCode).length;
        } else {
          return 0;
        }
     } else {
        return this.campaignDetail.shopid.length;
     }
    },
    isThisWeed() {
      const now = new Date();
      const nowDayOfWeek =  (now.getDay() === 0) ? 7 : now.getDay();
      const weekEndDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + (7 - nowDayOfWeek));
      const weekStartDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1 - nowDayOfWeek);
      return this.campaignDetail.campaignDateFrom >= weekStartDate.getTime() && this.campaignDetail.campaignDateFrom <= weekEndDate.getTime() ;
    },
    isLessThanFivedays() {
      const now = new Date().getTime();
      const campaignEndTime = this.campaignDetail.openEndDt;
      const campaignDateFrom = this.campaignDetail.campaignDateFrom;
      const day = parseInt(1000 * 60 * 60 * 24);
      return  (now <= campaignEndTime && now >= campaignDateFrom) ? campaignEndTime - now <= (day * 5) : false;
    },
    isCampaignStart() {
      const now = new Date().getTime();
      const campaignEndTime = this.campaignDetail.openEndDt;
      const day = parseInt(1000 * 60 * 60 * 24);
      return (now >= this.campaignDetail.campaignDateFrom) && !(campaignEndTime - now <= (day * 5));
    },
    isNotThisWeed() {
      const now = new Date();
      const nowDayOfWeek = now.getDay();
      const weekEndDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + (7 - nowDayOfWeek));
      const weekStartDate = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1 - nowDayOfWeek);
      if(now.getTime() > this.campaignDetail.campaignDateFrom) return false;
      return this.campaignDetail.campaignDateFrom < weekStartDate.getTime() || this.campaignDetail.campaignDateFrom > weekEndDate ;
    },
    compaignEndTime: function() {
      return this.__fromNow(this.campaignDetail.campaignDateTo);
    },
    isTransfer: function() {
      if (this.campaignDetail.totalCampaignNum > 0 && this.campaignDetail.campaignGiveFlg === 0) {
        return true;
      } else {
        return false;
      }
    },
    isExchange: function() {
      if (this.campaignDetail.giftNums > 0 && _.filter(this.campaignDetail.userStampList, item =>  item.status === 1).length > 0) {
        return true;
      } else {
        return false;
      }
    },
    currentCompaign: function() {
      if(this.campaignDetail.userStampList.length === this.campaignDetail.participateRound) {
        return (this.campaignDetail.totalCampaignNum % this.campaignDetail.stampNeeded  === 0) ?
        this.campaignDetail.stampNeeded : this.campaignDetail.totalCampaignNum % this.campaignDetail.stampNeeded;
      } else {
        return this.campaignDetail.totalCampaignNum % this.campaignDetail.stampNeeded;
      }
    },
    completeRounds() {
      return parseInt(this.campaignDetail.userTotalStampNum / this.campaignDetail.stampNeeded);
    }
  },
  filters: {
    formatOnLookCount(onlookerCount) {
      if(onlookerCount % 10000 === 0) {
        return onlookerCount / 10000;
      } else {
        return (onlookerCount/ 10000).toFixed(1);
      }
    },
    formatDate: function (timestamp) {
     const year = new Date(timestamp).getFullYear().toString();
     const month = (new Date(timestamp).getMonth()+1).toString().padStart(2,'0');
     const day = new Date(timestamp).getDate().toString().padStart(2,'0');
     return `${year}.${month}.${day}`;
    },
    formatThisWeekDate(timestamp) {
      const weekday = ['周日','周一','周二','周三','周四','周五','周六'];
      const date = new Date(timestamp);
      const hours = ('0' + date.getHours()).slice(-2);
      const minutes = ('0' + date.getMinutes()).slice(-2);
      const weekdayLength = date.getDay();
      return `本${weekday[weekdayLength]} ${hours}:${minutes}开始`;
    },
    formatNotThisWeekDate(timestamp) {
      const nowDate = new Date();
      const campaignStartDate = timestamp;
      const campaignStartMonth= (new Date(campaignStartDate).getMonth()+1).toString().padStart(2,'0');
      const campaignStartDay= new Date(campaignStartDate).getDate().toString().padStart(2,'0');
      const campaignStartHour= new Date(campaignStartDate).getHours().toString().padStart(2,'0');
      const campaignStartMinutes = new Date(campaignStartDate).getMinutes().toString().padStart(2,'0');
      const differentiateBetweenDate = campaignStartDate-nowDate.getTime();
      const days = parseInt(differentiateBetweenDate / (1000 * 60 * 60 * 24));
      const day = parseInt(1000 * 60 * 60 * 24);

      if(nowDate < campaignStartDate && (campaignStartDate - nowDate > 5 * day)) {
        return `${campaignStartMonth}月${campaignStartDay}日 ${campaignStartHour}:${campaignStartMinutes}分开始`;
      } else {
        return `${days}天后 ${campaignStartHour}:${campaignStartMinutes}分开始`;
      }
    },
    formatLessFiveDay(timestamp) {
      const nowDate = new Date();
      const campaignEndDate = timestamp;
      const campaignEndHour= new Date(campaignEndDate).getHours().toString().padStart(2,'0');
      const campaignEndMinutes = new Date(campaignEndDate).getMinutes().toString().padStart(2,'0');
      const differentiateBetweenDate = campaignEndDate-nowDate.getTime();
      const days = parseInt(differentiateBetweenDate / (1000 * 60 * 60 * 24));
      return `${days}天后 ${campaignEndHour}:${campaignEndMinutes}分结束`;
    },
    toKilometer(number) {
      if (number > 20) {
        return '>20km';
      } else {
        return number.toFixed(2) + 'km';
      }
    }
  },
  methods: {
    fetchCampaignBarrage() {
      const regionBlockCode = envService.getRegionBlockCode();
      const campaignId = this.$route.params.campaignId;
      api.campaign.campaignBarrage({regionBlockCode,campaignId}).then(data => {
        this.barrageList = data;
      });
    },
    showRegionDiffModal(campaignRegionBlockCode) {
      let campaignRegionBlockName = reginonService.getCurrentRegionName(campaignRegionBlockCode);
      const userInfo = envService.getUserInfo();
      const msg = userInfo ? `你所在区域无法参与，当前内容仅限${campaignRegionBlockName}` : '你当前查阅内容，可能受区域限制有所不同' ;

      MessageBox.alert({ title: '友情提示', msg: msg });
    },
    recordCampaignPopularStatistics() {
      const campaignDetailId = this.$route.params.campaignId;
      popularStatisticsService.recordCampaignPopularStatistics(campaignDetailId);
    },
    __handleCampaignProductsRes(campaignProducts) {
      const newCampaignProducts = campaignProducts.list.slice(0, 4);
      this.campaignProducts = newCampaignProducts;
    },
    hideCampaignExchangeNotWinningModal() {
      this.isShowCampaignExchangeNotWinningModal = 'hide';
      document.body.style.overflow = "auto";
    },
    __campaignnExchang() {
      const campaignId = this.campaignDetail.campaignId;
      const stampId = _.find(this.campaignDetail.userStampList, function(item) {
        return item.status === 1;
      }).stampId;
      CommittingModal.show();
      api.campaign.campaignGift({ campaignId, stampId }).then(res => {
        this.campaignGift = res;
        const isCoupon = this.campaignGift.giftType === 1 ? true : false;
        const isGift = this.campaignGift.giftType === 2 ? true : false;
        if(isGift) {
          const userGiftId = this.campaignGift.id;
          this.$router.push({ path: `/campaign-post-exchange/`, query: { userGiftId }});
        }
        else if(isCoupon) {
          this.isShowCampaignExchangeCouponSuccessModal = 'show';
        }
        CommittingModal.hide();
      })
      .catch(err => {
        if(err.data.code === 201 && err.data.errors[0].code === '5008') {
          const flowerExchange = (this.campaignDetail.rewardType === 1)  ? true : false;
          const flowerLuckyDraw = (this.campaignDetail.rewardType === 0) ? true : false;

          if(flowerExchange) {
            MessageBox.alert({ title: '兑换失败', msg: '很遗憾,礼品已经被抢完了。' },
            (onIknow) => {
              this.__fetchCampaignDetail();
            });
          } else if(flowerLuckyDraw) {
            this.isShowCampaignExchangeNotWinningModal = 'show';
            this.__fetchCampaignDetail();
          }
          CommittingModal.hide();
        }
      });
    },
    __fetchCampaignDetail() {
      return new Promise((resolve,reject) => {
        LoadingModal.show();
        const regionBlockCode  = envService.getRegionBlockCode();
        const campaignDetailId = this.$route.params.campaignId;
        api.campaign.fetchCampaignDetail({ campaignDetailId, regionBlockCode }).then(campaignDetailRes => {
          this.campaignDetail = campaignDetailRes;
          LoadingModal.hide();
          resolve();
        })
        .catch(err => {
          reject(err);
          LoadingModal.hide();
        });
      })
    },
    goToCampaignProducts() {
      const userInfo = envService.getUserInfo();
      const campaignId = this.campaignDetail.campaignId;
      if(userInfo) {
        this.$router.push({ path: `/campaign-products/${campaignId}` });
      } else {
        wx.miniProgram.reLaunch({ url: `/pages/index/index?pageKey=campaignDetail&commodityId=${campaignId}&sourceType=internal` });
      }
    },
    goToCampaignAvailableShopList() {
      const userInfo = envService.getUserInfo();
      const campaignId = this.campaignDetail.campaignId;
      if (userInfo) {
        const { shopUseFlg, shopid } =  this.campaignDetail;
        this.$router.push({ name: `campaign-Available-shop-list`, params: { shopUseFlg, shopid } });
      } else {
        wx.miniProgram.reLaunch({ url: `/pages/index/index?pageKey=campaignDetail&commodityId=${campaignId}&sourceType=internal` });
      }
    },
    campaignnTransferModal() {
      const msg = '提醒你的好友，要24小时内及时领取哦';
      MessageBox.alert({ title: '转赠完成', msg: msg },
        (onIknow) => {
          this.__fetchCampaignDetail();
        }
      );
    },
    setCurrentLocation() {
      wechatSdkService.getLocation().then(res => {
        const latitude = res.latitude;
        const longitude = res.longitude;
        this.currentLocation = { latitude, longitude };
      });
    }
  },
};
</script>

<style lang="scss" scoped>
$campaign-detail-activity-time: $oss-images+'/campaign/campaign-detail-activity-time.png';
$campaign-detail-shop: $oss-images+'/campaign/campaign-detail-shop.png';
$campaign-detail-commodity: $oss-images+'/campaign/campaign-detail-commodity.png';
$campaign-detail-explain: $oss-images+'/campaign/campaign-detail-explain.png';

@import "style/variables.scss";
