  width: 100vw;
  background: #f7f7f7;
  &__banner-region {
    position: relative;
    width: 100vw;
    height: 100vw;
    &__new-gain {
      display: flex;
      align-items: center;
      height: 100%;
    }
    &__img {
      width: 100vw;
      height: 100vw;
    }
    &__sub {
      position: absolute;
      top: 13.86vw;
      display: flex;
      height: 8vw;
      overflow: hidden;
      &__even {
        position: relative;
        margin-left: 3.2vw;
        margin-top: 0;
        display: flex;
        align-items: center;
        height: 100%;
        background: rgba($color: #E65C58, $alpha: 0.8);
        border-radius: 10vw;
        color: #ffffff;
        font-size: $font-size-smallest;
        overflow: hidden;
      }
      &__odd {
        width: 49.6vw;
        height: 100%;
      }
      &__profile {
        padding-left: 1.6vw;
        padding-right: 1.6vw;
        width: 4.8vw;
        height: 4.8vw;
      }
      &__name {
        padding-left: 1.06vw;
        padding-right: 1.06vw;
        font-size: $font-size-smallest;
      }
      &__tweets {
        padding-right: 1.6vw;
        font-size: $font-size-smallest;
      }
    }
  }
  &__title-region {
    z-index: 0;
    top: -12.66vw;
    position: relative;
    margin: 0 auto;
    width: 93.6vw;
    background: #ffffff;
    border-radius: 2.66vw;
    &__name-region {
      display: flex;
