<template>
  <div class="single-coupon">
    <div class="single-coupon__image">
      <img :src="pickCouponActivity.backgroundImage">
    </div>
    <div class="single-coupon__button"
      :style="{ top: couponShowOffset + 'px' }">
      <img class="single-coupon__button__image"
        :src="pickCouponActivity.couponImageList[1].couponImage"
        v-if="isActivityEnd || isActivityBegin || isActivityGoingAndNoRemain">

      <img class="single-coupon__button__image"
        :src="pickCouponActivity.couponImageList[0].couponImage"
        v-else>

      <span class="single-coupon__button__text"
        v-if="isActivityBegin">即将开始</span>
      <span class="single-coupon__button__text"
        v-else-if="isActivityEnd">活动结束</span>
      <span class="single-coupon__button__text"
        v-else-if="coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0">已领完</span>
      <span class="single-coupon__button__text"
        v-else-if="coupon.hasPicked">已领取</span>
      <span class="single-coupon__button__text"
        v-else-if="!coupon.hasPicked" @click="pickCoupon(coupon.couponId)">立即领取</span>
    </div>
  </div>
</template>
<script>
import api from '@/api';
import envService from '@/services/env';

export default {
  props: ['activityDetail'],

  computed: {
    pickCouponActivity() {
      return this.activityDetail.pickCouponActivity;
    },

    coupon() {
      return this.activityDetail.couponList[0];
    },

    isActivityBegin() {
      const currentTime = Date.parse(new Date());
      return currentTime < this.pickCouponActivity.beginTime ? true : false;
    },

    isActivityEnd() {
      const currentTime = Date.parse(new Date());
      return currentTime > this.pickCouponActivity.endTime ? true : false;
    },

    isActivityGoingAndNoRemain() {
      const currentTime = Date.parse(new Date());
      return (currentTime >= this.pickCouponActivity.beginTime && currentTime <= this.pickCouponActivity.endTime) && (this.coupon.couponRemain <= 0 && this.coupon.sendUpperLimit > 0) ? true : false;
    },

    couponShowOffset() {
      const deviceWidth = window.screen.width;
      const couponShowOffsetInIphone6 = this.pickCouponActivity.couponShowOffset;
      const deviceWidthIphone6 = 375;
      return (couponShowOffsetInIphone6 * deviceWidth) / deviceWidthIphone6;
    }
  },

  methods: {
    pickCoupon(couponId) {
      this.$emit("pickCoupon", couponId);
    }
  }
}
</script>
