<template>
  <div class="toast" v-if="visible">
    <transition name="fade">
      <div v-if="iconName === 'success-icon'">
        <div class="toast__container">
          <div class="toast__container__wrapper">
            <span class="toast__container__wrapper__icon success"></span>
            <div class="toast__container__wrapper__msg">{{ msg }}</div>
          </div>
        </div>
      </div>
      <div v-else-if="iconName === 'error-icon'">
        <div class="toast__container">
          <div class="toast__container__wrapper">
            <span class="toast__container__wrapper__icon error"></span>
            <div class="toast__container__wrapper__msg">{{ msg }}</div>
          </div>
        </div>
      </div>
      <div class="toast__msg" v-else>{{ msg }}</div>
    </transition>
  </div>
</template>

<script>

export default {
  props: {
    msg: String,
    iconName: String
  },

  data () {
    return {
      visible: false
    };
  },

  mounted () {
    window.addEventListener('popstate', e => {
      this.visible = false;
    });
  }
};
</script>

