<template>
  <div class="new-shops-coupon">
    <pull-down-refresh @refresh="fetchNewShops" :isRefreshing="isRefreshing">
      <div class="new-shops-coupon__container">
        <div class="new-shops-coupon__select-region">
          <div class="new-shops-coupon__select-region__button" v-if="cityList !== null && currentCity !== null">
            <div>{{ currentCity.cityName }}</div>
            <div class="new-shops-coupon__select-region__button__icon"></div>
            <select v-model="currentCity">
              <option :value ="city" v-for="(city, index) in cityList" :key="city.cityId">{{ city.cityName }}</option>
            </select>
          </div>
        </div>
        <div class="new-shops-coupon__content" v-if="newShopList !== null && newShopList.length > 0">
            <new-shop-item @pickNewShopCoupon="pickNewShopCoupon" v-for="(newShopItem, index) in newShopList" :key="index" :newShop="newShopItem"></new-shop-item>
        </div>
        <div v-else class="new-shops-coupon__no-data">
          <img :src="ossImageUrl + '/coupon/icon-no-data.png'">
          <div>暂无内容</div>
        </div>
      </div>
    </pull-down-refresh>
    <goto-home></goto-home>
  </div>
</template>
<script>
import Toast       from '@/components/Toast';
import GotoHome    from '@/components/GotoHome';
import NewShopItem from './NewShopsCoupon/NewShopItem';
import LoadingModal from '@/components/LoadingModal';
import PullDownRefresh from '@/components/PullDownRefresh';

import api from '@/api';
import envService from '@/services/env';

export default {
  data() {
    return {
      newShopList: null,
      ossImageUrl: process.env.VUE_APP_OSS_BASE_URL,
      cityList: null,
      currentCity: null,
      isRefreshing: false,
    }
  },

  components: {
    GotoHome,
    NewShopItem,
    PullDownRefresh
  },

  async created () {
    await this.fetchCityList();
    this.fetchNewShops();
  },

  methods: {
    fetchNewShops() {
      this.isRefreshing = true;
      const regionBlockCode = envService.getRegionBlockCode();
      const cityId = this.currentCity.cityId;

      api.newShops.fetchNewShops({ regionBlockCode, cityId }).then(newShopCoupon => {
        this.newShopList = newShopCoupon;
        this.isRefreshing = false;
      })
      .catch(error => {
        this.isRefreshing = false;
      });
    },

    fetchCityList() {
      LoadingModal.show();
      return new Promise((resolve, reject) => {
        const regionBlockCode = envService.getRegionBlockCode();
        api.cityList.fetchCityList({ regionBlockCode }).then(res => {
          this.cityList = res.data;
          this.currentCity = this.cityList[0];
          resolve();
        }).finally(() => LoadingModal.hide());
      })
    },

    pickNewShopCoupon({ couponId, shopId }) {
      const regionBlockCode = envService.getRegionBlockCode();

      api.newShops.pickNewShopCoupon({ regionBlockCode, couponId, shopId }).then(pickNewShopCoupon => {
        const cityId = this.currentCity.cityId;

        api.newShops.fetchNewShops({ regionBlockCode, cityId }).then(newShopCoupon => {
          this.newShopList = newShopCoupon;
        });

        Toast.show({ msg: '抢券成功', iconName: 'success-icon' });
      });
    }
  },

  watch: {
    currentCity(val, oldVal) {
      if (oldVal) {
        this.fetchNewShops();
      }
    }
  }
}
</script>
