<template>
  <div class="picked" v-if="pickedCapsules !== null">
    <div class="picked__capsules" v-if="pickedCapsules.length > 0">
      <div class="picked__capsule" v-for="capsule in pickedCapsules">
        <div class="picked__capsule__capsule" :class="capsule.color"></div>
        <div class="picked__capsule__info" v-if="capsule.type === 'coupon'">
          <p class="picked__capsule__info__title">{{ capsule.reward.couponName }}</p>
          <p class="picked__capsule__info__subtitle">详情可至“优惠券”查看</p>
        </div>
        <div class="picked__capsule__info" v-else-if="capsule.type === 'point'">
          <p class="picked__capsule__info__title">赠送积分+{{ capsule.reward.lotteryPoint }}</p>
          <p class="picked__capsule__info__subtitle">请下载并安装APP，可至我的查看</p>
        </div>
        <div class="picked__capsule__info" v-else-if="capsule.type === 'lotteryCoin'">
          <p class="picked__capsule__info__title">赠送抽奖币+{{ capsule.reward.lotteryTicketNum }}</p>
          <p class="picked__capsule__info__subtitle">已经收入你的账户，可至任务中心查看</p>
        </div>
      </div>
    </div>
    <div class="picked__no-content" v-else>
      <div class="picked__no-content__icon"></div>
      <div class="picked__no-content__text">暂无内容</div>
    </div>
  </div>
</template>

<script>
import envService from '@/services/env';
import api from '@/api';

import LoadingModal from '@/components/LoadingModal';

export default {
  data() {
    return {
      pickedCapsules: null,
    }
  },
  methods: {
    getCollectedCapsules(lotteryHistory) {
      return lotteryHistory.filter(lotteryHistoryEntry => {
        // lotteryType为0,1,2时表示中奖，其余情况为未中奖，在显示抽奖历史时需过滤掉
        const gotRewardLotteryTypes = [0, 1, 2];
        const gotReward = gotRewardLotteryTypes.indexOf(lotteryHistoryEntry.lotteryType) > -1 ? true : false;
        return gotReward;
      }).map(lotteryHistoryEntry => {
        const rewardType = (() => {
          if (lotteryHistoryEntry.lotteryType === 0) {
            return 'point';
          } else if (lotteryHistoryEntry.lotteryType === 1) {
            return 'coupon';
          } else if (lotteryHistoryEntry.lotteryType === 2) {
            return 'lotteryCoin';
          }
        })();

        if (rewardType === 'point') {
          return { type: 'point' , color: 'blue', reward: lotteryHistoryEntry };
        } else if (rewardType === 'coupon') {
          return { type: 'coupon', color: 'red', reward: lotteryHistoryEntry };
        } else if (rewardType === 'lotteryCoin') {
          return { type: 'lotteryCoin', color: 'yellow', reward: lotteryHistoryEntry };
        }
      });
    },
  },
  created() {
    LoadingModal.show();
    const regionBlockCode = envService.getRegionBlockCode();

    api.lottery.fetchLotteryHistory({ regionBlockCode }).then(lotteryHistory => {
      LoadingModal.hide();
      this.pickedCapsules = this.getCollectedCapsules(lotteryHistory);
    });
  }
}
</script>

