<template>
  <div class="get-coupons">
    <pull-down-refresh @refresh="fetchData" :isRefreshing="isRefreshing">
      <div class="get-coupons__content">
        <div class="get-coupons__pick-coupon-remind">
            <span class="get-coupons__pick-coupon-remind__text">优惠订阅，活动开启前提醒我。</span>
            <div class="get-coupons__pick-coupon-remind__button" @click="remindSwitch" v-if="isRemind">
              <span class="get-coupons__pick-coupon-remind__button__switch"></span>
            </div>
            <div class="get-coupons__pick-coupon-remind__button-gray" @click="remindSwitch" v-else>
              <span class="get-coupons__pick-coupon-remind__button-gray__switch"></span>
            </div>
        </div>
        <div class="get-coupons__banner" v-if="pickCouponActivityList !== null">
          <swiper :options="swiperOption">
            <swiper-slide v-for="(pickCouponItem, index) in pickCouponActivityList.pickCouponActivityList" :key="index">
              <img :src="pickCouponItem.imagePathList" @click="goToPickCouponActivityDetail( pickCouponItem.pickCouponActivityId )">
            </swiper-slide>
          </swiper>
          <div class="swiper-pagination" slot="pagination"></div>
        </div>
        <div class="get-coupons__image" @click="goToNewShopCoupon" v-if="hasNewShopsCouponVisible">
          <img :src="newShopBannerImage">
        </div>
        <div class="get-coupons__coupon-list"  v-if="pickCouponList !== null">
          <div v-for="(couponItem, index) in pickCouponList" :key="index">
            <coupon-item-not-begin
              :coupon="couponItem"
              v-if="couponItem.notBegin">
            </coupon-item-not-begin>
            <coupon-item-picked
              :coupon="couponItem"
              v-else-if="couponItem.hasPicked">
            </coupon-item-picked>
            <coupon-item-no-remaining
              :coupon="couponItem"
              v-else-if="couponItem.couponRemain <= 0 && couponItem.sendUpperLimit > 0">
              </coupon-item-no-remaining>
            <coupon-item-unclaimed
              :coupon="couponItem"
              v-else-if="!couponItem.hasPicked"
              @click="pickCoupon(couponItem.couponId)">
            </coupon-item-unclaimed>
          </div>
        </div>
      </div>

    </pull-down-refresh>
    <goto-home></goto-home>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';

import Toast                 from '@/components/Toast';
import GotoHome              from '@/components/GotoHome';
import LoadingModal          from '@/components/LoadingModal';
import PullDownRefresh         from '@/components/PullDownRefresh';
import CouponItemPicked      from './PickCouponList/CouponItemPicked';
import CouponItemNotBegin    from './PickCouponList/CouponItemNotBegin';
import CouponItemUnclaimed   from './PickCouponList/CouponItemUnclaimed';
import CouponItemNoRemaining from './PickCouponList/CouponItemNoRemaining';

import api from '@/api';
import envService from '@/services/env';

export default {
  data () {
    return {
      pickCouponActivityList: null,
      pickCouponList: null,
      swiperOption: {
        autoplay: {
          delay: 1500,
          disableOnInteraction: false
        },
        speed: 500,
        loop: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      hasNewShopsCouponVisible: false,
      newShopBannerImage: null,
      isRemind: false,
      isRefreshing: false
    }
  },

  components: {
    swiper,
    GotoHome,
    swiperSlide,
    PullDownRefresh,
    CouponItemPicked,
    CouponItemNotBegin,
    CouponItemUnclaimed,
    CouponItemNoRemaining
  },

  created() {
    LoadingModal.show();
    this.refusedPushState();
    this.fetchData();
  },

  methods: {
    goToNewShopCoupon() {
      this.$router.push({ path: '/new-shops-coupon' });
    },

    fetchData() {
      this.isRefreshing = true;
      return Promise.all([
        this.fetchPickCouponActivityList(),
        this.fetchPickCouponList(),
        this.hasNewShopsCoupon()
      ])
      .then(([pickCouponActivityListRes, pickCouponListRes, hasNewShopsCouponRes]) => {
        if (hasNewShopsCouponRes.hasCoupon) {
          const regionBlockCode = envService.getRegionBlockCode();

          api.coupon.getNewShopTopBanner({ regionBlockCode }).then(res => {
            this.newShopBannerImage = res.data.list[0].imagePath;
          });

        }

        pickCouponListRes.forEach(coupon => {
          coupon.notBegin    = coupon.currentDate < coupon.useDateFrom ? true : false;
          coupon.percentage  = Math.round((coupon.sendUpperLimit - coupon.couponRemain) / coupon.sendUpperLimit * 100);
          coupon.isRemaining = coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0 ? true : false;
        });

        this.pickCouponActivityList = pickCouponActivityListRes;
        this.pickCouponList = pickCouponListRes;
        this.hasNewShopsCouponVisible = hasNewShopsCouponRes.hasCoupon;
      }).finally(() => {
        this.isRefreshing = false;
        LoadingModal.hide();
      });;
    },

    fetchPickCouponActivityList() {
      const regionBlockCode = envService.getRegionBlockCode();

      return api.coupon.fetchPickCouponActivityList({ regionBlockCode });
    },

    fetchPickCouponList() {
      const regionBlockCode = envService.getRegionBlockCode();

      return api.coupon.fetchPickCouponList({ regionBlockCode });
    },

    hasNewShopsCoupon: function () {
      const regionBlockCode = envService.getRegionBlockCode();

      return api.coupon.hasNewShopsCoupon({ regionBlockCode });
    },

    goToPickCouponActivityDetail(pickCouponActivityId) {
      this.$router.push({ name: 'pick-coupon-activity-detail', params: { pickCouponActivityId } });
    },

    remindSwitch() {

      if (this.isRemind) {
        api.coupon.disagreePush().then( res => {
          Toast.show({ msg: '您已关闭优惠订阅' });
          this.isRemind = false;
        });
      }
      else {
        api.coupon.agreePush().then( res => {
          Toast.show({ msg: '您已开启闭优惠订阅' });
          this.isRemind = true;
        });
      }
    },

    pickCoupon(couponId) {
      api.coupon.pickCoupon({ couponId }).then(pickCoupon => {
        Toast.show({ msg: '抢券成功', iconName: 'success-icon' });

        const regionBlockCode = envService.getRegionBlockCode();
        api.coupon.fetchPickCouponList({ regionBlockCode }).then( pickCouponListRes => {
          pickCouponListRes.map(coupon => {
            coupon.notBegin    = coupon.currentDate < coupon.useDateFrom ? true : false;
            coupon.percentage  = Math.round((coupon.sendUpperLimit - coupon.couponRemain) / coupon.sendUpperLimit * 100);
            coupon.isRemaining = coupon.couponRemain <= 0 && coupon.sendUpperLimit > 0 ? true : false;
          });
          this.pickCouponList = pickCouponListRes;
        })
      });
    },

    refusedPushState() {
      api.coupon.refusedPushState().then( res => {
        if(res.deagreed) {
          this.isRemind = false;
        }
        else {
          this.isRemind = true;
        }
      });
    }
  }
}
</script>
