<template>
  <div class="campaign-post-exchange">
    <img class="campaign-post-exchange__image" :src="imagePath" />
    <div class="campaign-post-exchange__description">{{ giftName }}</div>

    <div class="campaign-post-exchange__post-info-wrapper">
      <div class="campaign-post-exchange__post-info-wrapper__title-info">
        <span class="campaign-post-exchange__post-info-wrapper__title-info__icon"></span>
        <span class="campaign-post-exchange__post-info-wrapper__title-info__title">寄送地址</span>
      </div>
      <div v-if="address !== null" @click="gotoChoosePostAddressInfo">
        <div class="campaign-post-exchange__post-info-wrapper__post-address-info">
          <span class="campaign-post-exchange__post-info-wrapper__post-address-info__address">{{ address.addressName }}</span>
          <span class="campaign-post-exchange__post-info-wrapper__post-address-info__icon"></span>
        </div>
        <div class="campaign-post-exchange__post-info-wrapper__receiver-info">
          <span class="campaign-post-exchange__post-info-wrapper__receiver-info__name">{{ address.name }}</span>
          <span class="campaign-post-exchange__post-info-wrapper__receiver-info__phone-number">{{ address.phoneNumber }}</span>
        </div>
      </div>
      <div v-else @click="gotoChoosePostAddressInfo">
        <div class="campaign-post-exchange__post-info-wrapper__post-address-info">
          <span class="campaign-post-exchange__post-info-wrapper__post-address-info__address">请选择寄送地址</span>
          <span class="campaign-post-exchange__post-info-wrapper__post-address-info__icon"></span>
        </div>
      </div>
    </div>

    <div class="campaign-post-exchange__intro-info-wrapper">
      <div class="campaign-post-exchange__intro-info-wrapper__title-info">
        <span class="campaign-post-exchange__intro-info-wrapper__title-info__icon"></span>
        <span class="campaign-post-exchange__intro-info-wrapper__title-info__title">兑换须知</span>
      </div>
      <div class="campaign-post-exchange__intro-info-wrapper__other-description">❶ 请选择寄送地址；</div>
      <div class="campaign-post-exchange__intro-info-wrapper__other-description">❷ 确认兑换后，1小时内修改寄送地址。</div>
    </div>
    <div class="campaign-post-exchange__term-description">点击“确认地址”表示您已阅读并同意以上条款</div>
    <div class="campaign-post-exchange__exchange-button-wrapper">
      <div class="campaign-post-exchange__exchange-button-wrapper__button"
           :class="{ 'active': confirmAddressButtonVisible }"
           @click="handleConfirmAddress">确认地址
      </div>
    </div>
  </div>
</template>

<script>
import MessageBox from '@/components/MessageBox';
import api from '@/api';
import Toast from '@/components/Toast';
import LoadingModal from '@/components/LoadingModal';
import envService from '@/services/env';
import campaignExchangeAddressModuleOption from '@/store/campaign-exchange-address/campaign-exchange-address';

export default {
  data() {
    return {
      imagePath: null,
      giftName: null,
      address: null,
      isChooseAddressButtonVisible: false,
      confirmAddressButtonVisible: false,
      userGiftId: null
    }
  },
  created() {
    this.registerCampaignExchangeAddressModule();

    this.userGiftId = this.$route.query.userGiftId;

    this.getUserGiftInfo(this.userGiftId);

    const chooseAddressInfo = this.$store.state.campaignExchangeAddress.chooseAddressInfo;
    const hasAddressInfo = chooseAddressInfo ? true : false;
    if (hasAddressInfo) {
      this.address = chooseAddressInfo;
      this.confirmAddressButtonVisible = true;
    }
  },
  methods: {
    getUserGiftInfo(userGiftId) {
      LoadingModal.show();
      api.campaign.getUserGiftInfo({ userGiftId }).then(res => {
        LoadingModal.hide();
        this.imagePath = res.data.imagePath;
        this.giftName = res.data.giftName;
      }).catch(err => {
        LoadingModal.hide();
        Toast.show({ msg: '获取礼品信息失败, 请稍后再试' });
      })
    },
    registerCampaignExchangeAddressModule() {
      this.$store.registerModule('campaignExchangeAddress', campaignExchangeAddressModuleOption);
    },
    gotoChoosePostAddressInfo() {
      this.$router.push({ path: '/campaign-post-exchange/choose-address' });
    },
    handleConfirmAddress() {
      if (this.confirmAddressButtonVisible) {
        const regionBlockCode = envService.getRegionBlockCode();

        const saveAddressInfo = {
          regionBlockCode: regionBlockCode,
          userGiftId: Number(this.userGiftId),
          userName: this.address.name,
          address: `${this.address.addressDetailName}${this.address.addressName}${this.address.addressExtraInfo}`,
          mobile: this.address.phoneNumber
        }
        api.campaign.campaignGiftSaveAddress({ saveAddressInfo }).then((res) => {
          const saveCampaignPostAddressSuccess = res.code === 0 ? true : false;
          if (saveCampaignPostAddressSuccess) {
            const addressMsg = `寄送地址: ${this.address.addressDetailName}${this.address.addressName}${this.address.addressExtraInfo}`;

            MessageBox.alert({ title: '地址提交成功', msg: addressMsg }, onIknow => {
              this.$router.go(-1);
              this.$store.commit('campaignExchangeAddress/cleanupChooseAddressInfo');
              this.$store.unregisterModule('campaignExchangeAddress');
            });
          }
        }).catch(err => {
          Toast.show({ msg: '确认地址信息失败, 请稍后再试' });
        })
      }
    }
  },
}
</script>

