<template>
  <div class="pick-coupon-activity-detail" v-if="activityDetail !== null">
    <single-coupon-activity
      v-if="activityDetail.couponList.length <= 1"
      :activityDetail="activityDetail"
      @pickCoupon="pickCoupon"></single-coupon-activity>
    <multiple-coupon-activity
      v-else
      :activityDetail="activityDetail"
      @pickCoupon="pickCoupon"></multiple-coupon-activity>

    <goto-home></goto-home>
  </div>
</template>
<script>
import api from '@/api';

import envService     from '@/services/env';
import reginonService from '@/services/region';

import GotoHome     from '@/components/GotoHome';
import MessageBox   from '@/components/MessageBox';
import LoadingModal from '@/components/LoadingModal';

import SingleCouponActivity   from './PickCouponActivityDetail/SingleCouponActivity';
import MultipleCouponActivity from './PickCouponActivityDetail/MultipleCouponActivity';

export default {
  data() {
    return {
      activityDetail: null
    }
  },

  components: {
    GotoHome,
    SingleCouponActivity,
    MultipleCouponActivity
  },

  created() {
    this.fetchPickCouponDetail();
  },

  methods: {
    fetchPickCouponDetail() {
      LoadingModal.show();
      const pickCouponActivityId = this.$route.params.pickCouponActivityId;

      api.coupon.fetchPickCouponDetail({ pickCouponActivityId }).then(res => {
        this.activityDetail = res.data;
        const pickCouponActivity = this.activityDetail.pickCouponActivity;
        const regionBlockCode = envService.getRegionBlockCode();

        if(regionBlockCode !== pickCouponActivity.regionBlockCode) {
          this.showRegionDiffModal(pickCouponActivity.regionBlockCode);
        }

        wx.miniProgram.postMessage({ data: { shareTitle: pickCouponActivity.title } });
      }).finally(() => LoadingModal.hide());
    },

    showRegionDiffModal(couponRegionBlockCode) {
      let couponRegionBlockName = reginonService.getCurrentRegionName(couponRegionBlockCode);

      const isGuest = envService.getUserInfo() === null;
      if (isGuest) {
        MessageBox.alert({ title: '友情提示', msg: '你当前查阅内容，可能受区域限制有所不同' });
      } else {
        MessageBox.alert({ title: '友情提示', msg: `你所在区域无法参与，当前内容仅限${couponRegionBlockName}` });
      }
    },

    pickCoupon(couponId) {
      const pickCouponActivityId = this.$route.params.pickCouponActivityId;
      const isGuest = envService.getUserInfo() === null;

      if (isGuest) {
        wx.miniProgram.reLaunch({ url: `/pages/index/index?pageKey=pickCouponActivity&pickCouponActivityId=${pickCouponActivityId}&sourceType=internal` });
      }
      else {
        api.coupon.pickCouponActivity({ pickCouponActivityId, couponId }).then(res => {

          api.coupon.fetchPickCouponDetail({ pickCouponActivityId }).then(res => {
            this.activityDetail = res.data;
          });

        })
        .catch(err => {
          console.log(err);
        })
      }
    }
  }
}
</script>
