import axios from 'axios';
import signatureService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchLotteryDetail ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(`${baseUrl}/app/v1/lottery/`, {
        params: {
          timestamp,
          nonce,
          signature,
          regionBlockCode
        }
      }).then(res => {
        const fetchLotteryDetailSuccess = res.data.code === 0 ? true : false;
        if (fetchLotteryDetailSuccess) {
          const lotteryDetail = res.data.data;
          resolve(lotteryDetail);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  fetchLotteryHistory ({ regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.get(`${baseUrl}/app/v1/lottery/history`, {
        params: {
          timestamp,
          nonce,
          signature,
          regionBlockCode
        }
      }).then(res => {
        const fetchLotteryHistorySuccess = res.data.code === 0 ? true : false;
        if (fetchLotteryHistorySuccess) {
          const lotteryHistory = res.data.data;
          resolve(lotteryHistory);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
  startLottery ({ lotteryId, regionBlockCode }) {
    const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
    return new Promise((resolve, reject) => {
      axios.post(`${baseUrl}/app/v1/lottery/roll`, {
        timestamp,
        nonce,
        signature,
        lotteryId,
        regionBlockCode
      }).then(res => {
        const fetchLotteryResultSuccess = res.data.code === 0 ? true : false;
        if (fetchLotteryResultSuccess) {
          const lotteryResult = res.data.data;
          resolve(lotteryResult);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  }
}
