<template>
  <div class="campaign-express-address">
    <ul class="campaign-express-address__list">
      <li v-for="(item,index) in sendAddressList" class="campaign-express-address__list__item" :key="index">
        <div class="campaign-express-address__list__item__left" @click="back()">
          <p class="campaign-express-address__list__item__left__title">第{{index + 1}}轮: {{item.giftName}}</p>
          <p class="campaign-express-address__list__item__left__address">{{item.address ? item.address : "未设置"}}</p>
          <p class="campaign-express-address__list__item__left__info">
            <span class="campaign-express-address__list__item__left__name">{{item.userName}}</span>
            <span class="campaign-express-address__list__item__left__phone">{{item.mobile}}</span>
          </p>
        </div>
        <div class="campaign-express-address__list__item__right"  @click="goToCampaignChooseAddress(item.id)">
          <span>设置</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '@/api';

import LoadingModal from '@/components/LoadingModal';

export default {
  data() {
    return {
      sendAddressList: null
    };
  },
  created() {
    this.fetchSendAddressList();
  },
  methods: {
    fetchSendAddressList() {
      const campaignId = this.$route.params.campaignId;
      LoadingModal.show();
      api.campaign.getSendAddress({ campaignId }).then((res) => {
        this.sendAddressList = res;
        LoadingModal.hide();
      });
    },
    goToCampaignChooseAddress: function(userGiftId) {
      this.$router.push({ path: `/campaign-send-choose-address/${userGiftId}` });
    },
    back() {
      this.$router.back();
    }
  }
};

</script>

