import axios from 'axios';
import signatureService from '@/services/sign-api';

const baseUrl = process.env.VUE_APP_API_BASE_URL;

export default {
  fetchNewShops({ regionBlockCode, cityId }) {
    return new Promise((resolve, reject) => {
      const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
      axios.get(`${baseUrl}/app/v1/newShops/`, {
        params: { timestamp, nonce, signature, regionBlockCode, cityId }
      }).then(res => {
        const newShopsSuccess = res.data.code === 0 ? true : false;
        if (newShopsSuccess) {
          const newShopsList = res.data.data.list;
          resolve(newShopsList);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },

  pickNewShopCoupon({ regionBlockCode, couponId, shopId }){
    return new Promise((resolve, reject) => {
      const { timestamp, nonce, signature } = signatureService.generateMainApiSignature();
      axios.post(
        `${baseUrl}/app/v1/newShops/${shopId}/pickCoupon/`,
        {
          timestamp, nonce, signature, regionBlockCode, couponId, shopId
        }
      ).then(res => {
        const pickNewShopCouponSuccess = res.data.code === 0 ? true : false;
        if (pickNewShopCouponSuccess) {
          const pickNewShopCoupon = res.data.data;
          resolve(pickNewShopCoupon);
        } else {
          reject(res);
        }
      }).catch(err => {
        reject(err);
      });
    });
  },
}